// member.js

import React, { Component } from 'react';
import { Button, Avatar, Select, List, Card, Form, Col, Icon, Divider, Row, Modal, Input, Typography, Tag, Spin, Popover, notification } from 'antd';
import { Link, Redirect } from 'react-router-dom';

import axios from 'axios';

import { getStatus } from '../../services/auth';

import MainLayout from '../../components/layout/layout';

import './member.css';

const { Meta } = Card;
const { Title, Paragraph, Text } = Typography;
const { Search } = Input;
const { Option } = Select;

const usersStatus = [
  { color: '#FED330', icon: 'star', title: 'Presidente', label: 'Rei da Cocada Preta' },
  { color: '#26DE81', icon: 'star', title: 'Vice-Presidente/Diretor de RH', label: 'Manda só quando o presidente não está.' },
  { color: '#45AAF2', icon: 'crown', title: 'Diretor de Projetos', label: 'The Best' },
  { color: '#0FB9B1', icon: 'star', title: 'Diretor Administrativo-Financeiro', label: 'Contando Centavos...' },
  { color: '#A55EEA', icon: 'star', title: 'Diretor de Marketing', label: 'Criando umas Artes' },
  { color: '#FD9644', icon: 'thunderbolt', title: 'Membro', label: 'Abastecendo-se de Café' },
  { color: '#FC5C65', icon: 'fire', title: 'Trainee', label: 'Sofrendo' },
  { color: '#4B6584', icon: 'bulb', title: 'Ancião', label: 'Os Baqueados' }
];

class MemberMainPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loadingPage: true,
      tags: [],
      members: [],
      member: '',
      visibleModalSettings: false,
      visibleModalPassword: false,
      passwordsButtonLoading: false,
      changeStatusLoading: false
    };
  }

  componentDidMount() {
    axios.get('/api/members').then(res => {
      const members = res.data;

      members.sort((obj1, obj2) => (obj1.status < obj2.status ? -1 : (obj1.status > obj2.status ? 1 : 0)));

      this.setState({ members, loadingPage: false });
    }).catch(() => {
      // console.log(err);
    });
  }

  searchByName = (e) => {
    if (e.target.value === '') { this.componentDidMount(); }

    axios.get(`/api/members/search/${e.target.value}`).then(res => {
      const members = res.data;

      members.sort((obj1, obj2) => (obj1.status < obj2.status ? -1 : (obj1.status > obj2.status ? 1 : 0)));

      this.setState({ members });
    }).catch(() => {
      // console.log(err);
    });
  }

  searchByStatus = (tag) => {
    const tags = tag.includes('-1') ? tag.concat(['0', '1', '2', '3', '4']) : tag;
    this.setState({ tags });
  }

  showModalSettings = (member) => {
    this.setState({ visibleModalSettings: true, member });
  }

  showModalChangePassword = (member) => {
    this.setState({ visibleModalPassword: true, member });
  }

  hideModal = () => {
    this.setState({
      visibleModalSettings: false,
      visibleModalPassword: false,
      member: ''
    });

    this.props.form.resetFields();
  }

  compareToFirstPassword = (rule, value, callback) => {
    if (value && value !== this.props.form.getFieldValue('password')) {
      callback('Senhas Diferentes!');
    } else {
      callback();
    }
  }

  validateToNextPassword = (rule, value, callback) => {
    if (value && this.props.form.getFieldValue('confirmPassword')) {
      this.props.form.validateFields(['confirmPassword'], { force: true });
    }
    callback();
  }

  filteredOptions = (value, option) => {
    if (!option.props.label && option.key.toLowerCase().includes(value.toLowerCase())) return true;

    return false;
  }

  handleStatus = (status) => {
    this.setState({ changeStatusLoading: true });
    const { member } = this.state;

    if (getStatus() > 4) {
      this.setState({ changeStatusLoading: false });
      notification.error({
        message: 'Errooouu!',
        description: 'Tem problema aqui, ó: Você não tem autorização, mané'
      });
    } else {
      const hasUserWithSameStatus = this.state.members.map(e => e.status).includes(status);

      if (hasUserWithSameStatus && status < 5) {
        this.setState({ changeStatusLoading: false });
        notification.error({
          message: 'Errooouu!',
          description: 'Tem problema aqui, ó: Não pode ter 2 diretores com o mesmo cargo!'
        });
      } else {
        axios.put('/api/members', {
          id: member._id, status: status.toString()
        }).then(() => {
          this.setState({ changeStatusLoading: false });
          this.hideModal();

          notification.success({
            message: 'Uhull',
            description: 'Status Atualizado com sucesso!'
          });

          this.componentDidMount();
        }).catch(() => {
          this.setState({ changeStatusLoading: false });
          // console.log(err);

          this.hideModal();

          notification.error({
            message: 'Errooouu!',
            description: 'Tem problema aqui, ó: Não sabemos ao certo, tente novamente, se persistirem os sintomas o médico deverá ser consultado, ops, o técnico.'
          });
        });
      }
    }
  }

  handlePasswords = (e) => {
    this.setState({ passwordsButtonLoading: true });
    e.preventDefault();

    this.props.form.validateFields((err, values) => {
      if (getStatus() > 4) {
        this.setState({ passwordsButtonLoading: false });

        notification.error({
          message: 'Errooouu!',
          description: 'Tem problema aqui, ó: Você não tem autorização, mané'
        });
      } else if (!err) {
        const { password } = values;

        axios.put('/api/members', { id: this.state.member._id, password }).then(() => {
          this.setState({ passwordsButtonLoading: false });
          this.hideModal();

          notification.success({
            message: 'Uhull',
            description: 'Senha Atualizada com sucesso!'
          });
        }).catch(() => {
          // console.log(error);
          this.setState({ passwordsButtonLoading: false });
          this.hideModal();

          notification.error({
            message: 'Errooouu!',
            description: 'Tem problema aqui, ó: Não sabemos ao certo, tente novamente, se persistirem os sintomas o médico deverá ser consultado, ops, o técnico.'
          });
        });
      } else {
        this.setState({ passwordsButtonLoading: false });
      }
    });
  }

  render() {
    if (this.state.nav) return (<Redirect to = { this.state.nav } />);
    if (['-1'].includes(getStatus())) return (<Redirect to = "/notAuthorizate" />);

    const { getFieldDecorator, getFieldValue, getFieldError } = this.props.form;

    return (
      <MainLayout page = "members">
        <Spin spinning = { this.state.loadingPage }>
          <Row style = {{ marginBottom: 0 }}>
            <Title id = "info" level = {2} style = {{ fontWeight: 500 }}> Polvilhosss :D </Title>

            <Divider style = {{ marginTop: 0 }} />
          </Row>

          <Row gutter = {16} type = "flex" justify = "end">
            <Col span = {12}>
              <Search
                placeholder = "Pesquise pelo nome, username ou RA"
                onChange = { e => this.searchByName(e) }
                style = {{ marginBottom: 18 }}
                size = "default"
              />
            </Col>

            <Col span = {6}>
              <Select className = "membersSelectByCargo" style = {{ width: '100%' }} mode = "multiple" placeholder = "Pesquise por Cargos" filterOption = { this.filteredOptions } onChange = { event => this.searchByStatus(event) }>
                <Option key = "Diretoria" value = "-1">
                  <Tag color = "#3867D6"> Diretoria </Tag>
                </Option>
                { usersStatus.map((item, index) => (
                  index > 4 ? (
                    <Option key = { item.title } value = { index.toString() }>
                      <Tag color = { item.color }> { item.title } </Tag>
                    </Option>
                  ) : null
                )) }
              </Select>
            </Col>

            { getStatus() < 5 ? (
              <Link to = "/memberSignUp">
                <Button type = "primary" icon = "plus"> Adicionar Membros </Button>
              </Link>
            ) : null }
          </Row>

          <Row style = {{ marginBottom: 50 }}>
            <Col span = {22} offset = {1}>
              <List
                grid = {{ gutter: 16, column: 4 }}
                size = "small"
                dataSource = { this.state.members }
                renderItem = { (item) => (
                  this.state.tags.length === 0 || this.state.tags.includes(item.status.toString()) ? (
                    <Col style = {{ paddingTop: 30 }} span = {6}>
                      <Card bordered hoverable>
                        <Avatar size = {70} src = { item.image } />
                        <Popover placement = "bottomRight" title = { usersStatus[item.status].title } content = { usersStatus[item.status].label }>
                          <Avatar icon = { usersStatus[item.status].icon } style = {{ cursor: 'pointer', verticalAlign: 'bottom', marginLeft: '-26px', marginBottom: '-10px', color: 'white', backgroundColor: usersStatus[item.status].color }} size = {30} />
                        </Popover>

                        <Popover placement = "top" content = "Ver Perfil">
                          <Button size = "small" style = {{ position: 'absolute', right: 10, top: 10 }} type = "primary" onClick = { () => this.setState({ nav: `/profile/${item.username}` }) } icon = "eye" />
                        </Popover>
                        { getStatus() < 5 ? ([
                          <Button.Group key = { item._id } size = "small" style = {{ position: 'absolute', right: 46, top: 10 }}>
                            <Popover placement = "top" content = "Alterar Senha">
                              <Button onClick = { () => this.showModalChangePassword(item) } icon = "lock" />
                            </Popover>

                            <Popover placement = "top" content = "Configurações">
                              <Button onClick = { () => this.showModalSettings(item) } icon = "setting" />
                            </Popover>
                          </Button.Group>
                        ]) : null }

                        <Meta
                          style = {{ marginTop: 20 }}
                          title = { item.name }
                          description = {[<Text key = { item._id } type = "secondary"> { usersStatus[item.status].title } </Text>]}
                        />
                      </Card>
                    </Col>
                  ) :
                    <span />
                )}
              />
            </Col>
          </Row>

          <Modal visible = { this.state.visibleModalSettings } footer = { null } onCancel = { this.hideModal } centered>
            <Row type = "flex" justify = "center">
              <Avatar size = {60} src = { this.state.member.image } />
            </Row>
            <Paragraph style = {{ fontSize: 30, textAlign: 'center', marginBottom: 5 }}> Configurações </Paragraph>

            <Divider style = {{ minWidth: '60%', width: '60%', marginTop: 0, marginLeft: 'auto', marginRight: 'auto' }}>
              <Icon type = "setting" />
            </Divider>

            <Title level = {4} style = {{ textAlign: 'left', marginBottom: 0, fontWeight: 500 }}> { this.state.member.name } </Title>
            <Text type = "secondary" style = {{ textAlign: 'left' }}> { this.state.member ? usersStatus[this.state.member.status].title : null } </Text>

            <Spin spinning = { this.state.changeStatusLoading }>
              <Paragraph style = {{ marginTop: 20, textAlign: 'center' }}>
                Troca de Cargo: <br />
                { usersStatus.map((item, index) => (
                  this.state.member.status !== index ? (
                    <Tag style = {{ marginTop: 4 }} key = { item.title } onClick = { () => this.handleStatus(index)} color = { usersStatus[index].color }>
                      <Icon type = "retweet" /> { usersStatus[index].title }
                    </Tag>
                  ) : null
                )) }
              </Paragraph>
            </Spin>
          </Modal>

          <Modal visible = { this.state.visibleModalPassword } footer = { null } onCancel = { this.hideModal } centered>
            <Row type = "flex" justify = "center">
              <Avatar size = {60} src = { this.state.member.image } />
            </Row>
            <Paragraph style = {{ fontSize: 30, textAlign: 'center', marginBottom: 5 }}> Trocar Senhas </Paragraph>

            <Divider style = {{ minWidth: '60%', width: '60%', marginTop: 0, marginLeft: 'auto', marginRight: 'auto' }}>
              <Icon type = "lock" />
            </Divider>

            <Row style = {{ marginTop: 20 }} gutter = {16}>
              <Form onSubmit = { this.handlePasswords }>
                <Row gutter = {16}>
                  <Col span = {12}>
                    <Form.Item label = "Senha">
                      { getFieldDecorator('password', {
                        rules: [
                          { required: true, whitespace: true, message: 'Por favor, insira uma nova senha!' },
                          { min: 6, message: 'Por favor, insira uma senha maior que 5!' },
                          { validator: this.validateToNextPassword }
                        ]
                      })(
                        <Input.Password
                          prefix = { <Icon type = "lock" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                          placeholder = "Nova Senha"
                        />
                      )}
                    </Form.Item>
                  </Col>

                  <Col span = {12}>
                    <Form.Item label = "Confirmar Senha" hasFeedback validateStatus = { getFieldError('confirmPassword') ? 'error' : getFieldValue('confirmPassword') ? 'success' : '' }>
                      { getFieldDecorator('confirmPassword', {
                        rules: [
                          { required: true, whitespace: true, message: 'Por favor, insira uma senha!' },
                          { min: 6, message: 'Por favor, insira uma senha maior que 5!' },
                          { validator: this.compareToFirstPassword }
                        ]
                      })(
                        <Input
                          type = "password"
                          prefix = { <Icon type = "lock" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                          placeholder = "Repita a Nova Senha"
                        />
                      )}
                    </Form.Item>
                  </Col>
                </Row>

                <Row style = {{ textAlign: 'right' }}>
                  <Button size = "default" onClick = { this.hideModal } style = {{ marginRight: 8 }}> Cancelar </Button>
                  <Button loading = { this.state.passwordsButtonLoading } type = "primary" htmlType = "submit" size = "default"> Cadastrar Nova Senha </Button>
                </Row>
              </Form>
            </Row>
          </Modal>
        </Spin>
      </MainLayout>
    );
  }
}

export default Form.create()(MemberMainPage);
