// menu.js

import React, { Component } from 'react';
import { Menu, Layout, Icon } from 'antd';
import { Link } from 'react-router-dom';

import { getStatus } from '../../services/auth';
import './menu.css';

const { Sider } = Layout;

export default class MenuContent extends Component {
  constructor(props) {
    super(props);

    this.state = {
      status: getStatus()
    };
  }

  render() {
    return (
      <Sider style = {{ background: '#FFF' }} collapsedWidth = "60" collapsed>
        { this.state.status >= 0 ? (
          <Menu style = {{ height: '100%', paddingTop: '60px' }} mode = "inline" defaultSelectedKeys = {[this.props.page]}>
            <Menu.Item key = "home">
              <Link to = "/home">
                <Icon type = "home" />
                <span> Página Principal </span>
              </Link>
            </Menu.Item>

            <Menu.Item key = "members">
              <Link to = "/members">
                <Icon type = "team" />
                <span> Nossa Equipe </span>
              </Link>
            </Menu.Item>

            <Menu.Item key = "customers">
              <Link to = "/customers">
                <Icon type = "contacts" />
                <span> Nossos Clientes </span>
              </Link>
            </Menu.Item>

            <Menu.Item key = "projects">
              <Link to = "/projects">
                <Icon type = "laptop" />
                <span> Nossos Projetos </span>
              </Link>
            </Menu.Item>

            { this.state.status && this.state.status >= '0' && this.state.status <= '4' ? (
              <Menu.Item key = "files">
                <Link to = "/files">
                  <Icon type = "folder-open" />
                  <span> Arquivos </span>
                </Link>
              </Menu.Item>
            ) : null }

            <Menu.Item key = "calendar">
              <Link to = "/calendar">
                <Icon type = "calendar" />
                <span> Calendário </span>
              </Link>
            </Menu.Item>

            <Menu.Item key = "goals">
              <Link to = "/goals">
                <Icon type = "flag" />
                <span> Metas </span>
              </Link>
            </Menu.Item>

            <Menu.Item key = "gamification">
              <Link to = "/gamification">
                <Icon type = "trophy" />
                <span> Gamification </span>
              </Link>
            </Menu.Item>
          </Menu>
        ) : (
          <Menu style = {{ height: '100%', paddingTop: '60px' }} mode = "inline" defaultSelectedKeys = {[this.props.page]}>
            <Menu.Item key = "projects">
              <Link to = "/projects">
                <Icon type = "laptop" />
                <span> Seus Projetos </span>
              </Link>
            </Menu.Item>
          </Menu>
        ) }
      </Sider>
    );
  }
}
