// profile.js

import React, { Component } from 'react';
import { DatePicker, Divider, Row, Col, Select, Typography, Button, Icon, Skeleton, Card, Tabs, Tag, Avatar, Popover, Tooltip, notification, List } from 'antd';
import { Redirect, Link } from 'react-router-dom';

import axios from 'axios';
import moment from 'moment';

import { getStatus, getID } from '../../services/auth';

import MainLayout from '../../components/layout/layout';

import './profile.css';

const { Title, Text, Paragraph } = Typography;
const { TabPane } = Tabs;
const { Option } = Select;

const usersStatus = [
  { color: '#FED330', icon: 'star', title: 'Presidente', label: 'Rei da Cocada Preta' },
  { color: '#26DE81', icon: 'star', title: 'Vice-Presidente/Diretor de RH', label: 'Manda só quando o presidente não está.' },
  { color: '#45AAF2', icon: 'crown', title: 'Diretor de Projetos', label: 'The Best' },
  { color: '#0FB9B1', icon: 'star', title: 'Diretor Administrativo-Financeiro', label: 'Contando Centavos...' },
  { color: '#A55EEA', icon: 'star', title: 'Diretor de Marketing', label: 'Criando umas Artes' },
  { color: '#FD9644', icon: 'thunderbolt', title: 'Membro', label: 'Abastecendo-se de Café' },
  { color: '#FC5C65', icon: 'fire', title: 'Trainee', label: 'Sofrendo' },
  { color: '#4B6584', icon: 'bulb', title: 'Ancião', label: 'Os Baqueados' }
];

const projectStatus = [
  { color: '#3867D6', icon: 'deployment-unit', title: 'Planejamento', label: 'Começando o sofrimento denovo.' },
  { color: '#FA8231', icon: 'rocket', title: 'Inicializando', label: 'Fechando contrato e preparando as almas de membros para o abate.' },
  { color: '#45AAF2', icon: 'coffee', title: 'Em Andamento', label: 'Abastecendo os membros com café para manter o progresso.' },
  { color: '#778CA3', icon: 'frown', title: 'Cancelado', label: 'Cliente deu pra trás.' },
  { color: '#EB3B5A', icon: 'stop', title: 'Congelado', label: 'Sei lá, deu uma parada por agora.' },
  { color: '#20BF6B', icon: 'like', title: 'Acabou', label: 'Acaboooouuuu!!' }
];

const projectLabels = [
  { color: '#F7B731', title: 'Consultoria', icon: 'book' },
  { color: '#FED330', title: 'Site', icon: 'desktop' },
  { color: '#26DE81', title: 'Aplicativo', icon: 'mobile' },
  { color: '#0FB9B1', title: 'Sistema Web', icon: 'ie' },
  { color: '#2BCBBA', title: 'Sistema Interno', icon: 'home' }
];

const tagColors = ['#FC5C65', '#FA8231', '#FD9644', '#F7B731', '#FED330', '#26DE81', '#0FB9B1', '#2BCBBA', '#2D98DA', '#45AAF2', '#4B7BEC', '#8854D0', '#A55EEA', '#4B6584', '#778CA3'];

const CLIENT_ID = 'a909ba7f264d474e8f36';
const CLIENT_SECRET = '162fa8dcbaf50db000b185bc8678cf8aa119d272';

export default class Profile extends Component {
  constructor(props) {
    super(props);

    this.state = {
      nav: '',
      user: '',
      userGithub: '',
      programmingLanguages: [],
      commitsCount: [],
      additions: 0,
      deletions: 0,
      tab: '1',
      projects: [],
      currentPoints: 0,
      loadingProjects: true,
      loadingTasks: false,
      loadingMoreTasks: false,
      initLoadingTasks: true,
      userList: [],
      userListAux: [],
      countTask: 7,
      yearSearch: moment(),
      openYearSearch: false,
      semesterSearch: moment().month() <= 5 ? '0' : '1'
    };
  }

  componentDidMount() {
    this.getUserDataForProfile(this.props.match.params.username);
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    this.getUserDataForProfile(nextProps.match.params.username);
  }

  getUserDataForProfile = (username) => {
    this.setState({
      nav: '',
      user: '',
      userGithub: '',
      programmingLanguages: [],
      commitsCount: [],
      additions: 0,
      deletions: 0,
      tab: '1',
      projects: [],
      currentPoints: 0,
      loadingProjects: true,
      loadingTasks: false,
      loadingMoreTasks: false,
      initLoadingTasks: true,
      userList: [],
      userListAux: [],
      countTask: 7,
      yearSearch: moment(),
      openYearSearch: false,
      semesterSearch: moment().month() <= 5 ? '0' : '1'
    });

    axios.get('/api/task').then(resTasks => {
      const tasks = resTasks.data;

      axios.get(`/api/members/searchProfile/${username}`).then(res => {
        if (!res.data) this.setState({ nav: '/notFound' });
        else if (!res.data.github_code) {
          this.setState({ user: res.data });
          this.loadProjects();
          notification.warning({
            message: 'Opps!',
            description: 'Este usuário não sincronizou a conta com o Github'
          });
        } else {
          const config = { headers: { Authorization: `token ${res.data.github_code}` } };

          const points = [];
          res.data.points.forEach(item => {
            const task = tasks[tasks.map(e => e._id).indexOf(item.task[0])];
            if (task) {
              points.push({
                task,
                point: item.point,
                date: item.date
              });
            }
          });

          res.data.points = points;
          res.data.points.sort((obj1, obj2) => (moment(obj1.date).isBefore(moment(obj2.date)) ? 1 : (moment(obj1.date).isAfter(moment(obj2.date)) ? -1 : 0)));

          const userList = [];
          const userListAux = [];
          let currentPoint = 0;
          let index = 0;
          res.data.points.forEach((item) => {
            if (moment(item.date).format('YYYY') === this.state.yearSearch.format('YYYY') && ((moment(item.date).month() <= 5 && this.state.semesterSearch === '0') || (moment(item.date).month() > 5 && this.state.semesterSearch === '1'))) {
              userListAux.push(item);

              currentPoint += Number(item.point);

              if (index < this.state.countTask) {
                userList.push(item);
              }
              index++;
            }
          });

          this.setState({ user: res.data, currentPoints: currentPoint, initLoadingTasks: false, userList, userListAux });
          axios.get('https://api.github.com/user', config).then(resp => {
            this.setState({ userGithub: resp.data });
            this.loadProjects();

            axios.get('https://api.github.com/user/repos?per_page=100', config).then(respRepo => {
              let programmingLanguages = [];
              let commitsCount = [];
              let additions = 0;
              let deletions = 0;

              respRepo.data.forEach(item => {
                axios.get(`${item.languages_url}?per_page=100&client_id=${CLIENT_ID}&client_secret=${CLIENT_SECRET}`, config).then(respLanguage => {
                  programmingLanguages = programmingLanguages.concat(Object.keys(respLanguage.data));
                  this.setState({ programmingLanguages: [...new Set(programmingLanguages)] });
                });

                axios.get(`https://api.github.com/repos/${item.owner.login}/${item.name}/commits?author=${this.state.userGithub.login}&per_page=100&client_id=${CLIENT_ID}&client_secret=${CLIENT_SECRET}`, config).then(respCommits => {
                  commitsCount = commitsCount.concat(respCommits.data);
                  this.setState({ commitsCount });

                  respCommits.data.forEach(commit => {
                    axios.get(`${commit.url}?client_id=${CLIENT_ID}&client_secret=${CLIENT_SECRET}`, config).then(respStats => {
                      additions += respStats.data.stats.additions;
                      deletions += respStats.data.stats.deletions;

                      this.setState({ additions, deletions });
                    });
                  });
                });
              });
            });
          }).catch(() => {
            // console.log(err);

            notification.error({
              message: 'Errooouu!',
              description: 'Tem problema aqui, ó: Não conseguimos ler dados do Github.'
            });
          });
        }
      }).catch(() => {
        // console.log(err);

        notification.error({
          message: 'Errooouu!',
          description: 'Tem problema aqui, ó: Não conseguimos ler do Servidor.'
        });
      });
    }).catch(() => {
      // console.log(err);

      notification.error({
        message: 'Errooouu!',
        description: 'Tem problema aqui, ó: Não conseguimos ler do Servidor.'
      });
    });
  }

  loadProjects = () => {
    axios.get('/api/projects').then(resProjects => {
      axios.get('/api/members').then(resMembers => {
        axios.get('/api/customers').then(resCustomers => {
          const customers = resCustomers.data;
          const projects = [...new Set(resProjects.data.filter(e => e.leader[0] === this.state.user._id).concat(resProjects.data.filter(e => e.members[0].includes(this.state.user._id))))];

          projects.forEach((item, index) => {
            projects[index].leader = resMembers.data[resMembers.data.map(e => e._id).indexOf(item.leader[0])];
            projects[index].customer = customers[customers.map(e => e._id).indexOf(item.customer[0])];

            const members = item.members[0];
            if (item.members[0].length) {
              item.members[0].forEach((itemMember, indexMember) => {
                members[indexMember] = resMembers.data[resMembers.data.map(e => e._id).indexOf(itemMember)];
              });
              projects[index].members = members;
            } else {
              projects[index].members = [members];
            }
          });

          this.setState({ projects, loadingProjects: false });
        });
      });
    });
  }

  onChangeTab = (key) => this.setState({ tab: key });

  getNextBirthDay = (date) => {
    const birthday = moment(date);
    const today = moment().format('YYYY-MM-DD');

    const age = moment(today).diff(birthday, 'years');
    moment(age).format('YYYY-MM-DD');

    const nextBirthday = moment(birthday).add(age + 1, 'years');
    return moment(nextBirthday).format('YYYY-MM-DD');
  }

  onLoadMoreTasks = () => {
    const userList = [];
    const countTask = this.state.countTask + 7;

    this.setState({ loadingMoreTasks: true, countTask });

    this.state.userListAux.forEach((item, i) => {
      if (i < countTask) {
        userList.push(item);
      }
    });

    this.setState({
      userList,
      loadingMoreTasks: false
    });
  }

  searchByYear = (yearSearch) => {
    const userListAux = this.state.user.points.filter((item) => moment(item.date).format('YYYY') === yearSearch.format('YYYY') && ((moment(item.date).month() <= 5 && this.state.semesterSearch === '0') || (moment(item.date).month() > 5 && this.state.semesterSearch === '1')));
    const userList = [];
    const countTask = 7;

    if (userListAux) {
      userListAux.forEach((item, i) => {
        if (i < countTask) {
          userList.push(item);
        }
      });
    }

    this.setState({ yearSearch, countTask, userListAux, userList, openYearSearch: false });
  }

  searchBySemester = (semesterSearch) => {
    const userListAux = this.state.user.points.filter((item) => moment(item.date).format('YYYY') === this.state.yearSearch.format('YYYY') && ((moment(item.date).month() <= 5 && semesterSearch === '0') || (moment(item.date).month() > 5 && semesterSearch === '1')));
    const userList = [];
    const countTask = 7;

    if (userListAux) {
      userListAux.forEach((item, i) => {
        if (i < countTask) {
          userList.push(item);
        }
      });
    }

    this.setState({ semesterSearch, countTask, userListAux, userList, openYearSearch: false });
  }

  render() {
    if (this.state.nav) return (<Redirect to = { this.state.nav } />);
    if (['-1'].includes(getStatus())) return (<Redirect to = "/notAuthorizate" />);

    const { initLoadingTasks, loadingMoreTasks } = this.state;
    const loadMoreTasks =
    !initLoadingTasks && !loadingMoreTasks ? (
      <div
        style = {{
          textAlign: 'center',
          marginTop: 12,
          height: 32,
          lineHeight: '32px'
        }}
      >
        { this.state.userList.length !== this.state.userListAux.length ? (
          <Button onClick = {this.onLoadMoreTasks}> Carregar Mais </Button>
        ) : null }
      </div>
    ) : null;

    return (
      <MainLayout>
        <Row>
          <Col span = {16} offset = {4}>
            <Row>
              <Skeleton loading = { !this.state.user } active avatar>
                <Col span = {6}>
                  <Avatar style = {{ cursor: 'pointer' }} src = { this.state.user.image } size = {150} />
                  { this.state.user ? (
                    <Popover placement = "bottomRight" title = { usersStatus[this.state.user.status].title } content = { usersStatus[this.state.user.status].label }>
                      <Avatar icon = { usersStatus[this.state.user.status].icon } style = {{ cursor: 'pointer', verticalAlign: 'bottom', marginLeft: '-45px', color: 'white', backgroundColor: usersStatus[this.state.user.status].color }} size = {36} />
                    </Popover>
                  ) : null }
                </Col>

                <Col span = {18}>
                  <Row>
                    <Title level = {3} style = {{ marginBottom: 0 }} ellipsis>
                      { this.state.user.name } &nbsp;
                      <Popover placement = "bottom" content = { <Text> RA </Text> }>
                        <Text strong>
                          <Tag style = {{ verticalAlign: 3 }} color = { this.state.user ? usersStatus[this.state.user.status].color : null }> { this.state.user.RA } </Tag>
                        </Text>
                        <br />
                      </Popover>
                    </Title>
                    <Text type = "secondary" style = {{ marginTop: 0 }}> @{ this.state.user.username } </Text>
                  </Row>

                  <Row style = {{ marginTop: 15 }}>
                    <Text strong> { this.state.projects.length }</Text>
                    <Text> Filhos Desenvolvidos </Text>
                  </Row>

                  <Row>
                    <Text> Pontuação Atual:  </Text>
                    <Text strong> {this.state.currentPoints} </Text>
                  </Row>

                  <Row style = {{ marginTop: 10 }}>
                    <Tabs activeKey = { this.state.tab } onChange = { this.onChangeTab } tabPosition = "bottom">
                      <TabPane tab = { <span> <Icon type = "idcard" /> Informações Básicas </span> } key = "1" />
                      <TabPane tab = { <span> <Icon type = "project" /> Projetos </span> } key = "2" />
                      <TabPane tab = { <span> <Icon type = "trophy" /> Meus Pontos </span> } key = "3" />
                    </Tabs>
                  </Row>
                </Col>
              </Skeleton>
            </Row>
          </Col>

          { this.state.user._id === getID() ? (
            <Link to = "/edit/profile">
              <Button style = {{ marginLeft: 20 }}>
                Editar Perfil <Icon type = "edit" />
              </Button>
            </Link>
          ) : null }
        </Row>

        <Row style = {{ marginTop: 50, marginBottom: 50 }}>
          <Col span = {16} offset = {4}>
            { this.state.tab === '1' ? (
              <Row gutter = {16}>
                <Col span = {8}>
                  <Card
                    hoverable
                    loading = { !this.state.userGithub }
                    cover = {(
                      <Skeleton className = "skeleton-profile-github-account" loading = { !this.state.userGithub } active>
                        <img style = {{ height: 200, objectFit: 'cover' }} alt = "Github Profile" src = { this.state.userGithub.avatar_url } />
                      </Skeleton>
                    )}
                  >
                    <Card.Meta
                      title = {(
                        <span>
                          <a target = "_blank" rel = "noopener noreferrer" href = { this.state.userGithub.html_url }>
                            <Tooltip title = "Ver Perfil Online">
                              <Icon type = "link" />
                            </Tooltip>
                          </a> &nbsp; Informações do Github
                        </span>
                      )}
                      description = {(
                        <span>
                          { this.state.userGithub.login ? <Popover placement = "left" content = "Github Username"> <span> @  {this.state.userGithub.login} <br /> </span> </Popover> : null }
                          { this.state.userGithub.bio ? <Popover placement = "left" content = "Github Bio"> {this.state.userGithub.bio} <br /> </Popover> : null }
                          { this.state.userGithub.location ? <Popover placement = "left" content = "Localização"> <span role = "img" aria-label = "WorldMap"> 🗺️  {this.state.userGithub.location} <br /> </span> </Popover> : null }
                          { this.state.userGithub.blog ? <Popover placement = "left" content = "Blog"> <span role = "img" aria-label = "Global"> 🌐  {this.state.userGithub.blog} <br /> </span> </Popover> : null }
                          { this.state.userGithub.company ? <Popover placement = "left" content = "Empresa"> <span role = "img" aria-label = "Company"> 🏢  {this.state.userGithub.company} <br /> </span> </Popover> : null }
                          { this.state.userGithub.created_at ? <span> Desde: {moment(this.state.userGithub.created_at).format('DD/MM/YYYY')} <br /> </span> : null }
                        </span>
                      )}
                    />
                  </Card>
                </Col>

                <Col span = {8}>
                  <Card
                    extra = {(
                      <a href = { this.state.user ? `https://www.google.com/maps/search/?api=1&query=${`${this.state.user.location.address}, ${this.state.user.location.number} - ${this.state.user.location.neighborhood}, ${this.state.user.location.city} - ${this.state.user.location.uf}, ${this.state.user.location.cep}`}` : null} target = "_blank" rel = "noopener noreferrer">
                        <Tooltip title = "Ver Mapa Online"> <Icon type = "link" /> </Tooltip>
                      </a>
                    )}
                    loading = { !this.state.user }
                    title = "Endereço"
                    hoverable
                  >
                    <Card.Meta description = { this.state.user ? `${this.state.user.location.address}, ${this.state.user.location.number} - ${this.state.user.location.neighborhood}, ${this.state.user.location.city} - ${this.state.user.location.uf}, ${this.state.user.location.cep}. ${this.state.user.location.complement ? `(${this.state.user.location.complement})` : ''}` : null } />
                  </Card>

                  <Card loading = { !this.state.userGithub } cover = { <Text className = "ant-card-head-title" style = {{ marginTop: 15, textAlign: 'left', fontWeight: 500, fontSize: 16 }}> Estátisticas </Text> } style = {{ textAlign: 'center', border: 0 }}>
                    <Card.Grid style = {{ margin: 5, width: 'calc(50% - 10px)', padding: '20px 10px' }}>
                      <Avatar style = {{ color: '#3867D6', backgroundColor: '#3867D638' }}>
                        <Icon type = "appstore" rotate = {45} />
                      </Avatar>
                      <Title style = {{ marginBottom: 0, marginTop: 10 }} level = {3}> { this.state.userGithub.public_repos + this.state.userGithub.total_private_repos } </Title>
                      <Text type = "secondary"> repositórios </Text>
                    </Card.Grid>

                    <Card.Grid style = {{ margin: 5, width: 'calc(50% - 10px)', padding: '20px 10px' }}>
                      <Avatar style = {{ color: '#F7B731', backgroundColor: '#F7B73138' }}>
                        <Icon type = "to-top" />
                      </Avatar>
                      <Title style = {{ marginBottom: 0, marginTop: 10 }} level = {3}> { this.state.commitsCount.length } </Title>
                      <Text type = "secondary"> commits </Text>
                    </Card.Grid>

                    <Card.Grid style = {{ margin: 5, width: 'calc(50% - 10px)', padding: '20px 10px' }}>
                      <Avatar style = {{ color: '#20BF6B', backgroundColor: '#20BF6B38' }}>
                        <Icon type = "file-done" />
                      </Avatar>
                      <Title style = {{ marginBottom: 0, marginTop: 10 }} level = {3}> { this.state.additions } </Title>
                      <Text type = "secondary"> linhas novas </Text>
                    </Card.Grid>

                    <Card.Grid style = {{ margin: 5, width: 'calc(50% - 10px)', padding: '20px 10px' }}>
                      <Avatar style = {{ color: '#EB3B5A', backgroundColor: '#EB3B5A38' }}>
                        <Icon type = "diff" />
                      </Avatar>
                      <Title style = {{ marginBottom: 0, marginTop: 10 }} level = {3}> { this.state.deletions } </Title>
                      <Text type = "secondary"> linhas deletadas </Text>
                    </Card.Grid>
                  </Card>
                </Col>

                <Col span = {8}>
                  <Card
                    actions = {[
                      <Popover content = { <Paragraph style = {{ marginBottom: 0 }} copyable = {{ text: this.state.user.email }}> { this.state.user.email } </Paragraph> }>
                        <a rel = "noopener noreferrer" target = "_blank" href = {`mailto:${this.state.user.email}`}> <Icon type = "mail" /> </a>
                      </Popover>,
                      <Popover content = { <Paragraph style = {{ marginBottom: 0 }} copyable = {{ text: this.state.user.cellphone }}> { this.state.user.cellphone } </Paragraph> }>
                        <a rel = "noopener noreferrer" target = "_blank" href = {`https://wa.me/:${this.state.user.cellphone}`}> <Icon type = "phone" /> </a>
                      </Popover>
                    ]}
                    loading = { !this.state.user }
                    title = "Contato & Outras Informações"
                    hoverable
                  >
                    <Card.Meta
                      style = {{ textAlign: 'center' }}
                      description = { this.state.user ? (
                        <span>
                          <Text>
                            { this.state.user ? (
                              moment(this.getNextBirthDay(this.state.user.birth)).diff(moment(), 'days') === 0 ? (
                                <span>
                                  <Text style = {{ fontSize: 18 }} strong>
                                    <span role = "img" aria-label = "Aplause">
                                      🎉🎊 Feliz { moment().diff(this.state.user.birth, 'years') + 1 } aninhos 🎊🎉
                                    </span>
                                  </Text> <br />
                                  Hoje vai ter uma festa, 🎂 e 🥤, muitos 🍬🍭🍬🍭 pra você!! <br /><br />
                                  <Title style = {{ marginBottom: 0 }} level = {2}> <span role = "img" aria-label = "Applause"> 👏👏👏 </span> </Title>
                                </span>
                              ) : (
                                <span>
                                  <Text> { moment().diff(this.state.user.birth, 'years') } aninhos polvilho, ainda faltam </Text> <br />
                                  <Popover content = { `📆 Níver: ${moment(this.getNextBirthDay(this.state.user.birth)).format('DD/MM/YYYY')}` }>
                                    <Title style = {{ marginBottom: 0 }} level = {1}> { moment(this.getNextBirthDay(this.state.user.birth)).diff(moment(), 'days') } </Title>
                                  </Popover>
                                  <Text> dias para o aniversário de { this.state.user.username } </Text>
                                </span>
                              )
                            ) : null }
                          </Text>
                        </span>
                      ) : null }
                    />
                  </Card>

                  <Card loading = { !this.state.userGithub } style = {{ textAlign: 'center', marginTop: 15 }} title = "Linguagens Conhecidas" hoverable>
                    { this.state.programmingLanguages.map((item, index) => <Tag style = {{ margin: 5 }} key = { item } color = { tagColors[index % 15] }> { item } </Tag>) }
                  </Card>
                </Col>
              </Row>
            ) : this.state.tab === '2' ? (
              <List
                loading = { this.state.loadingProjects }
                grid = {{ gutter: 16, column: 4 }}
                size = "small"
                dataSource = { this.state.projects }
                renderItem = { (project) => (
                  <Card key = { project._id } bordered hoverable bodyStyle = {{ cursor: 'auto', paddingBottom: 6 }} style = {{ marginTop: 30 }}>
                    <Col span = {8}>
                      <img alt = "Imagem do Projeto" style = {{ width: '100%', maxHeight: 200, objectFit: 'cover' }} src = {project.image} />
                    </Col>

                    <Col span = {16}>
                      <Row style = {{ marginBottom: 15 }}>
                        <Title level = {3} style = {{ marginBottom: 0 }} ellipsis>
                          { project.name } &nbsp;
                          <Popover
                            overlayStyle = {{ width: 300 }}
                            title = { <Text> <Icon type = { projectStatus[project.status].icon } /> &nbsp; { projectStatus[project.status].title } </Text>}
                            content = {<Paragraph style = {{ marginBottom: 0 }}> { projectStatus[project.status].label } </Paragraph>}
                          >
                            <Tag style = {{ verticalAlign: 3, fontWeight: 600, cursor: 'pointer' }} color = { projectStatus[project.status].color }> { projectStatus[project.status].title } </Tag>
                          </Popover>

                          <Button type = "primary" onClick = { () => this.setState({ nav: `/insideProject/${project._id}` }) } icon = "eye" style = {{ position: 'absolute', right: 0 }} size = "small" />
                        </Title>
                      </Row>

                      <Tabs tabPosition = "bottom">
                        <TabPane tab = { <span> <Icon type = "laptop" /> Projeto </span> } key = "1">
                          <Row style = {{ marginBottom: 15, minHeight: 120 }}>
                            { project.customer ? (
                              <Col span = {12} style = {{ margin: 'auto' }}>
                                <Paragraph style = {{ marginBottom: 10 }}>
                                  <Text strong> Cliente: </Text>
                                  <Text> { project.customer.name } </Text>
                                </Paragraph>
                              </Col>
                            ) : null }

                            <Col span = {12} style = {{ margin: 'auto' }}>
                              <Paragraph style = {{ marginBottom: 10 }}>
                                <Text strong> Preço: </Text>
                                <Text> R$ {project.cost.toString().replace(/[^\d]/g, '').replace(/\B(?=(\d{3})+(?!\d))/g, '.')},00 </Text>
                              </Paragraph>
                            </Col>

                            { project.labels.length > 0 ? (
                              <Col span = {12} style = {{ margin: 'auto' }}>
                                <Paragraph style = {{ marginBottom: 10 }}>
                                  <Text strong> Rótulos: </Text>
                                  { project.labels.map(label => (
                                    <Popover key = { label } placement = "top" content = { projectLabels[label].title }>
                                      <Tag style = {{ marginBottom: 3, marginRight: 2, fontWeight: 600 }} key = { label } color = { projectLabels[label].color }> <Icon type = { projectLabels[label].icon } /> </Tag>
                                    </Popover>
                                  )) }
                                </Paragraph>
                              </Col>
                            ) : null }

                            { project.description ? (
                              <Col span = {24} style = {{ margin: 'auto' }}>
                                <Paragraph ellipsis style = {{ marginBottom: 10 }}>
                                  <Text strong> Descrição: </Text>
                                  <Text> { project.description } </Text>
                                </Paragraph>
                              </Col>
                            ) : null }
                          </Row>
                        </TabPane>

                        <TabPane tab = { <span> <Icon type = "team" /> Membros </span> } key = "2">
                          <List
                            style = {{ marginBottom: 15, minHeight: 120 }}
                            grid = {{ gutter: 16, column: 2 }}
                            size = "small"
                            dataSource = { [...new Set([project.leader].concat(project.members))] }
                            renderItem = { member => (
                              <Col key = { member._id } span = {12} style = {{ paddingLeft: 20 }}>
                                <Link to = {`/profile/${member.username}`}>
                                  <Avatar size = {30} style = {{ marginBottom: 5 }} src = { member.image } />
                                  { member === project.leader ? (
                                    <Popover placement = "top" content = "Lider do Projeto">
                                      <Avatar icon = "crown" style = {{ cursor: 'pointer', position: 'absolute', bottom: 0, left: 40, backgroundColor: '#F7B731' }} size = {20} />
                                    </Popover>
                                  ) : null }

                                  <Text style = {{ marginLeft: 11 }} ellipsis>{member.username}</Text>
                                </Link>
                              </Col>
                            )}
                          />
                        </TabPane>
                      </Tabs>
                    </Col>
                  </Card>
                )}
              />
            ) : (
              <Row style = {{ marginBottom: 0 }}>
                <Row gutter = {16} type = "flex" justify = "end" align = "top">
                  <Col span = {14}>
                    <Title id = "info" level = {3} style = {{ fontWeight: 500 }}> Suas Atividades :P </Title>
                  </Col>

                  <Col span = {4}>
                    <Select
                      onChange = { e => this.searchBySemester(e) }
                      value = {this.state.semesterSearch}
                      className = "goalSelectBy"
                      style = {{ width: '100%' }}
                      placeholder = "Selecione uma Meta"
                    >
                      <Option value = "0"> 1º Semestre </Option>
                      <Option value = "1"> 2º Semestre </Option>
                    </Select>
                  </Col>

                  <Col span = {6}>
                    <DatePicker
                      onChange = { event => this.searchByYear(event) }
                      allowClear = {false}
                      onPanelChange = { event => this.searchByYear(event) }
                      onOpenChange = { (openYearSearch) => this.setState({ openYearSearch }) }
                      open = { this.state.openYearSearch }
                      value = { this.state.yearSearch }
                      format = "YYYY"
                      mode = "year"
                    />
                  </Col>

                  <Divider style = {{ marginTop: 0 }} />
                </Row>

                <List
                  loading = {this.state.loadingTasks}
                  itemLayout = "horizontal"
                  locale = {{ emptyText: 'Você não ganhou nenhum ponto ainda, mas não fique triste ainda dá tempo 😛' }}
                  loadMore = {loadMoreTasks}
                  dataSource = {this.state.userList}
                  renderItem = {item => (
                    <List.Item>
                      <Skeleton title = {false} loading = {item.loading} active>
                        <List.Item.Meta
                          title = {item.task.name}
                          description = {`Pontuação: ${item.point} | Data: ${moment(item.date).format('L')}`}
                        />
                      </Skeleton>
                    </List.Item>
                  )}
                />
              </Row>
            ) }
          </Col>
        </Row>
      </MainLayout>
    );
  }
}
