// editProfileCustomer.js

import React, { Component } from 'react';
import { Anchor, Row, Col, Form, Select, Input, InputNumber, Button, Upload, Avatar, Typography, Divider, Icon, Spin, notification, message } from 'antd';

import axios from 'axios';

import { getID } from '../../services/auth';

import './profile.css';

const { Link } = Anchor;
const { Title } = Typography;
const { Option } = Select;

class editProfileCustomer extends Component {
  constructor(props) {
    super(props);

    this.state = {
      user: '',
      users: [],
      hasAddress: true,
      hasNeighborhood: true,
      newProfilePhoto: [],
      infoButtonloading: false,
      contactInfoButtonloading: false,
      changeProfilePhotoButtonloading: false,
      changePasswordButtonloading: false
    };
  }

  componentDidMount() {
    axios.get('/api/customers').then(res => {
      this.setState({ users: res.data });
    });

    axios.get(`/api/customers/id/${getID()}`).then(res => {
      this.setState({ user: res.data, newProfilePhoto: [] });

      this.props.form.setFieldsValue({
        document: res.data.document,
        documentType: res.data.documentType,
        username: res.data.username,
        name: res.data.name,
        email: res.data.email,
        cellphone: res.data.cellphone,
        cep: res.data.location.cep,
        address: res.data.location.address,
        neighborhood: res.data.location.neighborhood,
        city: res.data.location.city,
        number: res.data.location.number,
        complement: res.data.location.complement,
        uf: res.data.location.uf,
        password: '',
        confirmPassword: ''
      });
    });
  }

  documentValidation = (rule, value, callback) => {
    const { users } = this.state;
    const valueFormatted = this.documentMask(value);

    this.props.form.setFieldsValue({ document: valueFormatted });

    if (valueFormatted) {
      if (users[users.map((e) => e.document).indexOf(valueFormatted)] && valueFormatted !== this.state.user.document) {
        callback('Documento já Cadastrado!');
      } else if (valueFormatted.length !== 14 && this.props.form.getFieldValue('documentType') === 0) {
        callback('CPF deve ter 11 dígitos!');
      } else if (valueFormatted.length !== 18 && this.props.form.getFieldValue('documentType') === 1) {
        callback('CPNJ deve ter 14 dígitos!');
      } else {
        callback();
      }
    } else {
      callback();
    }

    if (this.props.form.isFieldValidating('document') === undefined) {
      this.props.form.validateFields(['document']);
    }
  }

  usernameValidation = (rule, value, callback) => {
    const { users } = this.state;

    this.props.form.setFieldsValue({ username: value.replace(/ /g, '') });

    if (users[users.map((e) => e.username).indexOf(value)] && value !== this.state.user.username) {
      callback('Username já Cadastrado!');
    } else {
      callback();
    }
  }

  emailValidation = (rule, value, callback) => {
    const { users } = this.state;

    if (users[users.map((e) => e.email).indexOf(value)] && value !== this.state.user.email) {
      callback('Email já Cadastrado!');
    } else {
      callback();
    }
  }

  cellphoneValidation = (rule, value, callback) => {
    const valueFormatted = value ? value.substring(0, 16).replace(/[^\d]/g, '').replace(/^(\d{2})(\d{1})(\d{4})(\d)/, '($1) $2 $3-$4') : '';
    this.props.form.setFieldsValue({ cellphone: valueFormatted });

    if (valueFormatted && valueFormatted.toString().length !== 16) {
      callback('Telefone deve ter 16 dígitos!');
    } else {
      callback();
    }

    if (this.props.form.isFieldValidating('cellphone') === undefined) {
      this.props.form.validateFields(['cellphone']);
    }
  }

  cepValidation = async (rule, value, callback) => {
    const valueFormatted = value ? value.substring(0, 9).replace(/[^\d]/g, '').replace(/^(\d{5})(\d)/, '$1-$2') : '';
    this.props.form.setFieldsValue({ cep: valueFormatted });

    if (valueFormatted && valueFormatted.length === 9) {
      try {
        const res = await axios.get(`http://api.postmon.com.br/v1/cep/${valueFormatted}`);

        if (res.status === 200) {
          this.props.form.setFieldsValue({
            city: res.data.cidade,
            uf: res.data.estado
          });

          this.setState({ hasAddress: true, hasNeighborhood: true });

          if (!res.data.logradouro) {
            this.setState({ hasAddress: false });
          } else {
            this.props.form.setFieldsValue({
              address: res.data.logradouro
            });
          }

          if (!res.data.bairro) {
            this.setState({ hasNeighborhood: false });
          } else {
            this.props.form.setFieldsValue({
              neighborhood: res.data.bairro
            });
          }

          callback();
        }
      } catch (ex) {
        callback('CEP não Existe!');
      }
    } else if (valueFormatted && valueFormatted.length !== 9) {
      callback('CEP deve ter 9 dígitos!');
    } else {
      callback();
    }

    if (this.props.form.isFieldValidating('cep') === undefined) {
      this.props.form.validateFields(['cep']);
    }
  }

  compareToFirstPassword = (rule, value, callback) => {
    if (value && value !== this.props.form.getFieldValue('password')) {
      callback('Senhas Diferentes!');
    } else {
      callback();
    }
  }

  validateToNextPassword = (rule, value, callback) => {
    if (value && this.props.form.getFieldValue('confirmPassword')) {
      this.props.form.validateFields(['confirmPassword'], { force: true });
    }
    callback();
  }

  documentMask = (value) => {
    if (value) {
      const documentType = this.props.form.getFieldValue('documentType');

      if (documentType === 0) {
        return `${value}`.substring(0, 14).replace(/[^\d]/g, '').replace(/^(\d{3})(\d{3})(\d{3})(\d)/, '$1.$2.$3-$4');
      }

      return `${value}`.substring(0, 18).replace(/[^\d]/g, '').replace(/^(\d{2})(\d{3})(\d{3})(\d{4})(\d)/, '$1.$2.$3/$4-$5');
    }
    return '';
  }

  beforeUploadPhoto = (file) => {
    if (file.type === 'image/jpeg' || file.type === 'image/png') {
      const arquivos = [file];
      this.setState({ newProfilePhoto: arquivos });
      return true;
    }

    message.error('Extensão Inválida! Permitido apenas imagens');
    this.setState({ newProfilePhoto: [] });
    return false;
  }

  changeProfilePhoto = () => {
    this.setState({ changeProfilePhotoButtonloading: true });

    const formData = new FormData();
    formData.append('api_key', '784737987534174');
    formData.append('timestamp', (Date.now() / 1000));
    formData.append('upload_preset', 'hatsxc2p');
    formData.append('file', this.state.newProfilePhoto[0]);

    axios.post('https://api.cloudinary.com/v1_1/haken/image/upload', formData, {
      headers: { 'X-Requested-With': 'XMLHttpRequest' }
    }).then(res => {
      const id = this.state.user._id;
      const image = res.data.secure_url;

      axios.put('/api/customers', { id, image }).then(() => {
        notification.success({
          message: 'Uhull',
          description: 'Foto de Perfil Atualizada!'
        });

        this.setState({ changeProfilePhotoButtonloading: false });
        this.componentDidMount();
      }).catch(() => {
        // console.log(error);
        this.setState({ changeProfilePhotoButtonloading: false });

        notification.error({
          message: 'Opa!',
          description: 'Houve um problema aqui: Não conseguimos enviar dados no Servidor.'
        });
      });
    }).catch(() => {
      // console.log(err);
      this.setState({ changeProfilePhotoButtonloading: false });

      notification.error({
        message: 'Opa!',
        description: 'Houve um problema aqui: Não conseguimos enviar dados no Servidor de Imagens.'
      });
    });
  }

  handleSubmitInfo = (e) => {
    e.preventDefault();

    this.setState({ infoButtonloading: true });

    this.props.form.validateFields(['document', 'documentType', 'username', 'name'], (err, values) => {
      if (!err) {
        const id = this.state.user._id;
        const { document, documentType, username, name } = values;

        axios.put('/api/customers', { id, document, documentType: documentType.toString(), username, name }).then(() => {
          notification.success({
            message: 'Tudo Certo!',
            description: 'Informações Atualizadas!'
          });

          this.setState({ infoButtonloading: false });
          this.componentDidMount();
        }).catch(() => {
          // console.log(error);
          this.setState({ infoButtonloading: false });

          notification.error({
            message: 'Opa!',
            description: 'Houve um problema aqui: Não conseguimos enviar dados no Servidor.'
          });
        });
      } else {
        this.setState({ infoButtonloading: false });
      }
    });
  }

  handleSubmitContactInfo = (e) => {
    e.preventDefault();

    this.setState({ contactInfoButtonloading: true });

    this.props.form.validateFields(['email', 'cep', 'address', 'neighborhood', 'number', 'complement', 'city', 'uf', 'cellphone'], (err, values) => {
      if (!err) {
        const id = this.state.user._id;
        const { email, cellphone, cep, address, neighborhood, number, complement, city, uf } = values;
        const location = { cep, address, neighborhood, number, complement, city, uf };

        axios.put('/api/customers', { id, email, cellphone, location }).then(() => {
          notification.success({
            message: 'Tudo Certo!',
            description: 'Informações Atualizadas!'
          });

          this.setState({ contactInfoButtonloading: false });
          this.componentDidMount();
        }).catch(() => {
          // console.log(error);
          this.setState({ contactInfoButtonloading: false });

          notification.error({
            message: 'Opa!',
            description: 'Houve um problema aqui: Não conseguimos enviar dados no Servidor.'
          });
        });
      } else {
        this.setState({ contactInfoButtonloading: false });
      }
    });
  }

  handleSubmitChangePassword = (e) => {
    e.preventDefault();

    this.setState({ changePasswordButtonloading: true });

    this.props.form.validateFields(['password', 'confirmPassword'], (err, values) => {
      if (!err) {
        const id = this.state.user._id;
        const { password } = values;

        axios.put('/api/customers', { id, password }).then(() => {
          notification.success({
            message: 'Tudo Certo!',
            description: 'Informações Atualizadas!'
          });

          this.setState({ changePasswordButtonloading: false });
          this.componentDidMount();
        }).catch(() => {
          // console.log(error);
          this.setState({ changePasswordButtonloading: false });

          notification.error({
            message: 'Opa!',
            description: 'Houve um problema aqui: Não conseguimos enviar dados no Servidor.'
          });
        });
      } else {
        this.setState({ changePasswordButtonloading: false });
      }
    });
  }

  render() {
    const { getFieldDecorator, getFieldError, getFieldValue } = this.props.form;

    return (
      <Spin spinning = { !this.state.user }>
        <Row gutter = {16} style = {{ marginBottom: 60 }}>
          <Col span = {16} offset = {4}>
            <Title id = "info" level = {2} style = {{ fontWeight: 500, marginBottom: 4 }}> Seus Dados </Title>
            <Divider style = {{ marginTop: 0 }} />

            <Row gutter = {16}>
              <Col span = {8}>
                <Avatar shape = "square" size = {200} src = { this.state.user.image } />
                <Upload beforeUpload = { this.beforeUploadPhoto } customRequest = { this.changeProfilePhoto } fileList = { this.state.newProfilePhoto } showUploadList = {false} accept = "image/*">
                  <Button loading = { this.state.changeProfilePhotoButtonloading } icon = "edit" type = "primary" style = {{ backgroundColor: '#383A3F', borderColor: '#383A3F', position: 'absolute', bottom: 5, left: 12 }}> Editar Imagem </Button>
                </Upload>
              </Col>

              <Col span = {14}>
                <Form onSubmit = { this.handleSubmitInfo }>
                  <Form.Item label = "Documento" hasFeedback validateStatus = { getFieldError('document') ? 'error' : getFieldValue('document') && getFieldValue('document') !== this.state.user.document ? 'success' : '' }>
                    <Input.Group compact>
                      { getFieldDecorator('document', {
                        rules: [
                          { required: true, message: 'Por favor, insira um documento!' },
                          { validator: this.documentValidation }
                        ]
                      })(
                        <Input style = {{ width: '80%' }} placeholder = "Documento" />
                      )}

                      { getFieldDecorator('documentType')(
                        <Select style = {{ width: '20%' }}>
                          <Option value = {0}> CPF </Option>
                          <Option value = {1}> CNPJ </Option>
                        </Select>
                      )}
                    </Input.Group>
                  </Form.Item>

                  <Form.Item label = "Username" hasFeedback validateStatus = { getFieldError('username') ? 'error' : getFieldValue('username') && getFieldValue('username') !== this.state.user.username ? 'success' : '' }>
                    { getFieldDecorator('username', {
                      rules: [
                        { required: true, whitespace: true, message: 'Por favor, insira um username!' },
                        { validator: this.usernameValidation }
                      ]
                    })(
                      <Input
                        prefix = { <Icon type = "smile" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                        placeholder = "Username"
                      />
                    )}
                  </Form.Item>

                  <Form.Item label = "Nome" hasFeedback validateStatus = { getFieldError('name') ? 'error' : getFieldValue('name') && getFieldValue('name') !== this.state.user.name ? 'success' : '' }>
                    { getFieldDecorator('name', {
                      rules: [{ required: true, whitespace: true, message: 'Por favor, insira um nome!' }]
                    })(
                      <Input
                        prefix = { <Icon type = "user" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                        placeholder = "Nome"
                      />
                    )}
                  </Form.Item>

                  <Row style = {{ textAlign: 'right' }}>
                    <Button loading = { this.state.infoButtonloading } htmlType = "submit" type = "primary"> Alterar Dados </Button>
                  </Row>
                </Form>
              </Col>
            </Row>

            <Title id = "contact-info" level = {2} style = {{ fontWeight: 500, marginBottom: 4, marginTop: 40 }}> Dados de Contato </Title>
            <Divider style = {{ marginTop: 0 }} />

            <Row gutter = {16}>
              <Col span = {14} offset = {8}>
                <Form onSubmit = { this.handleSubmitContactInfo }>
                  <Form.Item label = "Email" hasFeedback validateStatus = { getFieldError('email') ? 'error' : getFieldValue('email') && getFieldValue('email') !== this.state.user.email ? 'success' : '' }>
                    { getFieldDecorator('email', {
                      rules: [
                        { required: true, whitespace: true, message: 'Por favor, insira um email!' },
                        { validator: this.emailValidation },
                        { type: 'email', message: 'Por favor, insira um email válido!' }
                      ]
                    })(
                      <Input
                        prefix = { <Icon type = "mail" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                        placeholder = "Email"
                      />
                    )}
                  </Form.Item>

                  <Form.Item label = "Telefone Celular" style = {{ width: '200px' }} hasFeedback validateStatus = { getFieldError('cellphone') ? 'error' : getFieldValue('cellphone') && getFieldValue('cellphone') !== this.state.user.cellphone ? 'success' : '' }>
                    { getFieldDecorator('cellphone', {
                      rules: [
                        { required: true, message: 'Por favor, insira um celular!' },
                        { validator: this.cellphoneValidation }
                      ]
                    })(
                      <Input style = {{ width: '100%' }} placeholder = "Telefone Celular" />
                    )}
                  </Form.Item>

                  <Row gutter = {16}>
                    <Col span = {6}>
                      <Form.Item label = "CEP" hasFeedback validateStatus = { getFieldError('cep') ? 'error' : getFieldValue('cep') && getFieldValue('cep') !== this.state.user.location.cep ? 'success' : '' }>
                        { getFieldDecorator('cep', {
                          rules: [
                            { required: true, message: 'Por favor, insira um cep!' },
                            { validator: this.cepValidation }
                          ]
                        })(
                          <Input style = {{ width: '100%' }} placeholder = "CEP" />
                        )}
                      </Form.Item>
                    </Col>

                    <Col span = {18}>
                      <Form.Item label = "Bairro" hasFeedback validateStatus = { getFieldError('neighborhood') ? 'error' : getFieldValue('neighborhood') && getFieldValue('neighborhood') !== this.state.user.location.neighborhood ? 'success' : '' }>
                        { getFieldDecorator('neighborhood', {
                          rules: [{ required: true, whitespace: true, message: 'Por favor, insira um bairro!' }]
                        })(
                          <Input
                            disabled = { this.state.hasNeighborhood }
                            prefix = { <Icon type = "home" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                            placeholder = "Bairro"
                          />
                        )}
                      </Form.Item>
                    </Col>
                  </Row>

                  <Row gutter = {16}>
                    <Col span = {18}>
                      <Form.Item label = "Endereço" hasFeedback validateStatus = { getFieldError('address') ? 'error' : getFieldValue('address') && getFieldValue('address') !== this.state.user.location.address ? 'success' : '' }>
                        { getFieldDecorator('address', {
                          rules: [{ required: true, whitespace: true, message: 'Por favor, insira um endereço!' }]
                        })(
                          <Input
                            disabled = { this.state.hasAddress }
                            prefix = { <Icon type = "home" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                            placeholder = "Endereço"
                          />
                        )}
                      </Form.Item>
                    </Col>

                    <Col span = {6}>
                      <Form.Item label = "Número" hasFeedback validateStatus = { getFieldError('number') ? 'error' : getFieldValue('number') && getFieldValue('number') !== this.state.user.location.number ? 'success' : '' }>
                        { getFieldDecorator('number', {
                          rules: [{ required: true, message: 'Por favor, insira um número!' }]
                        })(
                          <InputNumber
                            className = "no-number-options"
                            placeholder = "Número"
                          />
                        )}
                      </Form.Item>
                    </Col>
                  </Row>

                  <Row gutter = {16}>
                    <Col span = {9}>
                      <Form.Item label = "Complemento" hasFeedback validateStatus = { getFieldError('complement') ? 'error' : getFieldValue('complement') && getFieldValue('complement') !== this.state.user.location.complement ? 'success' : '' }>
                        { getFieldDecorator('complement')(
                          <Input
                            prefix = { <Icon type = "home" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                            placeholder = "Complemento"
                          />
                        )}
                      </Form.Item>
                    </Col>

                    <Col span = {10}>
                      <Form.Item label = "Cidade" hasFeedback validateStatus = { getFieldError('city') ? 'error' : getFieldValue('city') && getFieldValue('city') !== this.state.user.location.city ? 'success' : '' }>
                        { getFieldDecorator('city', {
                          rules: [{ required: true, whitespace: true, message: 'Por favor, insira uma cidade!' }]
                        })(
                          <Input
                            disabled
                            prefix = { <Icon type = "home" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                            placeholder = "Cidade"
                          />
                        )}
                      </Form.Item>
                    </Col>

                    <Col span = {5}>
                      <Form.Item label = "Estado" hasFeedback validateStatus = { getFieldError('uf') ? 'error' : getFieldValue('uf') && getFieldValue('uf') !== this.state.user.location.uf ? 'success' : '' }>
                        { getFieldDecorator('uf', {
                          rules: [{ required: true, whitespace: true, message: 'Por favor, insira um estado!' }]
                        })(
                          <Input
                            disabled
                            prefix = { <Icon type = "home" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                            placeholder = "Estado"
                          />
                        )}
                      </Form.Item>
                    </Col>
                  </Row>

                  <Row style = {{ textAlign: 'right' }}>
                    <Button loading = { this.state.contactInfoButtonloading } htmlType = "submit" type = "primary"> Alterar Dados </Button>
                  </Row>
                </Form>
              </Col>
            </Row>

            <Title id = "change-password" level = {2} style = {{ fontWeight: 500, marginBottom: 4, marginTop: 40 }}> Trocar Senha </Title>
            <Divider style = {{ marginTop: 0 }} />

            <Row gutter = {16}>
              <Col span = {14} offset = {8}>
                <Form onSubmit = { this.handleSubmitChangePassword }>
                  <Row gutter = {16}>
                    <Col span = {12}>
                      <Form.Item label = "Senha">
                        { getFieldDecorator('password', {
                          rules: [
                            { required: true, whitespace: true, message: 'Por favor, insira uma senha!' },
                            { min: 6, message: 'Por favor, insira uma senha maior que 5!' },
                            { validator: this.validateToNextPassword }
                          ]
                        })(
                          <Input.Password
                            prefix = { <Icon type = "lock" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                            placeholder = "Senha"
                          />
                        )}
                      </Form.Item>
                    </Col>

                    <Col span = {12}>
                      <Form.Item label = "Confirmar Senha" hasFeedback validateStatus = { getFieldError('confirmPassword') ? 'error' : getFieldValue('confirmPassword') ? 'success' : '' }>
                        { getFieldDecorator('confirmPassword', {
                          rules: [
                            { required: true, whitespace: true, message: 'Por favor, insira uma senha!' },
                            { min: 6, message: 'Por favor, insira uma senha maior que 5!' },
                            { validator: this.compareToFirstPassword }
                          ]
                        })(
                          <Input
                            type = "password"
                            prefix = { <Icon type = "lock" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                            placeholder = "Repita a Senha"
                          />
                        )}
                      </Form.Item>
                    </Col>
                  </Row>

                  <Row style = {{ textAlign: 'right' }}>
                    <Button loading = { this.state.changePasswordButtonloading } htmlType = "submit" type = "primary"> Alterar Senha </Button>
                  </Row>
                </Form>
              </Col>
            </Row>
          </Col>

          <Col span = {4} style = {{ marginTop: 60 }}>
            <Anchor offsetTop = {20} bounds = {500}>
              <Link href = "#info" title = "Informações Básicas" />
              <Link href = "#contact-info" title = "Informações de Contato" />
              <Link href = "#change-password" title = "Trocar Senha" />
            </Anchor>
          </Col>
        </Row>
      </Spin>
    );
  }
}

export default Form.create()(editProfileCustomer);
