// projects.js

import React, { Component } from 'react';
import { Button, Select, List, Card, Form, Col, Divider, Row, Input, Typography, Tag, Spin, Icon, Popover } from 'antd';
import { Link, Redirect } from 'react-router-dom';

import axios from 'axios';

import { getStatus, getID } from '../../services/auth';

import MainLayout from '../../components/layout/layout';

import './projects.css';

const { Title, Text, Paragraph } = Typography;
const { Search } = Input;
const { Option } = Select;

const projectStatus = [
  { color: '#3867D6', icon: 'deployment-unit', title: 'Planejamento', label: 'Começando o sofrimento denovo.' },
  { color: '#FA8231', icon: 'rocket', title: 'Inicializando', label: 'Fechando contrato e preparando as almas de membros para o abate.' },
  { color: '#45AAF2', icon: 'coffee', title: 'Em Andamento', label: 'Abastecendo os membros com café para manter o progresso.' },
  { color: '#778CA3', icon: 'frown', title: 'Cancelado', label: 'Cliente deu pra trás.' },
  { color: '#EB3B5A', icon: 'stop', title: 'Congelado', label: 'Sei lá, deu uma parada por agora.' },
  { color: '#20BF6B', icon: 'like', title: 'Acabou', label: 'Acaboooouuuu!!' }
];

const projectLabels = [
  { color: '#F7B731', title: 'Consultoria', icon: 'book' },
  { color: '#FED330', title: 'Site', icon: 'desktop' },
  { color: '#26DE81', title: 'Aplicativo', icon: 'mobile' },
  { color: '#0FB9B1', title: 'Sistema Web', icon: 'ie' },
  { color: '#2BCBBA', title: 'Sistema Interno', icon: 'home' }
];

class ProjectsMainPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loadingPage: true,
      statusProject: [],
      labelsProject: [],
      projects: [],
      customers: [],
      nav: ''
    };
  }

  componentDidMount() {
    axios.get('/api/customers').then(resCustomers => {
      const customers = resCustomers.data;

      axios.get('/api/projects').then(resProjects => {
        const projects = resProjects.data;

        projects.sort((obj1, obj2) => (obj1.status < obj2.status ? -1 : (obj1.status < obj2.status ? 1 : 0)));

        projects.forEach((project, index) => {
          projects[index].customer = customers[customers.map(r => r._id).indexOf(project.customer[0])];
        });

        this.setState({ customers, projects, loadingPage: false });
      }).catch(() => {
        // console.log(errProjects);
      });
    }).catch(() => {
      // console.log(errCustomers);
    });
  }

  searchByName = (e) => {
    if (e.target.value === '') {
      this.componentDidMount();
    } else {
      const { customers } = this.state;

      axios.get(`/api/projects/search/${e.target.value}`).then(res => {
        const projects = res.data;

        projects.sort((obj1, obj2) => (obj1.status < obj2.status ? -1 : (obj1.status < obj2.status ? 1 : 0)));

        projects.forEach((project, index) => {
          projects[index].customer = customers[customers.map(r => r._id).indexOf(project.customer[0])];
        });

        this.setState({ projects });
      }).catch(() => {
        // console.log(err);
      });
    }
  }

  render() {
    if (this.state.nav) return (<Redirect to = { this.state.nav } />);

    return (
      <MainLayout page = "projects">
        <Spin spinning = { this.state.loadingPage }>
          <Row style = {{ marginBottom: 0 }}>
            <Title id = "info" level = {2} style = {{ fontWeight: 500 }}> { getStatus() !== '-1' ? 'Projetinhos da Haken :D' : 'Seus Projetos' } </Title>

            <Divider style = {{ marginTop: 0 }} />
          </Row>

          <Row gutter = {16} type = "flex" justify = "end">
            <Col span = {9}>
              <Search placeholder = "Pesquise pelo nome ou descrição" onChange = { e => this.searchByName(e) } />
            </Col>

            <Col span = {6}>
              <Select className = "selectProjectByStatus" style = {{ width: '100%' }} mode = "multiple" placeholder = "Pesquise por Status" filterOption = { this.filteredOptions } onChange = { statusProject => this.setState({ statusProject }) }>
                { projectStatus.map((item, index) => (
                  <Option key = { item.title } value = { index.toString() }>
                    <Tag color = { item.color }> { item.title } </Tag>
                  </Option>
                )) }
              </Select>
            </Col>

            <Col span = {6}>
              <Select className = "selectProjectByStatus" style = {{ width: '100%' }} mode = "multiple" placeholder = "Pesquise por Rótulos" filterOption = { this.filteredOptions } onChange = { labelsProject => this.setState({ labelsProject }) }>
                { projectLabels.map((item, index) => (
                  <Option key = { item.title } value = { index.toString() }>
                    <Tag color = { item.color }> { item.title } </Tag>
                  </Option>
                )) }
              </Select>
            </Col>

            { getStatus() < 5 && getStatus() !== '-1' ? (
              <Link to = "/newProject">
                <Button type = "primary" icon = "plus"> Criar Projeto </Button>
              </Link>
            ) : null }
          </Row>

          <List
            style = {{ marginBottom: 50 }}
            grid = {{ gutter: 16, column: 4 }}
            size = "small"
            dataSource = { this.state.projects }
            renderItem = { item => (
              (this.state.statusProject.length === 0 || this.state.statusProject.includes(item.status.toString())) && (this.state.labelsProject.length === 0 || item.labels.some(r => this.state.labelsProject.includes(r.toString()))) ? (
                getStatus() !== '-1' || (item.customer && item.customer._id === getID()) ? (
                  <Col style = {{ paddingTop: 30 }} span = {6}>
                    <Card bordered hoverable>
                      <Row type = "flex">
                        <Col span = {10} style = {{ textAlign: 'left' }}>
                          { getStatus() !== '-1' ? (
                            <Popover
                              overlayStyle = {{ width: 300 }}
                              title = { <Text> <Icon type = { projectStatus[item.status].icon } /> &nbsp; { projectStatus[item.status].title } </Text> }
                              content = { <Paragraph style = {{ marginBottom: 0 }}> { projectStatus[item.status].label } </Paragraph> }
                            >
                              <Tag style = {{ verticalAlign: 3, fontWeight: 600, cursor: 'pointer' }} color = { projectStatus[item.status].color }> { projectStatus[item.status].title } </Tag>
                            </Popover>
                          ) : null }
                        </Col>

                        <Col span = {14} style = {{ textAlign: 'right' }}>
                          { getStatus() < 5 && getStatus() !== '-1' ? (
                            <Popover placement = "top" content = "Editar Projeto">
                              <Button size = "small" onClick = { () => this.setState({ nav: `/editProject/${item._id}` }) } icon = "edit" style = {{ marginRight: 5 }} />
                            </Popover>
                          ) : null }

                          <Popover placement = "top" content = "Ver Detalhes do Projeto">
                            <Button size = "small" type = "primary" onClick = { () => this.setState({ nav: `/insideProject/${item._id}` }) } icon = "eye" />
                          </Popover>
                        </Col>
                      </Row>

                      <Row type = "flex" justify = "center" style = {{ textAlign: 'center' }}>
                        <Title level = {3}> { item.name } </Title>
                      </Row>

                      <Row type = "flex" justify = "center">
                        <img alt = "Imagem do Projeto" style = {{ width: '100%', maxHeight: 200, objectFit: 'cover' }} src = { item.image } />
                      </Row>

                      <Row style = {{ marginTop: 30 }}>
                        { item.customer ? (
                          <Paragraph style = {{ marginBottom: 3 }}>
                            <Text strong> Cliente: </Text>
                            <Text> { item.customer.name } </Text>
                          </Paragraph>
                        ) : (
                          <Paragraph style = {{ marginBottom: 3 }}> Este projeto não têm cliente <span role = "img" aria-label = "Feliz">😜</span> </Paragraph>
                        )}

                        <Paragraph style = {{ marginBottom: 3 }}>
                          <Text strong> Rótulos: </Text>
                          { item.labels.map(label => (
                            <Popover key = { projectLabels[label].title } placement = "top" content = { projectLabels[label].title }>
                              <Tag color = { projectLabels[label].color } style = {{ marginBottom: 3, marginRight: 2, fontWeight: 600 }}> <Icon type = { projectLabels[label].icon } /> </Tag>
                            </Popover>
                          ))}
                        </Paragraph>
                      </Row>
                    </Card>
                  </Col>
                ) : <span />
              ) : <span />
            )}
          />
        </Spin>
      </MainLayout>
    );
  }
}

export default Form.create()(ProjectsMainPage);
