// goals.js

import React, { Component } from 'react';
import { Avatar, List, Input, Card, Form, Button, Icon, Modal, Row, Checkbox, Col, Typography, Spin, Divider, Select, notification, DatePicker } from 'antd';
import { Redirect } from 'react-router-dom';
import { ResponsiveLine } from '@nivo/line';

import axios from 'axios';
import moment from 'moment';

import { getStatus } from '../../services/auth';

import MainLayout from '../../components/layout/layout';

import './goals.css';

const { Title, Paragraph, Text } = Typography;
const { Option } = Select;

const nameGoals = [
  { name: 'Faturamento', icon: 'dollar', color: '#FED330', min: 0, formatterBefore: 'R$ ', formatterAfter: ',00', parser: /\B(?=(\d{3})+(?!\d))/g, replace: '.' },
  { name: 'Projeto', icon: 'appstore', color: '#26DE81', min: 0, defaultValue: 0 },
  { name: 'Membros em Projeto', icon: 'global', color: '#45AAF2', defaultValue: 100, min: 0, max: 100, formatterAfter: '%' }
];

class GoalsMainPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      goals: [],
      goalsFiltered: [],
      yearSearch: moment(),
      yearModal: moment(),
      status: 0,
      visibleModal: false,
      openYearSearch: false,
      openYearModal: false,
      loadingPage: true,
      loadingGoalsSettings: false
    };
  }

  componentDidMount() {
    this.setState({ loadingPage: true, yearSearch: moment(), visibleModal: false, loadingGoalsSettings: false });

    axios.get('/api/projects').then(resProjects => {
      const projects = resProjects.data;

      axios.get('/api/members').then(resMembers => {
        const members = resMembers.data;

        axios.get('/api/goals').then(res => {
          const goals = [];

          res.data.forEach(item => {
            const membersInProject = [];
            let numberOfProjects = 0;
            let costOfProjects = 0;
            let numberOfMembers = 0;

            const dataCostOfProjects = [{ id: nameGoals[0].name, data: [] }, { id: 'Meta', data: [] }];
            const dataNumberOfProjects = [{ id: nameGoals[1].name, data: [] }, { id: 'Meta', data: [] }];
            const dataMemberInProjects = [{ id: nameGoals[2].name, data: [] }, { id: 'Meta', data: [] }];

            members.forEach(member => {
              if ((moment(item.year).isSameOrAfter(moment(member.createdAt), 'year') && member.status < 7) || (member.status === 7 && moment(member.updatedAt).isSameOrAfter(moment(item.year), 'year'))) {
                numberOfMembers += 1;
              }
            });

            projects.forEach(project => {
              if (moment(item.year).isSame(moment(project.createdAt), 'year')) {
                project.members[0].forEach(member => {
                  membersInProject.push(member);
                });
                costOfProjects += project.cost;
                numberOfProjects += 1;
              }

              let dataCost = 0;
              let dataProjects = 0;
              let dataMembers = 0;

              moment.monthsShort().forEach((month, index) => {
                if (moment(project.createdAt).format('MMM') === month && moment(item.year).isSame(moment(project.createdAt), 'year')) {
                  dataCost += project.cost;
                  dataProjects += numberOfProjects;
                  dataMembers += ([...new Set(membersInProject)].length / numberOfMembers).toFixed(2) * 100;
                }

                const indexData = dataCostOfProjects[1].data.map(e => e.x).indexOf(month);
                if (indexData !== -1) {
                  if (!moment().isSame(moment(item.year), 'year') || moment().format('M') > index) {
                    dataCostOfProjects[0].data[indexData].y += dataCost;
                    dataNumberOfProjects[0].data[indexData].y = dataProjects;
                    dataMemberInProjects[0].data[indexData].y = dataMembers;
                  }

                  dataCostOfProjects[1].data[indexData].y = item.goals.map(g => g.status).indexOf(0) > -1 ? parseInt(((item.goals[item.goals.map(g => g.status).indexOf(0)].value / 12) * (index + 1)).toFixed(), 10) : 0;
                  dataNumberOfProjects[1].data[indexData].y = item.goals.map(g => g.status).indexOf(1) > -1 ? parseInt(((item.goals[item.goals.map(g => g.status).indexOf(1)].value / 12) * (index + 1)).toFixed(), 10) : 0;
                  dataMemberInProjects[1].data[indexData].y = item.goals.map(g => g.status).indexOf(2) > -1 ? parseInt(((item.goals[item.goals.map(g => g.status).indexOf(2)].value / 12) * (index + 1)).toFixed(), 10) : 0;
                } else {
                  if (!moment().isSame(moment(item.year), 'year') || moment().format('M') > index) {
                    dataCostOfProjects[0].data.push({ x: month, y: dataCost });
                    dataNumberOfProjects[0].data.push({ x: month, y: dataProjects });
                    dataMemberInProjects[0].data.push({ x: month, y: dataMembers });
                  }

                  dataCostOfProjects[1].data.push({ x: month, y: item.goals.map(g => g.status).indexOf(0) > -1 ? parseInt(((item.goals[item.goals.map(g => g.status).indexOf(0)].value / 12) * (index + 1)).toFixed(), 10) : 0 });
                  dataNumberOfProjects[1].data.push({ x: month, y: item.goals.map(g => g.status).indexOf(1) > -1 ? parseInt(((item.goals[item.goals.map(g => g.status).indexOf(1)].value / 12) * (index + 1)).toFixed(), 10) : 0 });
                  dataMemberInProjects[1].data.push({ x: month, y: item.goals.map(g => g.status).indexOf(2) > -1 ? parseInt(((item.goals[item.goals.map(g => g.status).indexOf(2)].value / 12) * (index + 1)).toFixed(), 10) : 0 });
                }
              });
            });

            const numberOfMembersInProject = [...new Set(membersInProject)].length;

            const goalsInternal = [];
            item.goals.forEach(goal => {
              goalsInternal.push({
                status: goal.status,
                value: goal.value,
                data: goal.status === 0 ? dataCostOfProjects : goal.status === 1 ? dataNumberOfProjects : dataMemberInProjects,
                currentValue: goal.status === 0 ? costOfProjects : goal.status === 1 ? numberOfProjects : ((numberOfMembersInProject / numberOfMembers).toFixed(2) * 100).toFixed(),
                percentage: goal.status === 0 ? ((costOfProjects / goal.value).toFixed(2) * 100).toFixed() : goal.status === 1 ? ((numberOfProjects / goal.value).toFixed(2) * 100).toFixed() : (((numberOfMembersInProject / numberOfMembers).toFixed(2) * 100 / goal.value).toFixed(2) * 100).toFixed()
              });
            });

            goals.push({
              _id: item._id,
              year: item.year,
              goals: goalsInternal
            });
          });

          const goalsFiltered = goals.filter((item) => moment(item.year).format('YYYY') === moment().format('YYYY') && item.goals.map(e => e.status).includes(0));

          this.setState({ goals, goalsFiltered, loadingPage: false });
        }).catch(() => {
          // console.log(err);
        });
      }).catch(() => {
        // console.log(errMembers);
      });
    }).catch(() => {
      // console.log(errProjects);
    });
  }

  searchByYear = (yearSearch) => {
    const goalsFiltered = this.state.goals.filter((item) => moment(item.year).format('YYYY') === yearSearch.format('YYYY') && item.goals.map(e => e.status).includes(this.state.status));

    this.setState({ goalsFiltered, yearSearch, openYearSearch: false });
  }

  searchByGoal = (status) => {
    const goalsFiltered = this.state.goals.filter((item) => moment(item.year).format('YYYY') === this.state.yearSearch.format('YYYY') && item.goals.map(e => e.status).includes(status));

    this.setState({ goalsFiltered, status });
  }

  changeYearModal = (yearModal) => {
    const goals = this.props.form.getFieldValue('goals');
    const goalModal = this.state.goals[this.state.goals.map(e => moment(e.year).format('YYYY')).indexOf(yearModal.format('YYYY'))];

    goals.forEach((item, index) => {
      goals[index].status = false;
      goals[index].value = nameGoals[index].defaultValue !== null && nameGoals[index].defaultValue !== undefined && nameGoals[index].parser ? nameGoals[index].defaultValue.replace(/[^\d]/g, '').replace(nameGoals[index].parser, nameGoals[index].replace) : nameGoals[index].defaultValue !== null && nameGoals[index].defaultValue !== undefined ? nameGoals[index].defaultValue : '';
    });

    if (goalModal) {
      goalModal.goals.forEach(item => {
        goals[item.status].status = true;
        goals[item.status].value = item.value;
      });
    }

    this.props.form.setFieldsValue({ goals });

    this.setState({ yearModal, openYearModal: false });
  }

  showModal = () => {
    this.changeYearModal(moment());

    this.setState({
      visibleModal: true
    });
  }

  hideModal = () => {
    this.setState({
      visibleModal: false
    });
  }

  handleGoals = (e) => {
    e.preventDefault();

    const { yearModal } = this.state;
    this.setState({ loadingGoalsSettings: true });

    this.props.form.validateFields((err, values) => {
      if (!err) {
        const goals = [];

        values.goals.forEach((goal, index) => {
          if (goal.status) {
            goals.push({
              status: index,
              value: goal.value
            });
          }
        });

        if (this.state.goals.map(r => moment(r.year).format('YYYY')).includes(yearModal.format('YYYY'))) {
          const id = this.state.goals[this.state.goals.map(r => moment(r.year).format('YYYY')).indexOf(yearModal.format('YYYY'))];
          axios.put('/api/goals', { id, goals: goals.map(g => ({ status: g.status, value: g.value.replace(/[^\d]/g, '') })) }).then(() => {
            notification.success({
              message: 'Uhull',
              description: 'Meta Atualizada!'
            });

            this.componentDidMount();
          }).catch(() => {
            // console.log(error);
            this.setState({ loadingGoalsSettings: false });

            notification.error({
              message: 'Errooouu!',
              description: 'Tem problema aqui, ó: Não conseguimos enviar dados no Servidor.'
            });
          });
        } else {
          axios.post('/api/goals', { year: yearModal, goals: goals.map(g => ({ status: g.status, value: g.value.replace(/[^\d]/g, '') })) }).then(() => {
            notification.success({
              message: 'Uhull',
              description: 'Meta Cadastrada!'
            });

            this.componentDidMount();
          }).catch(() => {
            // console.log(error);
            this.setState({ loadingGoalsSettings: false });

            notification.error({
              message: 'Errooouu!',
              description: 'Tem problema aqui, ó: Não conseguimos enviar dados no Servidor.'
            });
          });
        }
      } else {
        this.setState({ loadingGoalsSettings: false });
      }
    });
  }

  render() {
    if (['-1'].includes(getStatus())) return (<Redirect to = "/notAuthorizate" />);

    const { getFieldDecorator, getFieldValue, resetFields } = this.props.form;

    return (
      <MainLayout page = "goals">
        <Spin spinning = { this.state.loadingPage }>
          <Row style = {{ marginBottom: 0 }}>
            <Title id = "info" level = {2} style = {{ fontWeight: 500 }}> Metaasss :D </Title>
            <Divider style = {{ marginTop: 0 }} />
          </Row>

          <Row gutter = {16} type = "flex" justify = "end" align = "top">
            <Col span = {5}>
              <Select
                onChange = { e => this.searchByGoal(e) }
                defaultValue = {0}
                className = "goalSelectBy"
                style = {{ width: '100%' }}
                placeholder = "Selecione uma Meta"
              >
                { nameGoals.map((item, index) => (
                  <Option key = { item.name } value = { index }>
                    { item.name }
                  </Option>
                )) }
              </Select>
            </Col>

            <Col span = {4}>
              <DatePicker
                onChange = { event => this.searchByYear(event) }
                allowClear = {false}
                onPanelChange = { event => this.searchByYear(event) }
                onOpenChange = { (openYearSearch) => this.setState({ openYearSearch }) }
                open = { this.state.openYearSearch }
                value = { this.state.yearSearch }
                format = "YYYY"
                mode = "year"
              />
            </Col>

            { parseInt(getStatus(), 10) < 5 ? (
              <Button onClick = { this.showModal }>
                <Icon type = "setting" />
              </Button>
            ) : null }
          </Row>

          <List
            size = "small"
            dataSource = { this.state.goalsFiltered }
            renderItem = {item => (
              <Row style = {{ width: '100%', background: '#F6F6F5', padding: '30px', marginTop: 20, marginLeft: 0, marginRight: 0 }} gutter = {16}>
                <Col span = {14}>
                  <Card bordered = {false} hoverable style = {{ height: 400, borderColor: 'rgba(0, 0, 0, 0.09)', boxShadow: '0 2px 8px rgba(0, 0, 0, 0.09)' }}>
                    <div style = {{ height: 350 }}>
                      <Title level = {3} style = {{ textAlign: 'center', position: 'absolute', left: 0, width: '100%' }}> Meta de {nameGoals[this.state.status].name} </Title>

                      <ResponsiveLine
                        data = { item.goals[item.goals.map(e => e.status).indexOf(this.state.status)].data }
                        margin = {{ top: 60, right: 40, bottom: 50, left: 80 }}
                        xScale = {{ type: 'point' }}
                        yScale = {{ type: 'linear', stacked: false }}
                        curve = "catmullRom"
                        axisTop = {null}
                        axisRight = {null}
                        axisBottom = {{
                          orient: 'bottom',
                          tickSize: 5,
                          tickPadding: 5,
                          tickRotation: 0,
                          legend: '',
                          legendOffset: 36,
                          legendPosition: 'middle'
                        }}
                        yFormat = { value => `${nameGoals[this.state.status].formatterBefore || ''}${value.toString().replace(/[^\d]/g, '').replace(nameGoals[this.state.status].parser, nameGoals[this.state.status].replace)}${nameGoals[this.state.status].formatterAfter || ''}` }
                        axisLeft = {{
                          format: value => `${nameGoals[this.state.status].formatterBefore || ''}${value.toString().replace(/[^\d]/g, '').replace(nameGoals[this.state.status].parser, nameGoals[this.state.status].replace)}${nameGoals[this.state.status].formatterAfter || ''}`,
                          orient: 'left',
                          tickSize: 5,
                          tickPadding: 5,
                          tickRotation: 30,
                          legendOffset: -40,
                          legendPosition: 'middle'
                        }}
                        enableGridX = {false}
                        colors = {[nameGoals[this.state.status].color, '#4B6584']}
                        pointColor = "#FFFFFF"
                        pointBorderWidth = {2}
                        pointBorderColor = {{ from: 'serieColor' }}
                        pointLabel = "yFormatted"
                        pointSize = {10}
                        pointLabelYOffset = {-12}
                        enableSlices = "x"
                        crosshairType = "x"
                        useMesh
                        legends = {[{
                          anchor: 'bottom',
                          direction: 'row',
                          justify: false,
                          translateX: 0,
                          translateY: 50,
                          itemWidth: 100,
                          itemHeight: 20,
                          itemsSpacing: 4,
                          symbolSize: 14,
                          symbolShape: 'circle',
                          itemDirection: 'left-to-right',
                          itemTextColor: '#383A3F'
                        }]}
                        theme = {{
                          axis: {
                            ticks: {
                              line: {
                                stroke: '#383A3F'
                              },
                              text: {
                                fill: '#383A3F',
                                fontFamily: 'Maven Pro'
                              },
                              legend: {
                                fill: '#383A3F',
                                fontFamily: 'Maven Pro'
                              }
                            }
                          }
                        }}
                      />
                    </div>
                  </Card>
                </Col>

                <Col span = {5}>
                  <Card bordered = {false} hoverable style = {{ height: 400, borderColor: 'rgba(0, 0, 0, 0.09)', boxShadow: '0 2px 8px rgba(0, 0, 0, 0.09)' }}>
                    <Row style = {{ marginTop: 50 }} type = "flex" justify = "center" align = "middle">
                      <Avatar style = {{ color: nameGoals[this.state.status].color, backgroundColor: `${nameGoals[this.state.status].color}38` }} size = {100} icon = { nameGoals[this.state.status].icon } />
                    </Row>

                    <Row style = {{ marginTop: 25 }} type = "flex" justify = "center" align = "middle">
                      <Title style = {{ marginBottom: 0, marginTop: 0 }} level = {3}> {`${nameGoals[this.state.status].formatterBefore || ''}${item.goals[item.goals.map(e => e.status).indexOf(this.state.status)].currentValue.toString().replace(/[^\d]/g, '').replace(nameGoals[this.state.status].parser, nameGoals[this.state.status].replace)}${nameGoals[this.state.status].formatterAfter || ''}`} </Title>
                      <Text style = {{ fontSize: 22, textAlign: 'center', width: '100%' }}> Acumulado </Text>
                      <Text type = "secondary" style = {{ textAlign: 'center' }}> {nameGoals[this.state.status].name} total em {this.state.yearSearch.format('YYYY')}. </Text>
                    </Row>
                  </Card>
                </Col>

                <Col span = {5}>
                  <Card bordered = {false} hoverable style = {{ height: 400, borderColor: 'rgba(0, 0, 0, 0.09)', boxShadow: '0 2px 8px rgba(0, 0, 0, 0.09)' }}>
                    <Row style = {{ marginTop: 50 }} type = "flex" justify = "center" align = "middle">
                      <Avatar style = {{ color: '#4B6584', backgroundColor: '#4B658438' }} size = {100} icon = "rocket" />
                    </Row>

                    <Row style = {{ marginTop: 25 }} type = "flex" justify = "center" align = "middle">
                      <Title style = {{ marginBottom: 0, marginTop: 0 }} level = {3}> {`${nameGoals[this.state.status].formatterBefore || ''}${item.goals[item.goals.map(e => e.status).indexOf(this.state.status)].value.toString().replace(/[^\d]/g, '').replace(nameGoals[this.state.status].parser, nameGoals[this.state.status].replace)}${nameGoals[this.state.status].formatterAfter || ''}`} </Title>
                      <Text style = {{ fontSize: 22, textAlign: 'center', width: '100%' }}> Meta </Text>
                      <Text type = "secondary" style = {{ textAlign: 'center' }}> {item.goals[item.goals.map(e => e.status).indexOf(this.state.status)].percentage}% da meta anual alcançada. </Text>
                    </Row>
                  </Card>
                </Col>
              </Row>
            )}
          />

          <Modal visible = { this.state.visibleModal } footer = { null } onCancel = { this.hideModal } centered>
            <Paragraph style = {{ fontSize: 30, textAlign: 'center', marginBottom: 5 }}> Configurações de Metas </Paragraph>

            <Divider style = {{ minWidth: '60%', width: '60%', marginTop: 0, marginLeft: 'auto', marginRight: 'auto' }}>
              <Icon type = "flag" />
            </Divider>

            <Row gutter = {16} style = {{ marginBottom: 20 }}>
              <Col span = {16}>
                <DatePicker
                  onChange = { event => this.changeYearModal(event) }
                  onPanelChange = { event => this.changeYearModal(event) }
                  allowClear = {false}
                  value = { this.state.yearModal }
                  format = "YYYY"
                  mode = "year"
                  onOpenChange = { (openYearModal) => this.setState({ openYearModal }) }
                  open = { this.state.openYearModal }
                />
              </Col>
            </Row>

            <Form onSubmit = { this.handleGoals }>
              { nameGoals.map((item, index) => (
                <Row style = {{ marginBottom: 0 }} key = { item.name } gutter = {16}>
                  <Col span = {14}>
                    <Form.Item>
                      { getFieldDecorator(`goals[${index}].status`, {
                        valuePropName: 'checked',
                        initialValue: false
                      })(
                        <Checkbox onChange = { () => resetFields([`goals[${index}].value`], { force: true }) }>
                          { item.name }
                        </Checkbox>
                      )}
                    </Form.Item>
                  </Col>

                  <Col style = {{ display: getFieldValue(`goals[${index}].status`) ? 'block' : 'none' }} span = {10}>
                    <Form.Item>
                      { getFieldDecorator(`goals[${index}].value`, {
                        initialValue: item.defaultValue !== null && item.defaultValue !== undefined ? item.defaultValue : '',
                        normalize: (value) => value.toString().replace(/[^\d]/g, '').replace(item.parser, item.replace),
                        rules: [
                          { required: getFieldValue(`goals[${index}].status`), message: 'Por favor, insira um valor!' }
                        ]
                      })(
                        <Input addonBefore = { item.formatterBefore } addonAfter = { item.formatterAfter } style = {{ width: '100%' }} placeholder = "Valor Meta" />
                      )}
                    </Form.Item>
                  </Col>
                </Row>
              ))}

              <Row>
                <Col span = {14} offset = {8} style = {{ textAlign: 'right' }}>
                  <Button type = "primary" htmlType = "submit" loading = { this.state.loadingGoalsSettings }> Salvar </Button>
                </Col>
              </Row>
            </Form>
          </Modal>
        </Spin>
      </MainLayout>
    );
  }
}

export default Form.create()(GoalsMainPage);
