// file.js

import React, { Component } from 'react';
import { Breadcrumb, Tree, Typography, Spin, Modal, Menu, Dropdown, Upload, Row, Col, Table, Input, Button, Divider, Icon, Form, Popover, message, notification } from 'antd';
import { Link, Redirect } from 'react-router-dom';

import axios from 'axios';
import moment from 'moment';

import { getStatus } from '../../services/auth';

import MainLayout from '../../components/layout/layout';

import './file.css';

const { Column } = Table;
const { DirectoryTree, TreeNode } = Tree;
const { Paragraph } = Typography;

let fileIndex = 0;

class FileMainPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      path: [],
      children: [],
      files: [],
      currentFile: '',
      editNameText: '',
      enableEditRow: '',
      enableMovePopover: '',
      fileMovePopover: '',
      filesToUpload: '',
      filesUploaded: [],
      buttonEditNameLoading: false,
      buttonMoveFileLoading: false,
      buttonCreateDirectoryLoading: false,
      visibleModalCreateDirectory: false,
      loadingPage: true,
      nav: ''
    };
  }

  componentDidMount() {
    this.getFileDataForPage(this.props.match.params.id);
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (!nextProps.form.isFieldsTouched(['name']) && !this.props.form.isFieldsTouched(['name'])) {
      this.getFileDataForPage(nextProps.match.params.id);
    }
  }

  getFileDataForPage = (fileID) => {
    this.setState({
      path: [],
      children: [],
      files: [],
      currentFile: '',
      editNameText: '',
      enableEditRow: '',
      enableMovePopover: '',
      fileMovePopover: '',
      filesToUpload: '',
      filesUploaded: [],
      buttonEditNameLoading: false,
      buttonMoveFileLoading: false,
      buttonCreateDirectoryLoading: false,
      visibleModalCreateDirectory: false,
      loadingPage: true,
      nav: ''
    });

    axios.get('/api/files').then(res => {
      const files = res.data;
      const currentFile = files[files.map(e => e._id).indexOf(fileID)] || files[files.map(e => e.father.length).indexOf(0)];
      const path = [];

      let childrenDirectory = [];
      let childrenFile = [];
      let id = currentFile._id;

      while (files[files.map(e => e._id).indexOf(id)].father.length !== 0) {
        path.unshift(files[files.map(e => e._id).indexOf(id)]);

        const fatherID = files[files.map(e => e._id).indexOf(id)].father[0];
        id = fatherID;
      }

      currentFile.children.forEach(child => {
        const childFile = files[files.map(e => e._id).indexOf(child[0])];

        if (childFile) {
          if (childFile.type === 1) {
            childrenDirectory.push({
              _id: childFile._id,
              name: childFile.name,
              father: childFile.father[0],
              file: childFile.file,
              type: childFile.type,
              updatedAt: childFile.updatedAt
            });
          } else {
            childrenFile.push({
              _id: childFile._id,
              name: childFile.name,
              father: childFile.father[0],
              file: childFile.file,
              type: childFile.type,
              updatedAt: childFile.updatedAt
            });
          }
        }
      });

      childrenDirectory = childrenDirectory.sort((a, b) => (a.name && b.name ? a.name.toLowerCase().localeCompare(b.name.toLowerCase()) : 1));
      childrenFile = childrenFile.sort((a, b) => (a.name && b.name ? a.name.toLowerCase().localeCompare(b.name.toLowerCase()) : 1));

      this.setState({ path, files, currentFile, children: childrenDirectory.concat(childrenFile), loadingPage: false });
    }).catch(() => {
      // console.log(err);
    });
  }

  tableChange = (pagination, filters, sorter) => {
    let childrenDirectory = [];
    let childrenFile = [];

    this.state.children.forEach(item => {
      if (item.type === 1) {
        childrenDirectory.push(item);
      } else {
        childrenFile.push(item);
      }
    });

    childrenDirectory = childrenDirectory.sort((a, b) => (a.name && b.name ? a.name.toLowerCase().localeCompare(b.name.toLowerCase()) : 1));
    childrenFile = childrenFile.sort((a, b) => (a.name && b.name ? a.name.toLowerCase().localeCompare(b.name.toLowerCase()) : 1));

    if (sorter && sorter.order === 'descend') {
      childrenDirectory = childrenDirectory.reverse();
      childrenFile = childrenFile.reverse();
    }

    this.setState({ children: childrenDirectory.concat(childrenFile) });
  }

  directoryNameValidation = (rule, value, callback) => {
    const { children } = this.state;

    if (children[children.map((e) => e.name).indexOf(value)]) {
      callback('Nome já Cadastrado nessa Pasta!');
    } else if (value.toLowerCase() === 'projetos') {
      callback('Nome Proibido!');
    } else {
      callback();
    }
  }

  handleFilterSearch = (selectedKeys, confirm) => confirm();

  handleResetFilters = (clearFilters) => clearFilters();

  showModalCreateDirectory = () => this.setState({ visibleModalCreateDirectory: true });

  hideModalCreateDirectory = () => this.setState({ visibleModalCreateDirectory: false });

  renameDirectory = (id) => {
    this.setState({ buttonEditNameLoading: true });

    const { children, currentFile } = this.state;
    const value = this.state.editNameText;
    const file = children[children.map((e) => e.name).indexOf(value)];

    if (!value || (file && file._id !== id)) {
      if (!value) {
        message.error('Tem problema aqui, ó: Digite um nome válido.');
      } else {
        message.error('Tem problema aqui, ó: Já tem pasta/arquivo com esse nome.');
      }
      this.setState({ buttonEditNameLoading: false });
    } else {
      axios.put('/api/files', { name: value, id }).then(() => {
        this.setState({ nav: `/files/${currentFile._id}` });
      }).catch(() => {
        // console.log(ex);
        this.setState({ buttonEditNameLoading: false });

        notification.error({
          message: 'Errooouu!',
          description: 'Tem problema aqui, ó: Não conseguimos renomear este arquivo/diretório.'
        });
      });
    }
  }

  moveDirectory = (record) => {
    this.setState({ buttonMoveFileLoading: true });

    axios.put('/api/files/move', { idChild: record._id, idFather: this.state.fileMovePopover ? this.state.fileMovePopover : this.state.files[this.state.files.map(e => (e.father.length)).indexOf(0)]._id }).then(() => {
      this.setState({ nav: `/files/${this.state.fileMovePopover}` });
    }).catch(() => {
      // console.log(ex);
      this.setState({ buttonMoveFileLoading: false });

      notification.error({
        message: 'Errooouu!',
        description: 'Tem problema aqui, ó: Não conseguimos mover este arquivo/diretório.'
      });
    });
  }

  deleteDirectory = (id) => {
    axios.delete(`/api/files/${id}`).then(() => {
      const { currentFile } = this.state;

      this.setState({ nav: `/files/${currentFile._id}` });
    }).catch(() => {
      // console.log(ex);

      notification.error({
        message: 'Errooouu!',
        description: 'Tem problema aqui, ó: Não conseguimos apagar este arquivo/diretório.'
      });
    });
  }

  createDirectory = (e) => {
    this.setState({ buttonCreateDirectoryLoading: true });
    e.preventDefault();

    this.props.form.validateFields((err, values) => {
      if (!err) {
        const { name } = values;
        const { currentFile } = this.state;

        axios.post('/api/files', { name, father: currentFile._id, type: 1 }).then(() => {
          this.setState({ nav: `/files/${currentFile._id}` });
        }).catch(() => {
          // console.log(ex);
          this.setState({ buttonCreateDirectoryLoading: false });

          notification.error({
            message: 'Errooouu!',
            description: 'Tem problema aqui, ó: Não conseguimos apagar este arquivo/diretório.'
          });
        });
      } else {
        this.setState({ buttonCreateDirectoryLoading: false });
      }
    });
  }

  beforeUploadFiles = (file, fileList) => {
    fileIndex = 0;

    this.setState({ filesToUpload: fileList, filesUploaded: [] });
    return true;
  }

  uploadFiles = () => {
    this.setState({ loadingPage: true });
    const currentPhotoIndex = fileIndex;
    const filesTotalSize = this.state.filesToUpload.length;
    const fileCurrent = this.state.filesToUpload[currentPhotoIndex];
    fileIndex++;

    const formData = new FormData();
    formData.append('api_key', '784737987534174');
    formData.append('timestamp', (Date.now() / 1000));
    formData.append('upload_preset', 'hatsxc2p');
    formData.append('file', fileCurrent);

    axios.post('https://api.cloudinary.com/v1_1/haken/raw/upload', formData, {
      headers: { 'X-Requested-With': 'XMLHttpRequest' }
    }).then(res => {
      const { currentFile } = this.state;
      const file = res.data;
      const substringsFile = file.public_id.split('.');

      if (!file.format) file.format = substringsFile.length > 1 ? substringsFile[substringsFile.length - 1] : ' ';

      axios.post('/api/files', { name: res.data.original_filename, father: currentFile._id, type: 2, file }).then(() => {
        const files = this.state.filesUploaded.concat(currentPhotoIndex);

        this.setState({ filesUploaded: files });
        if (filesTotalSize === files.length) {
          this.setState({ nav: `/files/${currentFile._id}` });
        }
      }).catch(() => {
        // console.log(ex);
        this.setState({ loadingPage: false });

        notification.error({
          message: 'Errooouu!',
          description: 'Tem problema aqui, ó: Não conseguimos adicionar um dos arquivos.'
        });
      });
    }).catch(() => {
      // console.log(err);
      this.setState({ loadingPage: false });

      notification.error({
        message: 'Errooouu!',
        description: 'Tem problema aqui, ó: Não conseguimos enviar dados no Servidor de Arquivos.'
      });
    });
  }

  render() {
    if (this.state.nav) return (<Redirect to = { this.state.nav } />);
    if (['-1', '5', '6', '7'].includes(getStatus())) return (<Redirect to = "/notAuthorizate" />);

    const { getFieldDecorator } = this.props.form;

    return (
      <MainLayout page = "files">
        <Spin spinning = { this.state.loadingPage }>
          <Row style = {{ marginBottom: 0, display: 'flex' }}>
            <Breadcrumb>
              <Breadcrumb.Item>
                <Link to = "/files"><Icon type = "home" /></Link>
              </Breadcrumb.Item>
              { this.state.path.map((item, index) => (
                this.state.path.length === index + 1 ? (
                  <Breadcrumb.Item style = {{ cursor: 'pointer' }} key = { item._id }>{item.name}</Breadcrumb.Item>
                ) : (
                  <Breadcrumb.Item key = { item._id }>
                    <Link to = {`/files/${item._id}`}>{item.name}</Link>
                  </Breadcrumb.Item>
                )
              ))}
            </Breadcrumb>

            { this.state.currentFile && this.state.currentFile.name.toLowerCase() !== 'projetos' ? (
              <Dropdown
                placement = "bottomRight"
                overlay = {(
                  <Menu>
                    <Menu.Item onClick = { this.showModalCreateDirectory }> <Icon type = "folder" /> &nbsp; Pasta </Menu.Item>
                    <Menu.Item> <Upload showUploadList = {false} beforeUpload = { this.beforeUploadFiles } customRequest = { this.uploadFiles } fileList = { this.state.filesToUpload } multiple> <Icon type = "file" /> &nbsp; Arquivos </Upload> </Menu.Item>
                  </Menu>
                )}
              >
                <Button style = {{ marginLeft: 'auto', marginRight: 10 }} icon = "plus" type = "primary"> Novo </Button>
              </Dropdown>
            ) : null }
          </Row>

          <Divider style = {{ marginTop: 5 }} />

          <Table
            onChange = { this.tableChange }
            locale = {{ emptyText: 'Não há nada nessa pasta... Adicione um arquivo.' }}
            dataSource = { this.state.children }
            pagination = {false}
            rowKey = { record => record._id }
            onRow = { record => ({
              onDoubleClick: () => (record.type === 1 ? this.setState({ nav: `/files/${record._id}` }) : window.open(record.file.url, '_blank'))
            })}
          >
            <Column
              className = "columnType"
              width = {20}
              dataIndex = "type"
              render = { (name, record) => (record.type === 1 ? (
                <Icon theme = "twoTone" twoToneColor = "#4B6584" type = "folder" />
              ) : ['jpg', 'png', 'gif'].includes(record.file.format.toLowerCase()) ? (
                <Icon theme = "twoTone" twoToneColor = "#A55EEA" type = "file-image" />
              ) : record.file.format.toLowerCase() === 'pdf' ? (
                <Icon theme = "twoTone" twoToneColor = "#EB3B5A" type = "file-pdf" />
              ) : record.file.format.toLowerCase() === 'md' ? (
                <Icon theme = "twoTone" twoToneColor = "#FED330" type = "file-markdown" />
              ) : ['doc', 'docx', 'docm', 'dot', 'dotx', 'dotm', 'rtf', 'odt'].includes(record.file.format.toLowerCase()) ? (
                <Icon theme = "twoTone" twoToneColor = "#3867D6" type = "file-word" />
              ) : ['html', 'txt'].includes(record.file.format.toLowerCase()) ? (
                <Icon theme = "twoTone" twoToneColor = "#4B7BEC" type = "file-text" />
              ) : ['ppt', 'pptx', 'pptm', 'pps', 'ppsx', 'ppsm', 'pot', 'potx', 'potm'].includes(record.file.format.toLowerCase()) ? (
                <Icon theme = "twoTone" twoToneColor = "#FA8231" type = "file-ppt" />
              ) : ['xlsx', 'xlsm', 'xlt', 'xltx', 'xltm', 'ods', 'csv', 'tsv', 'tab'].includes(record.file.format.toLowerCase()) ? (
                <Icon theme = "twoTone" twoToneColor = "#20BF6B" type = "file-excel" />
              ) : ['zip', 'rar', '7z', 'tar'].includes(record.file.format.toLowerCase()) ? (
                <Icon theme = "twoTone" twoToneColor = "#778CA3" type = "file-zip" />
              ) : (
                <Icon theme = "twoTone" twoToneColor = "#4B6584" type = "file" />
              ))}
            />

            <Column
              title = "Nome"
              key = "name"
              dataIndex = "name"
              sorter
              defaultSortOrder = "ascend"
              filterDropdown = { ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
                <div style = {{ padding: 8 }}>
                  <Input
                    ref = { node => { this.searchInput = node; }}
                    placeholder = "Procurar por nome"
                    value = { selectedKeys[0] }
                    onChange = { e => setSelectedKeys(e.target.value ? [e.target.value] : []) }
                    onPressEnter = { () => this.handleFilterSearch(selectedKeys, confirm) }
                    style = {{ width: 188, marginBottom: 8, display: 'block' }}
                  />

                  <Button type = "primary" onClick = { () => this.handleFilterSearch(selectedKeys, confirm) } icon = "search" size = "small" style = {{ width: 90, marginRight: 8 }}> Procurar </Button>
                  <Button onClick = { () => this.handleResetFilters(clearFilters) } size = "small" style = {{ width: 90 }}> Resetar </Button>
                </div>
              )}
              filterIcon = { filtered => (<Icon type = "search" style = {{ color: filtered ? '#1890ff' : undefined }} />) }
              onFilter = { (value, record) => (record.name ? record.name.toString().toLowerCase().includes(value.toLowerCase()) : null) }
              onFilterDropdownVisibleChange = { (visible) => {
                if (visible) {
                  setTimeout(() => this.searchInput.select());
                }
              }}
              render = { (name, record) => (
                this.state.enableEditRow === record._id ? (
                  <Row style = {{ display: 'flex' }}>
                    <Input disabled = { this.state.buttonEditNameLoading } onPressEnter = { () => this.renameDirectory(record._id) } value = { this.state.editNameText } onChange = { value => this.setState({ editNameText: value.target.value }) } />
                    <Button loading = { this.state.buttonEditNameLoading } icon = "check" type = "primary" onClick = { () => this.renameDirectory(record._id) } style = {{ marginLeft: 5, marginRight: 5 }} />
                    <Button disabled = { this.state.buttonEditNameLoading } icon = "close" type = "danger" onClick = { () => this.setState({ enableEditRow: '', editNameText: '' }) } />
                  </Row>
                ) : (
                  <Popover
                    placement = "bottomLeft"
                    arrowPointAtCenter
                    trigger = "contextMenu"
                    onVisibleChange = { visible => (!visible ? this.setState({ enableMovePopover: '' }) : null) }
                    visible = { this.state.enableMovePopover === record._id }
                    title = {(
                      <span>
                        Escolha uma pasta para mover... &nbsp;&nbsp;
                        <Icon className = "close-modal-move-directory" type = "close" onClick = { () => this.setState({ enableMovePopover: '' }) } />
                      </span>
                    )}
                    content = {(
                      <div>
                        <Paragraph strong style = {{ marginBottom: 2 }}> { this.state.files[this.state.files.map(e => (this.state.fileMovePopover ? e._id : e.father.length)).indexOf(this.state.fileMovePopover ? this.state.fileMovePopover : 0)].name }: </Paragraph>

                        { this.state.files && this.state.files.length ? (
                          <DirectoryTree expandedKeys = {[this.state.fileMovePopover]} onExpand = { (expandedKeys) => this.setState({ fileMovePopover: expandedKeys[expandedKeys.length - 1] }) }>
                            { this.state.fileMovePopover && this.state.files[this.state.files.map(e => e.father.length).indexOf(0)]._id !== this.state.fileMovePopover ? (<TreeNode title = "/.." key = { this.state.files[this.state.files.map(e => e._id).indexOf(this.state.fileMovePopover)].father[0] } />) : '' }

                            { this.state.files[this.state.files.map(e => (this.state.fileMovePopover ? e._id : e.father.length)).indexOf(this.state.fileMovePopover ? this.state.fileMovePopover : 0)].children.map(child => {
                              const childFile = this.state.files[this.state.files.map(e => e._id).indexOf(child[0])];
                              if (childFile && childFile.type === 1) {
                                return (<TreeNode disabled = { record._id === childFile._id } title = { childFile.name } key = { childFile._id } />);
                              }
                              return '';
                            })}
                          </DirectoryTree>
                        ) : (<Spin />) }

                        <Button loading = { this.state.buttonMoveFileLoading } onClick = { () => this.moveDirectory(record) } style = {{ width: '100%', marginTop: 10 }} disabled = { this.state.files[this.state.files.map(e => (this.state.fileMovePopover ? e._id : e.father.length)).indexOf(this.state.fileMovePopover ? this.state.fileMovePopover : 0)]._id === record.father || (this.state.fileMovePopover && this.state.files[this.state.files.map(e => e._id).indexOf(this.state.fileMovePopover)].name.toLowerCase() === 'projetos') } type = "primary" size = "small"> Mover Aqui </Button>
                      </div>
                    )}
                  >
                    { record.type === 1 ? name || '' : record.file.format !== ' ' ? `${name || ''}.${record.file.format}` : name || '' }
                  </Popover>
                )
              )}
            />

            <Column align = "right" title = "Última Modificação" key = "updatedAt" dataIndex = "updatedAt" render = { date => (<span> {moment(date).format('DD')} de {moment(date).format('MMMM')} de {moment(date).format('YYYY')} </span>) } />

            <Column
              align = "right"
              key = "_id"
              dataIndex = "_id"
              width = {20}
              render = {(id, record) => (
                (record.type === 1 && record.name.toLowerCase() === 'projetos') || (this.state.currentFile.name.toLowerCase() === 'projetos') ? null : (
                  <Dropdown
                    overlay = {(
                      <Menu>
                        <Menu.Item onClick = { () => this.setState({ enableEditRow: id, editNameText: record.name, enableMovePopover: '', fileMovePopover: '' }) }> <Icon type = "edit" /> Renomear </Menu.Item>
                        <Menu.Item onClick = { () => this.setState({ enableMovePopover: id, fileMovePopover: '', enableEditRow: '', editNameText: '' }) }> <Icon type = "snippets" /> Mover </Menu.Item>

                        <Menu.Item
                          onClick = { () => {
                            const _this = this;
                            Modal.confirm({
                              title: 'Deseja realmente apagar esta pasta?',
                              content: 'Esta ação é permanente, ainda não implmentamos lixeira (e nem vamos)',
                              okType: 'danger',
                              onOk() {
                                _this.deleteDirectory(id);
                              },
                              onCancel() {}
                            });
                          }}
                        >
                          <Icon type = "delete" /> Excluir
                        </Menu.Item>
                      </Menu>
                    )}
                    placement = "bottomRight"
                  >
                    <Icon style = {{ cursor: 'pointer' }} type = "more" />
                  </Dropdown>
                )
              )}
            />
          </Table>

          <Modal visible = { this.state.visibleModalCreateDirectory } footer = { null } onCancel = { this.hideModalCreateDirectory } centered width = {391}>
            <Paragraph style = {{ fontSize: 30, textAlign: 'center', marginBottom: 5 }}> Nova Pasta </Paragraph>

            <Divider style = {{ fontSize: 20, minWidth: '60%', width: '60%', marginTop: 0, marginLeft: 'auto', marginRight: 'auto' }}>
              <Icon type = "folder-add" />
            </Divider>

            <Row gutter = {16}>
              <Form onSubmit = { this.createDirectory }>
                <Row gutter = {16}>
                  <Col span = {24}>
                    <Form.Item>
                      { getFieldDecorator('name', {
                        rules: [
                          { required: true, whitespace: true, message: 'Por favor, insira um nome para a pasta!' },
                          { validator: this.directoryNameValidation }
                        ]
                      })(
                        <Input
                          prefix = { <Icon type = "folder" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                          placeholder = "Nova Pasta"
                        />
                      )}
                    </Form.Item>
                  </Col>
                </Row>

                <Row style = {{ textAlign: 'right' }}>
                  <Button size = "default" onClick = { this.hideModalCreateDirectory } style = {{ marginRight: 8 }}> Cancelar </Button>
                  <Button loading = { this.state.buttonCreateDirectoryLoading } type = "primary" htmlType = "submit" size = "default"> Criar </Button>
                </Row>
              </Form>
            </Row>
          </Modal>
        </Spin>
      </MainLayout>
    );
  }
}

export default Form.create()(FileMainPage);
