// gamification.js

import React, { Component } from 'react';
import { Spin, Row, Col, Typography, Modal, Dropdown, Menu, Divider, Form, Input, InputNumber, Select, Avatar, List, Button, Icon, Popover, Statistic, Card, notification } from 'antd';
import { Redirect } from 'react-router-dom';

import axios from 'axios';
import moment from 'moment';

import { getStatus } from '../../services/auth';

import MainLayout from '../../components/layout/layout';

const { Title, Paragraph, Text } = Typography;
const { Search } = Input;

const usersStatus = [
  { title: 'Presidente', label: 'Rei da Cocada Preta' },
  { title: 'Vice-Presidente/Diretor de RH', label: 'Manda só quando o presidente não está.' },
  { title: 'Diretor de Projetos', label: 'The Best' },
  { title: 'Diretor Administrativo-Financeiro', label: 'Contando Centavos...' },
  { title: 'Diretor de Marketing', label: 'Criando umas Artes' },
  { title: 'Membro', label: 'Abastecendo-se de Café' },
  { title: 'Trainee', label: 'Sofrendo' },
  { title: 'Ancião', label: 'Os Baqueados' }
];

class Gamification extends Component {
  constructor(props) {
    super(props);

    this.state = {
      members: [],
      view: 'card',
      tasks: [],
      tasksSearched: [],
      task: '',
      loadingPage: true,
      visibleTasksModal: false,
      visibleTaskMemberModal: false,
      loadingTasksModalButton: false,
      loadingTaskMemberModalButton: false
    };
  }

  componentDidMount() {
    this.setState({ loadingPage: true });

    axios.get('/api/members').then(resMembers => {
      let members = resMembers.data;

      members.sort((obj1, obj2) => (obj1.status < obj2.status ? -1 : (obj1.status > obj2.status ? 1 : 0)));
      members = members.filter((e) => (e.status < 7));

      axios.get('api/task').then(resTasks => {
        const tasks = resTasks.data;

        this.setState({ tasks, tasksSearched: tasks, members, loadingPage: false });
      }).catch(() => {
        // console.log(err);
      });
    }).catch(() => {
      // console.log(errMembers);
    });
  }

  search = (e) => {
    const { tasks } = this.state;
    if (e.target.value === '') this.setState({ tasksSearched: tasks });
    else this.setState({ tasksSearched: tasks.filter(r => r.name.toLowerCase().includes(e.target.value.toLowerCase()) || (r.description && r.description.toLowerCase().includes(e.target.value.toLowerCase()))) });
  }

  changeVisualization = () => {
    if (this.state.view === 'card') this.setState({ view: 'list' });
    else this.setState({ view: 'card' });
  }

  openNewTaskModal = () => {
    this.props.form.resetFields();
    this.setState({ visibleTasksModal: true });
  }

  openEditTaskModal = (task) => {
    this.props.form.resetFields();

    if (task.score.type === '0') this.props.form.setFieldsValue({ point: task.score.point });
    if (task.score.type === '1') this.props.form.setFieldsValue({ min: task.score.min, max: task.score.max });
    if (task.score.type === '2') this.props.form.setFieldsValue({ point: task.score.point, mult: task.score.mult, observation: task.score.observation });
    if (task.score.type === '3') this.props.form.setFieldsValue({ point: task.score.point, multMax: task.score.multMax, mult: task.score.mult, observation: task.score.observation });

    this.props.form.setFieldsValue({
      name: task.name,
      description: task.description,
      type: task.score.type
    });

    this.setState({ visibleTasksModal: true, task });
  }

  openTaskMemberModal = () => {
    this.props.form.resetFields();
    this.setState({ visibleTaskMemberModal: true, loadingTaskMemberModalButton: false });
  }

  hideModals = () => this.setState({ task: '', visibleTasksModal: false, visibleTaskMemberModal: false, loadingTasksModalButton: false });

  filteredOptions = (value, option) => {
    if (!option.props.label && option.key.toLowerCase().includes(value.toLowerCase())) return true;
    return false;
  }

  handleSubmitNewTask = (e) => {
    e.preventDefault();

    this.setState({ loadingTasksModalButton: true });

    this.props.form.validateFields(['type', 'name', 'description', 'min', 'max', 'point', 'mult', 'multMax', 'observation'], (err, values) => {
      if (!err) {
        const score = {
          type: values.type
        };

        if (score.type === '0' || score.type === '2' || score.type === '3') score.point = values.point;
        if (score.type === '1') score.min = values.min;
        if (score.type === '1') score.max = values.max;
        if (score.type === '2' || score.type === '3') score.mult = values.mult;
        if (score.type === '2' || score.type === '3') score.observation = values.observation;
        if (score.type === '3') score.multMax = values.multMax;

        axios.post('/api/task', { name: values.name, description: values.description, score }).then(() => {
          notification.success({
            message: 'Uhull',
            description: 'Tarefa Criada!'
          });

          this.hideModals();
          this.componentDidMount();
        }).catch(() => {
          // console.log(error);
          this.setState({ loadingTasksModalButton: false });

          notification.error({
            message: 'Errooouu!',
            description: 'Tem problema aqui, ó: Não conseguimos enviar dados no Servidor.'
          });
        });
      } else {
        this.setState({ loadingTasksModalButton: false });
      }
    });
  }

  handleSubmitEditTask = (e) => {
    e.preventDefault();

    const { task } = this.state;
    this.setState({ loadingTasksModalButton: true });

    this.props.form.validateFields(['type', 'name', 'description', 'min', 'max', 'point', 'mult', 'multMax', 'observation'], (err, values) => {
      if (!err) {
        const score = {
          type: values.type
        };

        if (score.type === '0' || score.type === '2' || score.type === '3') score.point = values.point;
        if (score.type === '1') score.min = values.min;
        if (score.type === '1') score.max = values.max;
        if (score.type === '2' || score.type === '3') score.mult = values.mult;
        if (score.type === '2' || score.type === '3') score.observation = values.observation;
        if (score.type === '3') score.multMax = values.multMax;

        axios.put('/api/task', { id: task._id, name: values.name, description: values.description, score }).then(() => {
          notification.success({
            message: 'Uhull',
            description: 'Tarefa Atualizada!'
          });

          this.hideModals();
          this.componentDidMount();
        }).catch(() => {
          // console.log(error);
          this.setState({ loadingTasksModalButton: false });

          notification.error({
            message: 'Errooouu!',
            description: 'Tem problema aqui, ó: Não conseguimos enviar dados no Servidor.'
          });
        });
      } else {
        this.setState({ loadingTasksModalButton: false });
      }
    });
  }

  deleteTask = (id) => {
    axios.delete(`/api/task/${id}`).then(() => {
      notification.success({
        message: 'Uhull',
        description: 'Tarefa Removida!'
      });

      this.hideModals();
      this.componentDidMount();
    }).catch(() => {
      // console.log(error);
      this.setState({ loadingTasksModalButton: false });

      notification.error({
        message: 'Errooouu!',
        description: 'Tem problema aqui, ó: Não conseguimos enviar dados no Servidor.'
      });
    });
  }

  handleTaskToMember = (e) => {
    e.preventDefault();

    this.setState({ loadingTaskMemberModalButton: true });

    this.props.form.validateFields(['task', 'member', 'points'], (err, values) => {
      if (!err) {
        const task = this.state.tasks[this.state.tasks.map(r => r._id).indexOf(values.task)];
        let point = values.points;
        if (task.score.type === '3') {
          if (values.points * task.score.mult + task.score.point > task.score.multMax) point = task.score.multMax;
          else point = values.points * task.score.mult + task.score.point;
        }

        const member = this.state.members[this.state.members.map(r => r._id).indexOf(values.member)];

        member.points.push({
          task, point, date: moment()
        });

        axios.put('/api/members', { id: member._id, points: member.points }).then(() => {
          notification.success({
            message: 'Uhull',
            description: 'Pontos Anexado ao Membro!'
          });

          this.openTaskMemberModal();
        }).catch(() => {
          // console.log(error);
          this.setState({ loadingTaskMemberModalButton: false });

          notification.error({
            message: 'Errooouu!',
            description: 'Tem problema aqui, ó: Não conseguimos enviar dados no Servidor.'
          });
        });
      } else {
        this.setState({ loadingTaskMemberModalButton: false });
      }
    });
  }

  render() {
    if (['-1'].includes(getStatus())) return (<Redirect to = "/notAuthorizate" />);

    const { getFieldDecorator, getFieldValue, getFieldError, setFieldsValue } = this.props.form;

    return (
      <MainLayout page = "gamification">
        <Spin spinning = { this.state.loadingPage }>
          <Row style = {{ marginBottom: 0 }}>
            <Title id = "info" level = {2} style = {{ fontWeight: 500 }}> Gamification <span role = "img" aria-label = "Trophy">🏆</span> </Title>

            <Divider style = {{ marginTop: 0 }} />
          </Row>

          <Row gutter = {16} type = "flex" justify = "end">
            <Col span = {18}>
              <Search
                placeholder = "Pesquise pelo nome ou descrição"
                onChange = { e => this.search(e) }
                style = {{ marginBottom: 18 }}
                size = "default"
              />
            </Col>

            <Popover content = { this.state.view === 'card' ? 'Lista' : 'Visualização em Grade' } placement = "bottom">
              <Button style = {{ marginRight: 8 }} onClick = { () => this.changeVisualization() } type = "dashed" shape = "circle" icon = { this.state.view === 'card' ? 'align-left' : 'appstore' } />
            </Popover>

            { getStatus() < 5 ? (
              <Button type = "primary" icon = "plus" onClick = { () => this.openTaskMemberModal() }> Adicione Pontos - Membros </Button>
            ) : null }
          </Row>

          { this.state.view === 'card' ? (
            <Row>
              { getStatus() < 5 ? (
                <Col style = {{ marginTop: 20, height: 120, paddingRight: 8, paddingLeft: 8 }} span = {6}>
                  <Card bordered hoverable style = {{ textAlign: 'center', height: 120 }} onClick = { () => this.openNewTaskModal() }>
                    <Avatar style = {{ color: '#383A3F', backgroundColor: '#383A3F38' }}>
                      <Icon type = "plus" />
                    </Avatar>

                    <Title style = {{ marginBottom: 0, marginTop: 10, fontWeight: 600, fontSize: 13 }} level = {4}> Adicionar Nova Tarefa </Title>
                  </Card>
                </Col>
              ) : null }

              { this.state.tasksSearched ? this.state.tasksSearched.map(task => (
                task.score.type === '0' ? (
                  <Col key = { task._id } style = {{ marginTop: 20, height: 120, paddingRight: 8, paddingLeft: 8 }} span = {6}>
                    <Popover overlayStyle = {{ width: 300, textAlign: 'center' }} content = { <Text> { task.description ? (<Text> <Text strong> Descrição: </Text> { task.description }</Text>) : null } </Text> }>
                      <Card bordered hoverable style = {{ textAlign: 'center', height: 120 }}>
                        { getStatus() < 5 ? (
                          <Dropdown
                            overlay = {(
                              <Menu>
                                <Menu.Item onClick = { () => this.openEditTaskModal(task) }> <Icon type = "edit" /> Editar </Menu.Item>

                                <Menu.Item
                                  onClick = { () => {
                                    const _this = this;
                                    Modal.confirm({
                                      title: 'Deseja realmente apagar esta tarefa?',
                                      content: 'Esta ação é permanente, ainda não implmentamos lixeira (e nem vamos)',
                                      okType: 'danger',
                                      onOk() {
                                        _this.deleteTask(task._id);
                                      },
                                      onCancel() {}
                                    });
                                  }}
                                >
                                  <Icon type = "delete" /> Excluir
                                </Menu.Item>
                              </Menu>
                            )}
                            placement = "bottomRight"
                          >
                            <Icon style = {{ cursor: 'pointer', position: 'absolute', right: 10, top: 13 }} type = "more" />
                          </Dropdown>
                        ) : null }

                        <Statistic value = { task.score.point } valueStyle = {{ color: task.score.point > 0 ? '#20BF6B' : '#EB3B5A' }} />

                        <Title style = {{ marginBottom: 0, marginTop: 10, fontWeight: 600, fontSize: 13 }} level = {4}> { task.name } </Title>
                      </Card>
                    </Popover>
                  </Col>
                ) : task.score.type === '1' ? (
                  <Col key = { task._id } style = {{ marginTop: 20, height: 120, paddingRight: 8, paddingLeft: 8 }} span = {6}>
                    <Popover overlayStyle = {{ width: 300, textAlign: 'center' }} content = { <Text> { task.description ? (<Text> <Text strong> Descrição: </Text> { task.description }</Text>) : null } </Text> }>
                      <Card bordered hoverable style = {{ textAlign: 'center', height: 120 }}>
                        { getStatus() < 5 ? (
                          <Dropdown
                            overlay = {(
                              <Menu>
                                <Menu.Item onClick = { () => this.openEditTaskModal(task) }> <Icon type = "edit" /> Editar </Menu.Item>

                                <Menu.Item
                                  onClick = { () => {
                                    const _this = this;
                                    Modal.confirm({
                                      title: 'Deseja realmente apagar esta tarefa?',
                                      content: 'Esta ação é permanente, ainda não implmentamos lixeira (e nem vamos)',
                                      okType: 'danger',
                                      onOk() {
                                        _this.deleteTask(task._id);
                                      },
                                      onCancel() {}
                                    });
                                  }}
                                >
                                  <Icon type = "delete" /> Excluir
                                </Menu.Item>
                              </Menu>
                            )}
                            placement = "bottomRight"
                          >
                            <Icon style = {{ cursor: 'pointer', position: 'absolute', right: 10, top: 13 }} type = "more" />
                          </Dropdown>
                        ) : null }

                        <Statistic value = { task.score.max } valueStyle = {{ color: '#3867D6' }} prefix = { `${task.score.min} -` } />

                        <Title style = {{ marginBottom: 0, marginTop: 10, fontWeight: 600, fontSize: 13 }} level = {4}> { task.name } </Title>
                      </Card>
                    </Popover>
                  </Col>
                ) : task.score.type === '2' ? (
                  <Col key = { task._id } style = {{ marginTop: 20, height: 120, paddingRight: 8, paddingLeft: 8 }} span = {6}>
                    <Popover overlayStyle = {{ width: 300, textAlign: 'center' }} content = { <Row> { task.description ? (<Paragraph> <Text strong> Descrição: </Text> { task.description }</Paragraph>) : null } <Paragraph> <Text strong> Regra: </Text> X: { `${task.score.mult} ${task.score.observation}` } </Paragraph></Row> }>
                      <Card bordered hoverable style = {{ textAlign: 'center', height: 120 }}>
                        { getStatus() < 5 ? (
                          <Dropdown
                            overlay = {(
                              <Menu>
                                <Menu.Item onClick = { () => this.openEditTaskModal(task) }> <Icon type = "edit" /> Editar </Menu.Item>

                                <Menu.Item
                                  onClick = { () => {
                                    const _this = this;
                                    Modal.confirm({
                                      title: 'Deseja realmente apagar esta tarefa?',
                                      content: 'Esta ação é permanente, ainda não implmentamos lixeira (e nem vamos)',
                                      okType: 'danger',
                                      onOk() {
                                        _this.deleteTask(task._id);
                                      },
                                      onCancel() {}
                                    });
                                  }}
                                >
                                  <Icon type = "delete" /> Excluir
                                </Menu.Item>
                              </Menu>
                            )}
                            placement = "bottomRight"
                          >
                            <Icon style = {{ cursor: 'pointer', position: 'absolute', right: 10, top: 13 }} type = "more" />
                          </Dropdown>
                        ) : null }

                        <Statistic value = { task.score.point } valueStyle = {{ color: '#FD9644' }} prefix = "X +" />

                        <Title style = {{ marginBottom: 0, marginTop: 10, fontWeight: 600, fontSize: 13 }} level = {4}> { task.name } </Title>
                      </Card>
                    </Popover>
                  </Col>
                ) : (
                  <Col key = { task._id } style = {{ marginTop: 20, height: 120, paddingRight: 8, paddingLeft: 8 }} span = {6}>
                    <Popover overlayStyle = {{ width: 300, textAlign: 'center' }} content = { <Row> { task.description ? (<Paragraph> <Text strong> Descrição: </Text> { task.description }</Paragraph>) : null } <Paragraph> <Text strong> Regra: </Text> X: { `${task.score.mult} ${task.score.observation}` } </Paragraph> <Paragraph> <Text strong> Máximo: </Text> { task.score.multMax } </Paragraph></Row> }>
                      <Card bordered hoverable style = {{ textAlign: 'center', height: 120 }}>
                        { getStatus() < 5 ? (
                          <Dropdown
                            overlay = {(
                              <Menu>
                                <Menu.Item onClick = { () => this.openEditTaskModal(task) }> <Icon type = "edit" /> Editar </Menu.Item>

                                <Menu.Item
                                  onClick = { () => {
                                    const _this = this;
                                    Modal.confirm({
                                      title: 'Deseja realmente apagar esta tarefa?',
                                      content: 'Esta ação é permanente, ainda não implmentamos lixeira (e nem vamos)',
                                      okType: 'danger',
                                      onOk() {
                                        _this.deleteTask(task._id);
                                      },
                                      onCancel() {}
                                    });
                                  }}
                                >
                                  <Icon type = "delete" /> Excluir
                                </Menu.Item>
                              </Menu>
                            )}
                            placement = "bottomRight"
                          >
                            <Icon style = {{ cursor: 'pointer', position: 'absolute', right: 10, top: 13 }} type = "more" />
                          </Dropdown>
                        ) : null }

                        <Statistic value = { task.score.point } valueStyle = {{ color: '#A55EEA' }} prefix = "X +" />

                        <Title style = {{ marginBottom: 0, marginTop: 10, fontWeight: 600, fontSize: 13 }} level = {4}> { task.name } </Title>
                      </Card>
                    </Popover>
                  </Col>
                )
              )) : null }
            </Row>
          ) : (
            <List
              header = {(
                <Row>
                  <Row>
                    <Col span = {8}>
                      <Text strong> Tarefa </Text>
                    </Col>

                    <Col span = {8}>
                      <Text strong> Descrição </Text>
                    </Col>

                    <Col span = {3} offset = {1}>
                      <Text strong> Pontuação </Text>
                    </Col>

                    <Col span = {3}>
                      <Text strong> Observação </Text>
                    </Col>
                  </Row>

                  <Row> <Button type = "dashed" icon = "plus" style = {{ width: '100%', marginTop: 10 }} onClick = { () => this.openNewTaskModal() }> Adicionar Nova Tarefa </Button> </Row>
                </Row>
              )}
              dataSource = { this.state.tasksSearched }
              renderItem = { (task) => (
                <List.Item>
                  <Col span = {8}> { task.name } </Col>

                  <Col span = {8}>
                    <Paragraph ellipsis> { task.description || '' } </Paragraph>
                  </Col>

                  <Col span = {3} offset = {1}> { task.score.type === '0' ? task.score.point : task.score.type === '1' ? `${task.score.min} - ${task.score.max}` : `X + ${task.score.point}` } </Col>

                  <Col span = {3}>
                    { task.score.type === '2' ? (<Paragraph> <Text strong> X: </Text> { task.score.mult } </Paragraph>) : null }
                    { task.score.type === '3' ? (<Paragraph> <Text strong> X: </Text> { task.score.mult } (max: { task.score.multMax }) </Paragraph>) : null }
                  </Col>

                  <Col span = {1}>
                    { getStatus() < 5 ? (
                      <Dropdown
                        overlay = {(
                          <Menu>
                            <Menu.Item onClick = { () => this.openEditTaskModal(task) }> <Icon type = "edit" /> Editar </Menu.Item>

                            <Menu.Item
                              onClick = { () => {
                                const _this = this;
                                Modal.confirm({
                                  title: 'Deseja realmente apagar esta tarefa?',
                                  content: 'Esta ação é permanente, ainda não implmentamos lixeira (e nem vamos)',
                                  okType: 'danger',
                                  onOk() {
                                    _this.deleteTask(task._id);
                                  },
                                  onCancel() {}
                                });
                              }}
                            >
                              <Icon type = "delete" /> Excluir
                            </Menu.Item>
                          </Menu>
                        )}
                        placement = "bottomRight"
                      >
                        <Icon style = {{ cursor: 'pointer' }} type = "more" />
                      </Dropdown>
                    ) : null }
                  </Col>
                </List.Item>
              )}
            />
          )}

          <Modal visible = { this.state.visibleTasksModal } footer = { null } onCancel = { this.hideModals } centered>
            <Paragraph style = {{ fontSize: 30, textAlign: 'center', marginBottom: 5 }}> { this.state.task ? 'Editar' : 'Nova' } Tarefa </Paragraph>

            <Divider style = {{ minWidth: '60%', width: '60%', marginTop: 0, marginLeft: 'auto', marginRight: 'auto' }}>
              <Icon type = { this.state.task ? 'edit' : 'plus-circle' } />
            </Divider>

            <Form onSubmit = { this.state.task ? this.handleSubmitEditTask : this.handleSubmitNewTask }>
              <Form.Item label = "Nome" hasFeedback validateStatus = { getFieldError('name') ? 'error' : this.state.task && getFieldValue('name') !== this.state.task.name ? 'success' : !this.state.task && getFieldValue('name') ? 'success' : '' }>
                { getFieldDecorator('name', {
                  rules: [{ required: true, whitespace: true, message: 'Por favor, insira um nome!' }]
                })(
                  <Input
                    prefix = { <Icon type = "font-colors" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                    placeholder = "Nome"
                  />
                )}
              </Form.Item>

              <Form.Item label = "Descrição" hasFeedback validateStatus = { getFieldError('description') ? 'error' : this.state.task && getFieldValue('description') !== this.state.task.description ? 'success' : !this.state.task && getFieldValue('description') ? 'success' : '' }>
                { getFieldDecorator('description')(
                  <Input.TextArea placeholder = "Descrição da Tarefa" autosize = {{ minRows: 5, maxRows: 5 }} />
                )}
              </Form.Item>

              <Form.Item label = "Tipo" hasFeedback validateStatus = { getFieldError('type') ? 'error' : this.state.task && getFieldValue('type') !== this.state.task.score.type ? 'success' : !this.state.task && getFieldValue('type') ? 'success' : '' }>
                { getFieldDecorator('type', {
                  rules: [{ required: true, message: 'Por favor, insira um tipo!' }],
                  initialValue: '0'
                })(
                  <Select placeholder = "Tipo da Tarefa">
                    <Select.Option value = "0"> Valor Fixo </Select.Option>
                    <Select.Option value = "1"> Mínimo/Máximo </Select.Option>
                    <Select.Option value = "2"> Fórmula </Select.Option>
                    <Select.Option value = "3"> Fórmula + Valor Máximo </Select.Option>
                  </Select>
                )}
              </Form.Item>

              <Row gutter = {16} style = {{ display: getFieldValue('type') === '0' || getFieldValue('type') === '2' || getFieldValue('type') === '3' ? 'block' : 'none' }}>
                <Col span = {12}>
                  <Form.Item label = "Pontuação" hasFeedback validateStatus = { getFieldError('point') ? 'error' : this.state.task && this.state.task.score.point && getFieldValue('point') !== this.state.task.score.point.toString() ? 'success' : !this.state.task && getFieldValue('point') ? 'success' : '' }>
                    { getFieldDecorator('point', {
                      normalize: (value) => (value ? value.toString().replace(/[^\d.-]/g, '') : ''),
                      rules: [{ required: getFieldValue('type') === '0' || getFieldValue('type') === '2' || getFieldValue('type') === '3', message: 'Por favor, insira uma pontuação!' }]
                    })(
                      <Input placeholder = "Pontuação" style = {{ width: 120 }} addonBefore = { getFieldValue('type') === '2' || getFieldValue('type') === '3' ? 'X +' : null } />
                    )}
                  </Form.Item>
                </Col>

                <Col span = {12} style = {{ display: getFieldValue('type') === '3' ? 'block' : 'none' }}>
                  <Form.Item label = "Valor Máximo" hasFeedback validateStatus = { getFieldError('multMax') ? 'error' : this.state.task && this.state.task.score.multMax && getFieldValue('multMax') !== this.state.task.score.multMax.toString() ? 'success' : !this.state.task && getFieldValue('multMax') ? 'success' : '' }>
                    { getFieldDecorator('multMax', {
                      normalize: (value) => (value ? value.toString().replace(/[^\d.-]/g, '') : ''),
                      rules: [{ required: getFieldValue('type') === '3', message: 'Por favor, insira um valor máximo!' }]
                    })(
                      <Input style = {{ width: 120 }} placeholder = "Valor Máximo" />
                    )}
                  </Form.Item>
                </Col>
              </Row>

              <Row gutter = {16} style = {{ display: getFieldValue('type') === '1' ? 'block' : 'none' }}>
                <Col span = {12}>
                  <Form.Item label = "Mínimo" hasFeedback validateStatus = { getFieldError('min') ? 'error' : this.state.task && getFieldValue('min') !== this.state.task.score.min ? 'success' : !this.state.task && getFieldValue('min') ? 'success' : '' }>
                    { getFieldDecorator('min', {
                      rules: [{ required: getFieldValue('type') === '1', message: 'Por favor, insira um valor mínimo!' }]
                    })(
                      <InputNumber placeholder = "Mínimo" min = {0} max = { getFieldValue('max') ? getFieldValue('max') - 1 : null } />
                    )}
                  </Form.Item>
                </Col>

                <Col span = {12}>
                  <Form.Item label = "Máximo" hasFeedback validateStatus = { getFieldError('max') ? 'error' : this.state.task && getFieldValue('max') !== this.state.task.score.max ? 'success' : !this.state.task && getFieldValue('max') ? 'success' : '' }>
                    { getFieldDecorator('max', {
                      rules: [{ required: getFieldValue('type') === '1', message: 'Por favor, insira um valor máximo!' }]
                    })(
                      <InputNumber placeholder = "Máximo" min = { getFieldValue('min') ? getFieldValue('min') + 1 : null } />
                    )}
                  </Form.Item>
                </Col>
              </Row>

              <Row gutter = {16} style = {{ display: getFieldValue('type') === '2' || getFieldValue('type') === '3' ? 'block' : 'none' }}>
                <Col span = {12}>
                  <Form.Item label = "Valor X" hasFeedback validateStatus = { getFieldError('mult') ? 'error' : this.state.task && this.state.task.score.mult && getFieldValue('mult') !== this.state.task.score.mult.toString() ? 'success' : !this.state.task && getFieldValue('mult') ? 'success' : '' }>
                    { getFieldDecorator('mult', {
                      normalize: (value) => (value ? value.toString().replace(/[^\d.-]/g, '') : ''),
                      rules: [{ required: getFieldValue('type') === '2' || getFieldValue('type') === '3', message: 'Por favor, insira o valor de X!' }]
                    })(
                      <Input placeholder = "Valor do X" />
                    )}
                  </Form.Item>
                </Col>

                <Col span = {12}>
                  <Form.Item label = "Observação" hasFeedback validateStatus = { getFieldError('observation') ? 'error' : this.state.task && getFieldValue('observation') !== this.state.task.score.observation ? 'success' : !this.state.task && getFieldValue('observation') ? 'success' : '' }>
                    { getFieldDecorator('observation', {
                      rules: [{ required: getFieldValue('type') === '2' || getFieldValue('type') === '3', message: 'Por favor, insira a observação!' }]
                    })(
                      <Input placeholder = "Ex: por dias úteis." />
                    )}
                  </Form.Item>
                </Col>
              </Row>

              <Row>
                <Col span = {14} offset = {8} style = {{ textAlign: 'right' }}>
                  <Button type = "primary" htmlType = "submit" loading = { this.state.loadingTasksModalButton }> Salvar </Button>
                </Col>
              </Row>
            </Form>
          </Modal>

          <Modal visible = { this.state.visibleTaskMemberModal } footer = { null } onCancel = { this.hideModals } centered>
            <Paragraph style = {{ fontSize: 30, textAlign: 'center', marginBottom: 5 }}> Pontos Para Membros </Paragraph>

            <Divider style = {{ minWidth: '60%', width: '60%', marginTop: 0, marginLeft: 'auto', marginRight: 'auto' }}>
              <Icon type = "trophy" />
            </Divider>

            <Form onSubmit = { this.handleTaskToMember }>
              <Form.Item label = "Membro" hasFeedback validateStatus = { getFieldError('member') ? 'error' : getFieldValue('member') ? 'success' : '' }>
                { getFieldDecorator('member', {
                  rules: [{ required: true, message: 'Por favor, selecione um membro!' }]
                })(
                  <Select allowClear placeholder = "Membro" showSearch filterOption = { this.filteredOptions }>
                    <Select.OptGroup key = "Diretoria" label = "Diretoria">
                      { this.state.members.map(item => (
                        item.status < 5 ? (
                          <Select.Option key = { `${item.name} ${item.username}` } value = { item._id }>
                            { item.name } ({ item.username })
                          </Select.Option>
                        ) : null
                      )) }
                    </Select.OptGroup>

                    { usersStatus.map((categoria, index) => (
                      index > 4 && index < 7 ? (
                        <Select.OptGroup key = { categoria.title } label = { categoria.title }>
                          { this.state.members.map(item => (
                            index === item.status ? (
                              <Select.Option key = { `${item.name} ${item.username}` } value = { item._id }>
                                { item.name } ({ item.username })
                              </Select.Option>
                            ) : null
                          )) }
                        </Select.OptGroup>
                      ) : null
                    )) }
                  </Select>
                )}
              </Form.Item>

              <Form.Item label = "Tarefa" hasFeedback validateStatus = { getFieldError('task') ? 'error' : getFieldValue('task') ? 'success' : '' }>
                { getFieldDecorator('task', {
                  rules: [{ required: true, message: 'Por favor, selecione uma tarefa!' }]
                })(
                  <Select allowClear placeholder = "Tarefa" showSearch filterOption = { this.filteredOptions } onChange = { (value) => (this.state.tasks[this.state.tasks.map(e => e._id).indexOf(value)].score.type === '0' ? setFieldsValue({ point: this.state.tasks[this.state.tasks.map(e => e._id).indexOf(value)].score.point }) : null)}>
                    { this.state.tasks.map(item => (
                      <Select.Option key = { `${item.name} ${item.description}` } value = { item._id }>
                        { item.name }
                      </Select.Option>
                    )) }
                  </Select>
                )}
              </Form.Item>

              { getFieldValue('task') ? (
                <Row>
                  <Col span = {12}>
                    <Form.Item label = { this.state.tasks[this.state.tasks.map(e => e._id).indexOf(getFieldValue('task'))].score.type === '2' || this.state.tasks[this.state.tasks.map(e => e._id).indexOf(getFieldValue('task'))].score.type === '3' ? this.state.tasks[this.state.tasks.map(e => e._id).indexOf(getFieldValue('task'))].score.observation : 'Pontuação' } hasFeedback validateStatus = { getFieldError('points') ? 'error' : getFieldValue('points') ? 'success' : '' }>
                      { getFieldDecorator('points', {
                        initialValue: this.state.tasks[this.state.tasks.map(e => e._id).indexOf(getFieldValue('task'))].score.type === '0' ? this.state.tasks[this.state.tasks.map(e => e._id).indexOf(getFieldValue('task'))].score.point : 0,
                        rules: [{ required: true, message: `Por favor, insira a ${this.state.tasks[this.state.tasks.map(e => e._id).indexOf(getFieldValue('task'))].score.type === '2' || this.state.tasks[this.state.tasks.map(e => e._id).indexOf(getFieldValue('task'))].score.type === '3' ? 'medida' : 'pontuação'}!` }]
                      })(
                        <InputNumber disabled = { this.state.tasks[this.state.tasks.map(e => e._id).indexOf(getFieldValue('task'))].score.type === '0' } placeholder = { this.state.tasks[this.state.tasks.map(e => e._id).indexOf(getFieldValue('task'))].score.type === '2' || this.state.tasks[this.state.tasks.map(e => e._id).indexOf(getFieldValue('task'))].score.type === '3' ? 'Medida' : 'Pontuação' } max = { this.state.tasks[this.state.tasks.map(e => e._id).indexOf(getFieldValue('task'))].score.max } min = { this.state.tasks[this.state.tasks.map(e => e._id).indexOf(getFieldValue('task'))].score.min || 0 } />
                      )}
                    </Form.Item>
                  </Col>

                  <Col span = {12}>
                    { this.state.tasks[this.state.tasks.map(e => e._id).indexOf(getFieldValue('task'))].score.type === '1' ? (
                      <Row style = {{ marginTop: 20 }}>
                        <Paragraph> <Text strong> Mínimo: </Text> { this.state.tasks[this.state.tasks.map(e => e._id).indexOf(getFieldValue('task'))].score.min } </Paragraph>
                        <Paragraph> <Text strong> Máximo: </Text> { this.state.tasks[this.state.tasks.map(e => e._id).indexOf(getFieldValue('task'))].score.max } </Paragraph>
                      </Row>
                    ) : null }
                  </Col>
                </Row>
              ) : null }

              <Row>
                <Col span = {14} offset = {8} style = {{ textAlign: 'right' }}>
                  <Button type = "primary" htmlType = "submit" loading = { this.state.loadingTaskMemberModalButton }> Salvar </Button>
                </Col>
              </Row>
            </Form>
          </Modal>
        </Spin>
      </MainLayout>
    );
  }
}

export default Form.create()(Gamification);
