// calendar.js

import React, { Component } from 'react';
import { Row, Col, Form, Select, Input, DatePicker, Upload, Button, Checkbox, Radio, Calendar, Drawer, Modal, Divider, Avatar, Icon, Typography, Spin, Popover, Carousel, Tag, notification, message } from 'antd';
import { Redirect } from 'react-router-dom';

import './calendar.css';

import axios from 'axios';
import moment from 'moment';

import { getStatus } from '../../services/auth';

import MainLayout from '../../components/layout/layout';

const { Paragraph, Text, Title } = Typography;
const { RangePicker } = DatePicker;
const { Option } = Select;
const { CheckableTag } = Tag;
const { TextArea, Search } = Input;
const { confirm } = Modal;
const RadioGroup = Radio.Group;

let photoIndex = 0;

const eventsLabel = [
  { name: 'FEJEPAR', color: '#FC5C65' },
  { name: 'Reunião DIREX', color: '#FA8231' },
  { name: 'Reunião Geral', color: '#FD9644' },
  { name: 'Reunião com Cliente', color: '#F7B731' },
  { name: 'Levantamento de Requisitos', color: '#FED330' },
  { name: 'Planning Poker', color: '#26DE81' },
  { name: 'Processo Seletivo', color: '#0FB9B1' },
  { name: 'Reunião do Conselho', color: '#2D98DA' },
  { name: 'Reunião de Projeto', color: '#4B7BEC' },
  { name: 'Outros', color: '#A55EEA' }
];

const usersStatus = [
  { color: '#FED330', icon: 'star', title: 'Presidente', label: 'Rei da Cocada Preta' },
  { color: '#26DE81', icon: 'star', title: 'Vice-Presidente/Diretor de RH', label: 'Manda só quando o presidente não está.' },
  { color: '#45AAF2', icon: 'crown', title: 'Diretor de Projetos', label: 'The Best' },
  { color: '#0FB9B1', icon: 'star', title: 'Diretor Administrativo-Financeiro', label: 'Contando Centavos...' },
  { color: '#A55EEA', icon: 'star', title: 'Diretor de Marketing', label: 'Criando umas Artes' },
  { color: '#FD9644', icon: 'thunderbolt', title: 'Membro', label: 'Abastecendo-se de Café' },
  { color: '#FC5C65', icon: 'fire', title: 'Trainee', label: 'Sofrendo' },
  { color: '#4B6584', icon: 'bulb', title: 'Ancião', label: 'Os Baqueados' }
];

const checkMembersOptions = ['Convidado', 'Presente'];

class CalendarMainPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loadingPage: true,
      events: [],
      eventsFiltered: [],
      members: [],
      eventsPhotosList: [],
      eventsPhotosListUpload: [],
      searchText: '',
      checkedMemberList: checkMembersOptions[0],
      membersSelectedItems: [],
      selectedTagsCalendars: eventsLabel,
      visibleDrawer: false,
      editable: false,
      event: '',
      buttonLoading: false,
      addPhotosButtonLoading: false
    };
  }

  componentDidMount() {
    axios.get('/api/calendar').then(res => {
      axios.get('/api/members').then(resCalendar => {
        const members = resCalendar.data;

        members.sort((obj1, obj2) => (obj1.status < obj2.status ? -1 : (obj1.status < obj2.status ? 1 : 0)));

        this.setState({ members, loadingPage: false, events: res.data, eventsFiltered: res.data });
        this.searchEvents(this.state.searchText, this.state.checkedMemberList, this.state.membersSelectedItems, this.state.selectedTagsCalendars);
      }).catch(() => {
        // console.log(errCalendar);
      });
    }).catch(() => {
      // console.log(err);
    });
  }

  dateCellRender = (value) => {
    if (this.state.eventsFiltered.length > 0) {
      const valueFormatted = moment(value);
      return (
        <ul>
          { this.state.eventsFiltered.map(item => (
            valueFormatted.isSameOrAfter(moment(item.initialDate), 'day') && valueFormatted.isSameOrBefore(moment(item.finalDate), 'day') ? (
              <Popover
                key = { item._id }
                placement = "topLeft"
                trigger = "click"
                overlayStyle = {{ zIndex: 30 }}
                content = {(
                  <div style = {{ padding: 10, width: 400 }}>
                    { getStatus() < 5 ? (
                      <Row style = {{ textAlign: 'right', marginBottom: 10 }}>
                        <Icon onClick = { () => this.showEditableDrawer(item) } className = "calendar-edit-event" style = {{ cursor: 'pointer', marginRight: 10 }} type = "edit" />
                        <Icon onClick = { () => this.handleDelete(item) } className = "calendar-delete-event" style = {{ cursor: 'pointer' }} type = "delete" />
                      </Row>
                    ) : null }

                    <Row>
                      <Col>
                        <Title level = {4} style = {{ marginBottom: 0, paddingBottom: 6, borderBottom: '1px solid #E9E9E9', fontWeight: 500 }}>
                          <Popover placement = "topLeft" content = { eventsLabel[item.status].name } arrowPointAtCenter>
                            <Avatar size = {14} style = {{ backgroundColor: `${eventsLabel[item.status].color}`, marginBottom: 3, marginRight: 14 }} />
                          </Popover>
                          { item.name }
                        </Title>

                        <div style = {{ height: 10 }} />

                        <Text>
                          <Icon type = "calendar" style = {{ marginRight: 5 }} /> {(moment(item.finalDate).diff(moment(item.initialDate), 'days')) === 0 ? item.allDay ? moment(item.initialDate).format('dddd, D MMMM') : `${moment(item.initialDate).format('dddd, D MMMM')} ⠂${moment(item.initialDate).format('HH:mm')} - ${moment(item.finalDate).format('HH:mm')}` : item.allDay ? `${moment(item.initialDate).format('D')} - ${moment(item.finalDate).format('D')} de ${moment(item.finalDate).format('MMMM')} de ${moment(item.finalDate).format('YYYY')}` : `${moment(item.initialDate).format('D MMMM YYYY, HH:mm')} - ${moment(item.finalDate).format('D MMMM YYYY, HH:mm')}`}
                        </Text>

                        <div style = {{ height: 10 }} />

                        <Text>
                          { item.description ? <Icon type = "align-left" style = {{ marginRight: 5 }} /> : null} {item.description}
                        </Text>

                        <div style = {{ height: 10 }} />

                        <Text>
                          { item.membersInvited.length > 0 && item.membersInvited[0].length > 0 ? (
                            <div>
                              <Icon type = "user" style = {{ marginRight: 5 }} />
                              { item.membersInvited.length } membros convidados
                            </div>
                          ) : null }
                        </Text>

                        <div style = {{ height: 5 }} />

                        { item.membersInvited.length > 0 && item.membersInvited[0].length > 0 ? (
                          item.membersInvited.map(memberID => {
                            const id = memberID[0];
                            const member = this.state.members.map(e => e._id).indexOf(id);
                            const memberParticipation = item.membersPresent.map(e => e[0]).includes(id);

                            return (
                              member > -1 ? (
                                <Paragraph key = {id} style = {{ marginBottom: 5 }}>
                                  <Avatar style = {{ height: 24, width: 24, marginRight: 5, marginLeft: 20 }} src = { this.state.members[member].image } />
                                  { memberParticipation ? <Avatar size = {16} style = {{ backgroundColor: '#87d068', marginLeft: -12, verticalAlign: 'bottom', marginBottom: -2 }} icon = "check" /> : null }
                                  <Text style = {{ marginLeft: 2 }}> { this.state.members[member].name } </Text>
                                </Paragraph>
                              ) : null
                            );
                          })
                        ) : null }

                        <div style = {{ height: 5 }} />

                        <Carousel autoplay>
                          { item.photos && item.photos.length > 0 ? (
                            item.photos.map(photo => (
                              <div key = {photo.url} style = {{ width: '100% !important', height: '200px !important', overflow: 'hidden !important' }}>
                                <img style = {{ width: '100%', height: 200, objectFit: 'cover' }} alt = "Imagem do Evento" src = { photo.url } />
                              </div>
                            ))
                          ) : null }
                        </Carousel>
                      </Col>
                    </Row>
                  </div>
                )}
              >
                <Paragraph style = {{ cursor: 'pointer !important', marginBottom: 0, marginTop: 0 }}>
                  <Popover placement = "topLeft" content = { eventsLabel[item.status].name } arrowPointAtCenter>
                    <Avatar size = {8} style = {{ backgroundColor: `${eventsLabel[item.status].color}`, verticalAlign: 'baseline', marginRight: 5 }} />
                  </Popover>

                  { moment(item.initialDate).isSame(moment(item.finalDate), 'day') ? item.name : `${item.name} (Dia ${(moment(item.finalDate).diff(moment(item.initialDate), 'days')) - moment(item.finalDate).endOf('day').diff(valueFormatted, 'days') + 1} - ${moment(item.finalDate).diff(moment(item.initialDate), 'days') + 1})` }
                </Paragraph>
              </Popover>
            ) : null
          )) }
        </ul>
      );
    }
    return (<span />);
  }

  dateValidation = (rule, value, callback) => {
    if (value && value[0].isAfter(value[1])) {
      callback('Data Inicial deve ser menor que Data Final');
    } else {
      callback();
    }
  }

  filteredOptions = (value, option) => {
    if (!option.props.label && option.key.toLowerCase().includes(value.toLowerCase())) return true;

    return false;
  }

  searchEvents = (searchText, checkedMemberOption, membersSelectedItems, selectedTagsCalendars) => {
    const eventsFiltered = this.state.events.filter(e => {
      if ((e.name.toLowerCase().includes(searchText.toLowerCase()) || (e.description && e.description.toLowerCase().includes(searchText.toLowerCase()))) && selectedTagsCalendars.map(r => eventsLabel.indexOf(r)).includes(e.status)) {
        if (membersSelectedItems.length === 0) return true;
        if (checkedMemberOption === 'Convidado' && e.membersInvited.some(r => membersSelectedItems.includes(r[0]))) return true;
        if (checkedMemberOption === 'Presente' && e.membersPresent.some(r => membersSelectedItems.includes(r[0]))) return true;
      }

      return false;
    });

    this.setState({ eventsFiltered });
  }

  onChangeMembersOption = (e) => {
    this.setState({ checkedMemberList: e.target.value });
    this.searchEvents(this.state.searchText, e.target.value, this.state.membersSelectedItems, this.state.selectedTagsCalendars);
  }

  searchEventsByMembers = (membersSelectedItems) => {
    this.setState({ membersSelectedItems });
    this.searchEvents(this.state.searchText, this.state.checkedMemberList, membersSelectedItems, this.state.selectedTagsCalendars);
  }

  searchEventsByValue = (searchText) => {
    this.setState({ searchText });
    this.searchEvents(searchText, this.state.checkedMemberList, this.state.membersSelectedItems, this.state.selectedTagsCalendars);
  }

  searchEventsByCalendar = (tag, checked) => {
    const selectedTagsCalendars = checked ? [...this.state.selectedTagsCalendars, tag] : this.state.selectedTagsCalendars.filter(t => t.name !== tag.name);
    this.setState({ selectedTagsCalendars });
    this.searchEvents(this.state.searchText, this.state.checkedMemberList, this.state.membersSelectedItems, selectedTagsCalendars);
  }

  clearFilters = () => {
    this.setState({
      searchText: '',
      membersSelectedItems: [],
      selectedTagsCalendars: eventsLabel
    });

    this.searchEvents('', this.state.checkedMemberList, [], eventsLabel);
  }

  showDrawer = () => {
    this.setState({ visibleDrawer: true });
  }

  showEditableDrawer = (event) => {
    this.props.form.setFieldsValue({
      name: event.name,
      date: [moment(event.initialDate), moment(event.finalDate)],
      allDay: event.allDay,
      status: event.status.toString(),
      description: event.description,
      membersInvited: event.membersInvited.length > 0 && event.membersInvited[0].length > 0 ? event.membersInvited.map(e => e[0]) : [],
      membersPresent: event.membersPresent.length > 0 && event.membersPresent[0].length > 0 ? event.membersPresent.map(e => e[0]) : []
    });

    this.setState({ editable: true, event, eventsPhotosList: event.photos.slice(0), visibleDrawer: true });
  }

  hideDrawer = () => {
    this.setState({ visibleDrawer: false, editable: false, event: '', eventsPhotosList: [], eventsPhotosListUpload: [] });

    this.props.form.resetFields();
  }

  beforeUploadPhotos = (file, fileList) => {
    photoIndex = 0;

    if (file.type === 'image/jpeg' || file.type === 'image/png') {
      this.setState({ eventsPhotosListUpload: fileList });
      return true;
    }

    message.error('Extensão Inválida! Permitido apenas imagens');
    this.setState({ eventsPhotosListUpload: [] });
    return false;
  }

  addEventPhotos = () => {
    this.setState({ addPhotosButtonLoading: true });
    const currentPhotoIndex = photoIndex;
    const photosTotalSize = this.state.eventsPhotosList.length + this.state.eventsPhotosListUpload.length;
    const fileCurrent = this.state.eventsPhotosListUpload[currentPhotoIndex];
    photoIndex++;

    const formData = new FormData();
    formData.append('api_key', '784737987534174');
    formData.append('timestamp', (Date.now() / 1000));
    formData.append('upload_preset', 'hatsxc2p');
    formData.append('file', fileCurrent);

    axios.post('https://api.cloudinary.com/v1_1/haken/image/upload', formData, {
      headers: { 'X-Requested-With': 'XMLHttpRequest' }
    }).then(res => {
      const photos = this.state.eventsPhotosList.concat({
        uid: fileCurrent.uid,
        name: res.data.original_filename,
        url: res.data.secure_url,
        thumbUrl: res.data.secure_url
      });

      this.setState({ eventsPhotosList: photos });
      if (photosTotalSize === photos.length) {
        this.setState({ addPhotosButtonLoading: false });
      }
    }).catch(() => {
      // console.log(err);
      this.setState({ addPhotosButtonLoading: false });

      notification.error({
        message: 'Errooouu!',
        description: 'Tem problema aqui, ó: Não conseguimos enviar dados no Servidor de Imagens.'
      });
    });
  }

  onRemovePhoto = (file) => {
    const { eventsPhotosList } = this.state;
    eventsPhotosList.splice(eventsPhotosList.map(e => e.uid).indexOf(file.uid), 1);

    this.setState({ eventsPhotosList });
    return false;
  }

  handleSubmit = (e) => {
    e.preventDefault();

    this.setState({ buttonLoading: true });

    this.props.form.validateFields(['name', 'date', 'allDay', 'status', 'description', 'membersInvited', 'membersPresent'], (err, values) => {
      if (!err) {
        const { name, date, allDay, description, status, membersInvited, membersPresent } = values;

        axios.post('/api/calendar', {
          name, initialDate: date[0], finalDate: date[1], allDay, description, status, membersInvited: membersInvited || [], membersPresent: membersPresent || [], photos: this.state.eventsPhotosList
        }).then(() => {
          this.setState({ buttonLoading: false });

          notification.success({
            message: 'Uhull',
            description: 'Evento Cadastrado!'
          });

          this.hideDrawer();
          this.componentDidMount();
        }).catch(() => {
          // console.log(error);
          this.setState({ buttonLoading: false });

          notification.error({
            message: 'Errooouu!',
            description: 'Tem problema aqui, ó: Não conseguimos enviar dados no Servidor.'
          });
        });
      } else {
        this.setState({ buttonLoading: false });
      }
    });
  }

  handleEdit = (e) => {
    e.preventDefault();

    this.setState({ buttonLoading: true });

    this.props.form.validateFields(['name', 'date', 'allDay', 'status', 'description', 'membersInvited', 'membersPresent'], (err, values) => {
      if (!err) {
        const { name, date, allDay, description, status, membersInvited, membersPresent } = values;

        axios.put('/api/calendar', {
          id: this.state.event._id, name, initialDate: date[0], finalDate: date[1], allDay, description, status, membersInvited, membersPresent, photos: this.state.eventsPhotosList
        }).then(() => {
          this.setState({ buttonLoading: false });

          notification.success({
            message: 'Uhull',
            description: 'Evento Editado!'
          });

          this.hideDrawer();
          this.componentDidMount();
        }).catch(() => {
          // console.log(error);
          this.setState({ buttonLoading: false });

          notification.error({
            message: 'Errooouu!',
            description: 'Tem problema aqui, ó: Não conseguimos enviar dados no Servidor.'
          });
        });
      } else {
        this.setState({ buttonLoading: false });
      }
    });
  }

  handleDelete = (event) => {
    const _this = this;

    confirm({
      title: 'Certeza absoluta que quer apagar este evento?',
      content: 'Certeza absoluta mesmo? Não vai dar pra voltar atrás hein.',
      okType: 'danger',
      onOk() {
        axios.delete(`/api/calendar/${event._id}`).then(() => {
          notification.success({
            message: 'Uhull',
            description: 'Evento Apagado!'
          });

          _this.componentDidMount();
        }).catch(() => {
          // console.log(err);

          notification.error({
            message: 'Errooouu!',
            description: 'Tem problema aqui, ó: Não conseguimos apagar evento do Servidor.'
          });
        });
      },
      onCancel() {}
    });
  }

  render() {
    if (['-1'].includes(getStatus())) return (<Redirect to = "/notAuthorizate" />);

    const { getFieldDecorator, getFieldError, getFieldValue } = this.props.form;

    return (
      <MainLayout page = "calendar">
        <Spin spinning = { this.state.loadingPage }>
          <Row gutter = {16}>
            <Col span = {12}>
              <Search placeholder = "Pesquise eventos por nome ou descrição" value = { this.state.searchText } onChange = { e => this.searchEventsByValue(e.target.value) } onSearch = { value => this.searchEventsByValue(value) } />
            </Col>

            <Col span = {1}>
              <Popover content = "Limpar Filtros">
                <Button icon = "close" type = "danger" style = {{ width: 30, textAlign: 'center' }} onClick = { this.clearFilters } />
              </Popover>
            </Col>

            <Col span = {4}>
              <Popover
                placement = "bottomRight"
                title = "Filtros de Pesquisa"
                content = {(
                  <div>
                    <div style = {{ marginTop: 10, paddingBottom: 6, borderBottom: '1px solid #E9E9E9' }}>
                      <RadioGroup options = { checkMembersOptions } value = { this.state.checkedMemberList } onChange = { this.onChangeMembersOption } />

                      <br />

                      <Select mode = "multiple" placeholder = "Membros" style = {{ width: 400, marginTop: 10 }} value = { this.state.membersSelectedItems } filterOption = { this.filteredOptions } onChange = { this.searchEventsByMembers }>
                        <Select.OptGroup key = "Diretoria" label = "Diretoria">
                          { this.state.members.map(item => (
                            item.status < 5 ? (
                              <Select.Option key = { `${item.name} ${item.username}` } value = { item._id }>
                                { item.name } ({ item.username })
                              </Select.Option>
                            ) : null
                          )) }
                        </Select.OptGroup>

                        { usersStatus.map((categoria, index) => (
                          index > 4 ? (
                            <Select.OptGroup key = { categoria.title } label = { categoria.title }>
                              { this.state.members.map(item => (
                                index === item.status ? (
                                  <Select.Option key = { `${item.name} ${item.username}` } value = { item._id }>
                                    { item.name } ({ item.username })
                                  </Select.Option>
                                ) : null
                              )) }
                            </Select.OptGroup>
                          ) : null
                        )) }
                      </Select>
                    </div>

                    <br />

                    <Row style = {{ maxWidth: 400, textAlign: 'center' }}>
                      { eventsLabel.map(tag => (
                        <CheckableTag style = {{ color: `${tag.color}`, borderColor: `${tag.color}`, background: `${tag.color}20`, marginTop: 5 }} key = { tag.name } checked = { this.state.selectedTagsCalendars.indexOf(tag) > -1 } onChange = { checked => this.searchEventsByCalendar(tag, checked) }>
                          { tag.name }
                        </CheckableTag>
                      ))}
                    </Row>
                  </div>
                )}
              >
                <Button icon = "filter" style = {{ width: '100%', textAlign: 'left' }}> Outros Filtros </Button>
              </Popover>
            </Col>

            { getStatus() < 5 ? (
              <Button onClick = { this.showDrawer } style = {{ textAlign: 'right' }} type = "primary" icon = "plus"> Adicionar Evento </Button>
            ) : null }
          </Row>

          <Calendar style = {{ marginTop: 50 }} dateCellRender = { this.dateCellRender } />
        </Spin>

        <Drawer visible = { this.state.visibleDrawer } width = {720} footer = { null } onClose = { this.hideDrawer } centered>
          <Paragraph style = {{ fontSize: 30, textAlign: 'center', marginBottom: 5 }}> { this.state.editable ? 'Editar Evento' : 'Novo Evento' } </Paragraph>

          <Divider style = {{ minWidth: '60%', width: '60%', marginTop: 0, marginLeft: 'auto', marginRight: 'auto' }}>
            <Icon type = "plus-circle" />
          </Divider>

          <Row style = {{ marginTop: 20, paddingLeft: 10, paddingRight: 10 }} gutter = {16}>
            <Form onSubmit = { this.state.editable ? this.handleEdit : this.handleSubmit }>
              <Form.Item label = "Nome" hasFeedback validateStatus = { getFieldError('name') ? 'error' : this.state.editable && getFieldValue('name') !== this.state.event.name ? 'success' : !this.state.editable && getFieldValue('name') ? 'success' : '' }>
                { getFieldDecorator('name', {
                  rules: [{ required: true, whitespace: true, message: 'Por favor, insira um nome!' }]
                })(
                  <Input
                    prefix = { <Icon type = "user" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                    placeholder = "Nome"
                  />
                )}
              </Form.Item>

              <Form.Item style = {{ marginBottom: 5 }} label = "Data do Evento" hasFeedback validateStatus = { getFieldError('date') ? 'error' : this.state.editable && getFieldValue('date') !== [moment(this.state.event.initialDate), moment(this.state.event.finalDate)] ? 'success' : !this.state.editable && getFieldValue('date') ? 'success' : '' }>
                { getFieldDecorator('date', {
                  initialValue: [moment(), moment()],
                  rules: [
                    { required: true, message: 'Por favor, insira uma data!' },
                    { validator: this.dateValidation }
                  ]
                })(
                  <RangePicker separator = "---" showTime = { !getFieldValue('allDay') } style = {{ width: '100%' }} placeholder = "Data" format = { getFieldValue('allDay') ? 'DD/MM/YYYY' : 'DD/MM/YYYY - HH:mm' } allowClear = {false} />
                )}
              </Form.Item>

              { getFieldDecorator('allDay', {
                valuePropName: 'checked',
                initialValue: true
              })(
                <Checkbox style = {{ marginBottom: 25 }}> Dia Inteiro </Checkbox>
              )}

              <Form.Item label = "Tipo de Evento" hasFeedback validateStatus = { getFieldError('status') ? 'error' : this.state.editable && getFieldValue('status').toString() !== this.state.event.status.toString() ? 'success' : !this.state.editable && getFieldValue('status') ? 'success' : '' }>
                { getFieldDecorator('status', {
                  rules: [{ required: true, message: 'Por favor, selecione uma opção!' }]
                })(
                  <Select showSearch placeholder = "Tipo de Evento" filterOption = { this.filteredOptions }>
                    { eventsLabel.map((item, index) => (
                      <Option key = { item.name } value = { index.toString() }>
                        <Avatar size = {14} style = {{ backgroundColor: `${item.color}` }} /> { item.name }
                      </Option>
                    )) }
                  </Select>
                )}
              </Form.Item>

              <Form.Item label = "Descrição" hasFeedback validateStatus = { getFieldError('description') ? 'error' : this.state.editable && getFieldValue('description') !== this.state.event.description ? 'success' : !this.state.editable && getFieldValue('description') ? 'success' : '' }>
                { getFieldDecorator('description')(
                  <TextArea rows = {4} placeholder = "Descrição" />
                )}
              </Form.Item>

              <Form.Item label = "Membros Convidados" hasFeedback validateStatus = { getFieldError('membersInvited') ? 'error' : !this.state.editable && getFieldValue('membersInvited') && getFieldValue('membersInvited').length > 0 ? 'success' : this.state.editable && this.state.event.membersInvited.map(e => e[0]).toString() !== getFieldValue('membersInvited').toString() ? 'success' : '' }>
                { getFieldDecorator('membersInvited')(
                  <Select mode = "multiple" placeholder = "Membros Convidados" style = {{ width: '100%' }} filterOption = { this.filteredOptions }>
                    <Select.OptGroup key = "Diretoria" label = "Diretoria">
                      { this.state.members.map(item => (
                        item.status < 5 ? (
                          <Select.Option key = { `${item.name} ${item.username}` } value = { item._id }>
                            { item.name } ({ item.username })
                          </Select.Option>
                        ) : null
                      )) }
                    </Select.OptGroup>

                    { usersStatus.map((categoria, index) => (
                      index > 4 ? (
                        <Select.OptGroup key = { categoria.title } label = { categoria.title }>
                          { this.state.members.map(item => (
                            index === item.status ? (
                              <Select.Option key = { `${item.name} ${item.username}` } value = { item._id }>
                                { item.name } ({ item.username })
                              </Select.Option>
                            ) : null
                          )) }
                        </Select.OptGroup>
                      ) : null
                    )) }
                  </Select>
                )}
              </Form.Item>

              <Form.Item label = "Membros Presentes" hasFeedback validateStatus = { getFieldError('membersPresent') ? 'error' : !this.state.editable && getFieldValue('membersPresent') && getFieldValue('membersPresent').length > 0 ? 'success' : this.state.editable && this.state.event.membersPresent.map(e => e[0]).toString() !== getFieldValue('membersPresent').toString() ? 'success' : '' }>
                { getFieldDecorator('membersPresent')(
                  <Select mode = "multiple" placeholder = "Membros Presentes" style = {{ width: '100%' }} filterOption = { this.filteredOptions }>
                    <Select.OptGroup key = "Diretoria" label = "Diretoria">
                      { this.props.form.getFieldValue('membersInvited') ?
                        this.props.form.getFieldValue('membersInvited').map(memberInvited => (
                          this.state.members.map(item => (
                            item.status < 5 && item._id === memberInvited ? (
                              <Select.Option key = { `${item.name} ${item.username}` } value = { item._id }>
                                { item.name } ({ item.username })
                              </Select.Option>
                            ) : null
                          ))
                        )) : null
                      }
                    </Select.OptGroup>

                    { this.props.form.getFieldValue('membersInvited') ?
                      this.props.form.getFieldValue('membersInvited').map(memberInvited => (
                        usersStatus.map((categoria, index) => (
                          index > 4 ? (
                            <Select.OptGroup key = { categoria.title } label = { categoria.title }>
                              { this.state.members.map(item => (
                                index === item.status && item._id === memberInvited ? (
                                  <Select.Option key = { `${item.name} ${item.username}` } value = { item._id }>
                                    { item.name } ({ item.username })
                                  </Select.Option>
                                ) : null
                              )) }
                            </Select.OptGroup>
                          ) : null
                        ))
                      )) : null
                    }
                  </Select>
                )}
              </Form.Item>

              <Upload
                beforeUpload = { this.beforeUploadPhotos }
                customRequest = { this.addEventPhotos }
                listType = "picture-card"
                fileList = { this.state.eventsPhotosList }
                onRemove = { this.onRemovePhoto }
                multiple
                disabled = { this.state.addPhotosButtonLoading }
                accept = "image/*"
              >
                <Text style = {{ marginTop: 8, color: '#666' }}> <Icon type = { this.state.addPhotosButtonLoading ? 'loading' : 'plus' } /> <br /> Fotos do Evento </Text>
              </Upload>

              <Row style = {{ textAlign: 'right' }}>
                <Button size = "default" onClick = { this.hideDrawer } style = {{ marginRight: 8 }}> Cancelar </Button>
                <Button loading = { this.state.buttonLoading } type = "primary" htmlType = "submit" size = "default"> { this.state.editable ? 'Editar' : 'Cadastrar' } </Button>
              </Row>
            </Form>
          </Row>
        </Drawer>
      </MainLayout>
    );
  }
}

export default Form.create()(CalendarMainPage);
