// customer.js

import React, { Component } from 'react';
import { Button, Avatar, Select, List, Card, Form, Col, Icon, Divider, Row, Modal, Tag, Input, Typography, Spin, Popover, notification } from 'antd';
import { Link, Redirect } from 'react-router-dom';

import axios from 'axios';
import moment from 'moment';

import { getStatus } from '../../services/auth';

import MainLayout from '../../components/layout/layout';

import './customer.css';

const { Meta } = Card;
const { Paragraph, Title, Text } = Typography;
const { Search } = Input;
const { Option } = Select;

const projectStatus = [
  { color: '#3867D6', icon: 'deployment-unit', title: 'Planejamento', label: 'Começando o sofrimento denovo.' },
  { color: '#FA8231', icon: 'rocket', title: 'Inicializando', label: 'Fechando contrato e preparando as almas de membros para o abate.' },
  { color: '#45AAF2', icon: 'coffee', title: 'Em Andamento', label: 'Abastecendo os membros com café para manter o progresso.' },
  { color: '#778CA3', icon: 'frown', title: 'Cancelado', label: 'Cliente deu pra trás.' },
  { color: '#EB3B5A', icon: 'stop', title: 'Congelado', label: 'Sei lá, deu uma parada por agora.' },
  { color: '#20BF6B', icon: 'like', title: 'Acabou', label: 'Acaboooouuuu!!' }
];

const projectLabels = [
  { color: '#F7B731', title: 'Consultoria' },
  { color: '#FED330', title: 'Site' },
  { color: '#26DE81', title: 'Aplicativo' },
  { color: '#0FB9B1', title: 'Sistema Web' },
  { color: '#2BCBBA', title: 'Sistema Interno' }
];

class CustomerMainPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loadingPage: true,
      customers: [],
      tags: [],
      customer: '',
      visibleModalPassword: false,
      visibleModalProfile: false,
      passwordsButtonLoading: false
    };
  }

  componentDidMount() {
    axios.get('/api/customers').then(res => {
      const customers = res.data;

      customers.sort((obj1, obj2) => (moment(obj1.createdAt).isBefore(obj2.createdAt) ? 1 : (moment(obj1.createdAt).isAfter(obj2.createdAt) ? -1 : 0)));

      axios.get('/api/projects').then(resProjects => {
        const projects = resProjects.data;

        customers.forEach((customer, index) => {
          customers[index].projects = projects.filter(e => customer._id === e.customer[0]);
        });

        this.setState({ customers, loadingPage: false });
      }).catch(() => {
        // console.log(err);
      });
    }).catch(() => {
      // console.log(err);
    });
  }

  search = (e) => {
    if (e.target.value === '') { this.componentDidMount(); }

    axios.get(`/api/customers/search/${e.target.value}`).then(res => {
      const customers = res.data;

      customers.sort((obj1, obj2) => (moment(obj1.createdAt).isBefore(obj2.createdAt) ? 1 : (moment(obj1.createdAt).isAfter(obj2.createdAt) ? -1 : 0)));

      axios.get('/api/projects').then(resProjects => {
        const projects = resProjects.data;

        customers.forEach((customer, index) => {
          customers[index].projects = projects.filter(r => customer._id === r.customer[0]);
        });

        this.setState({ customers });
      }).catch(() => {
        // console.log(err);
      });
    }).catch(() => {
      // console.log(err);
    });
  }

  filteredOptions = (value, option) => {
    if (!option.props.label && option.key.toLowerCase().includes(value.toLowerCase())) return true;

    return false;
  }

  showModalChangePassword = (customer) => {
    this.setState({ visibleModalPassword: true, customer });
  }

  showModalProfile = (customer) => {
    this.setState({ visibleModalProfile: true, customer });
  }

  hideModal = () => {
    this.setState({
      visibleModalPassword: false,
      visibleModalProfile: false,
      customer: ''
    });

    this.props.form.resetFields();
  }

  compareToFirstPassword = (rule, value, callback) => {
    if (value && value !== this.props.form.getFieldValue('password')) {
      callback('Senhas Diferentes!');
    } else {
      callback();
    }
  }

  validateToNextPassword = (rule, value, callback) => {
    if (value && this.props.form.getFieldValue('confirmPassword')) {
      this.props.form.validateFields(['confirmPassword'], { force: true });
    }
    callback();
  }

  handlePasswords = (e) => {
    this.setState({ passwordsButtonLoading: true });
    e.preventDefault();

    this.props.form.validateFields((err, values) => {
      if (getStatus() > 4) {
        this.setState({ passwordsButtonLoading: false });

        notification.error({
          message: 'Errooouu!',
          description: 'Tem problema aqui, ó: Você não tem autorização, mané'
        });
      } else if (!err) {
        const { password } = values;

        axios.put('/api/customers', { id: this.state.customer._id, password }).then(() => {
          this.setState({ passwordsButtonLoading: false });
          this.hideModal();

          notification.success({
            message: 'Uhull',
            description: 'Senha Atualizada com sucesso!'
          });
        }).catch(() => {
          // console.log(error);
          this.setState({ passwordsButtonLoading: false });
          this.hideModal();

          notification.error({
            message: 'Errooouu!',
            description: 'Tem problema aqui, ó: Não sabemos ao certo, tente novamente, se persistirem os sintomas o médico deverá ser consultado, ops, o técnico.'
          });
        });
      } else {
        this.setState({ passwordsButtonLoading: false });
      }
    });
  }

  render() {
    if (this.state.nav) return (<Redirect to = { this.state.nav } />);
    if (['-1'].includes(getStatus())) return (<Redirect to = "/notAuthorizate" />);

    const { getFieldDecorator, getFieldValue, getFieldError } = this.props.form;

    return (
      <MainLayout page = "customers">
        <Spin spinning = { this.state.loadingPage }>
          <Row style = {{ marginBottom: 0 }}>
            <Title id = "info" level = {2} style = {{ fontWeight: 500 }}> Nossos Clientes. </Title>

            <Divider style = {{ marginTop: 0 }} />
          </Row>

          <Row gutter = {16} type = "flex" justify = "end">
            <Col span = {12}>
              <Search
                placeholder = "Pesquise pelo nome, username, documento ou email"
                onChange = { e => this.search(e) }
                style = {{ marginBottom: 18 }}
                size = "default"
              />
            </Col>

            <Col span = {6}>
              <Select className = "membersSelectByCargo" filterOption = { this.filteredOptions } onChange = { tags => this.setState({ tags }) } style = {{ width: '100%' }} mode = "multiple" placeholder = "Pesquise por Status de Projeto">
                { projectStatus.map((item, index) => (
                  <Option key = { item.title } value = { index.toString() }>
                    <Tag color = { item.color }> { item.title } </Tag>
                  </Option>
                )) }
              </Select>
            </Col>

            { getStatus() < 5 ? (
              <Link to = "/customerSignUp">
                <Button type = "primary" icon = "plus"> Adicionar Cliente </Button>
              </Link>
            ) : null }
          </Row>

          <Row style = {{ marginBottom: 50 }}>
            <Col span = {22} offset = {1}>
              <List
                grid = {{ gutter: 16, column: 4 }}
                size = "small"
                dataSource = { this.state.customers }
                renderItem = { (item) => (
                  this.state.tags.length === 0 || item.projects.map(e => e.status.toString()).some(r => this.state.tags.includes(r)) ? (
                    <Col style = {{ paddingTop: 30 }} span = {6}>
                      <Card bordered hoverable>
                        <Avatar size = {70} src = { item.image } />

                        <Popover placement = "top" content = "Ver Perfil">
                          <Button size = "small" style = {{ position: 'absolute', right: 10, top: 10 }} type = "primary" onClick = { () => this.showModalProfile(item) } icon = "eye" />
                        </Popover>
                        { getStatus() < 5 ? ([
                          <Button.Group key = { item._id } size = "small" style = {{ position: 'absolute', right: 46, top: 10 }}>
                            <Popover placement = "top" content = "Alterar Senha">
                              <Button onClick = { () => this.showModalChangePassword(item) } icon = "lock" />
                            </Popover>
                          </Button.Group>
                        ]) : null }

                        <Meta
                          className = "meta-data-customer-card"
                          style = {{ marginTop: 20 }}
                          title = { item.name }
                          description = {[<Text key = { item._id } type = "secondary"> { item.email } </Text>]}
                        />
                      </Card>
                    </Col>
                  ) :
                    <span />
                )}
              />
            </Col>
          </Row>

          <Modal visible = { this.state.visibleModalPassword } footer = { null } onCancel = { this.hideModal } centered>
            <Row type = "flex" justify = "center">
              <Avatar size = {60} src = { this.state.customer.image } />
            </Row>
            <Paragraph style = {{ fontSize: 30, textAlign: 'center', marginBottom: 5 }}> Trocar Senhas </Paragraph>

            <Divider style = {{ minWidth: '60%', width: '60%', marginTop: 0, marginLeft: 'auto', marginRight: 'auto' }}>
              <Icon type = "lock" />
            </Divider>

            <Row style = {{ marginTop: 20 }} gutter = {16}>
              <Form onSubmit = { this.handlePasswords }>
                <Row gutter = {16}>
                  <Col span = {12}>
                    <Form.Item label = "Senha">
                      { getFieldDecorator('password', {
                        rules: [
                          { required: true, whitespace: true, message: 'Por favor, insira uma nova senha!' },
                          { min: 6, message: 'Por favor, insira uma senha maior que 5!' },
                          { validator: this.validateToNextPassword }
                        ]
                      })(
                        <Input.Password
                          prefix = { <Icon type = "lock" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                          placeholder = "Nova Senha"
                        />
                      )}
                    </Form.Item>
                  </Col>

                  <Col span = {12}>
                    <Form.Item label = "Confirmar Senha" hasFeedback validateStatus = { getFieldError('confirmPassword') ? 'error' : getFieldValue('confirmPassword') ? 'success' : '' }>
                      { getFieldDecorator('confirmPassword', {
                        rules: [
                          { required: true, whitespace: true, message: 'Por favor, insira uma senha!' },
                          { min: 6, message: 'Por favor, insira uma senha maior que 5!' },
                          { validator: this.compareToFirstPassword }
                        ]
                      })(
                        <Input
                          type = "password"
                          prefix = { <Icon type = "lock" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                          placeholder = "Repita a Nova Senha"
                        />
                      )}
                    </Form.Item>
                  </Col>
                </Row>

                <Row style = {{ textAlign: 'right' }}>
                  <Button size = "default" onClick = { this.hideModal } style = {{ marginRight: 8 }}> Cancelar </Button>
                  <Button loading = { this.state.passwordsButtonLoading } type = "primary" htmlType = "submit" size = "default"> Cadastrar Nova Senha </Button>
                </Row>
              </Form>
            </Row>
          </Modal>

          <Modal visible = { this.state.visibleModalProfile } footer = { null } onCancel = { this.hideModal } centered>
            <Row type = "flex" justify = "center">
              <Avatar size = {60} src = { this.state.customer.image } />
            </Row>
            <Paragraph style = {{ fontSize: 30, textAlign: 'center', marginBottom: 5 }}> Outras Informações </Paragraph>

            <Divider style = {{ minWidth: '60%', width: '60%', marginTop: 0, marginLeft: 'auto', marginRight: 'auto' }}>
              <Icon type = "crown" />
            </Divider>

            <Row style = {{ padding: 20, paddingBottom: 0 }} gutter = {16}>
              <Title level = {4} style = {{ fontWeight: 500 }}> Informações Básicas </Title>
              <Paragraph style = {{ marginLeft: 20, marginBottom: 3 }}>
                <Text strong> Documento: </Text>
                <Text copyable>{this.state.customer.document}</Text>
              </Paragraph>

              <Paragraph style = {{ marginLeft: 20, marginBottom: 3 }}>
                <Text strong> Username: </Text>
                <Text>@{this.state.customer.username}</Text>
              </Paragraph>


              <Title level = {4} style = {{ fontWeight: 500 }}> Contato </Title>
              <Paragraph style = {{ marginLeft: 20, marginBottom: 3 }}>
                <Text strong> Celular: </Text>
                <Text copyable>{this.state.customer.cellphone}</Text>
              </Paragraph>

              <Paragraph style = {{ marginLeft: 20, marginBottom: 3 }}>
                <Text strong> Email: </Text>
                <Text copyable>{this.state.customer.email}</Text>
              </Paragraph>

              <Paragraph style = {{ marginLeft: 20, marginBottom: 3 }}>
                <Text strong> Endereço: </Text>
                <Text>{ this.state.customer ? `${this.state.customer.location.address}, ${this.state.customer.location.number} - ${this.state.customer.location.neighborhood}, ${this.state.customer.location.city} - ${this.state.customer.location.uf}, ${this.state.customer.location.cep}. ${this.state.customer.location.complement ? `(${this.state.customer.location.complement})` : ''}` : ''}</Text>
                <a href = { this.state.customer ? `https://www.google.com/maps/search/?api=1&query=${`${this.state.customer.location.address}, ${this.state.customer.location.number} - ${this.state.customer.location.neighborhood}, ${this.state.customer.location.city} - ${this.state.customer.location.uf}, ${this.state.customer.location.cep}`}` : null} target = "_blank" rel = "noopener noreferrer">
                  <Popover content = "Ver Mapa Online"> <Icon type = "link" /> </Popover>
                </a>
              </Paragraph>


              <Title level = {4} style = {{ fontWeight: 500 }}> Projetos </Title>
              { this.state.customer && this.state.customer.projects.length > 0 ? (
                this.state.customer.projects.map(item => (
                  <Popover
                    key = { item._id }
                    overlayStyle = {{ width: 300 }}
                    title = { <Text> <Icon type = { projectStatus[item.status].icon } /> &nbsp; { projectStatus[item.status].title } </Text> }
                    content = {(
                      <div>
                        <Paragraph type = "secondary" style = {{ textAlign: 'center' }}> { projectStatus[item.status].label } </Paragraph>

                        <Paragraph style = {{ marginLeft: 20, marginBottom: 3 }}>
                          <Text strong> Preço: </Text>
                          <Text> R$ {item.cost.toString().replace(/[^\d]/g, '').replace(/\B(?=(\d{3})+(?!\d))/g, '.')},00 </Text>
                        </Paragraph>

                        { item.labels.length > 0 ? (
                          <Paragraph style = {{ marginLeft: 20, marginBottom: 3 }}>
                            <Text strong> Labels: </Text>
                            { item.labels.map(label => (
                              <Tag style = {{ marginBottom: 3, marginRight: 2, fontWeight: 600 }} key = { label } color = { projectLabels[label].color }> { projectLabels[label].title } </Tag>
                            )) }
                          </Paragraph>
                        ) : null }
                      </div>
                    )}
                  >
                    <Text style = {{ cursor: 'pointer' }} onClick = { () => this.setState({ nav: `/insideProject/${item._id}` }) }>
                      <Avatar size = {5} style = {{ backgroundColor: projectStatus[item.status].color, verticalAlign: 'text-top', marginLeft: 20 }} />
                      <Text> {item.name} </Text><br />
                    </Text>
                  </Popover>
                ))
              ) : (<Paragraph> Este cliente ainda não tem nenhum projeto <span role = "img" aria-label = "Triste">😥</span>. </Paragraph>) }
            </Row>
          </Modal>
        </Spin>
      </MainLayout>
    );
  }
}

export default Form.create()(CustomerMainPage);
