// login.js

import React, { Component } from 'react';
import { Row, Col, Typography, Button, Modal, Icon, Avatar, Form, Input, notification } from 'antd';
import { Redirect } from 'react-router-dom';

import axios from 'axios';

import { login, getToken, getStatus } from '../../services/auth';

import './login.css';

const logo = require('../../images/logo.png');

const { Title, Text } = Typography;

class Login extends Component {
  constructor() {
    super();

    this.state = {
      nav: '',
      mainVisible: true,
      loginVisible: false,
      status: '', // -1: Customer Login; 1: Member Login;
      loading: false
    };
  }

  componentDidMount() {
    if (getToken()) {
      if (getStatus() === '-1') this.setState({ nav: '/projects' });
      else this.setState({ nav: '/home' });
    }
  }

  showMainModal = () => {
    this.setState({
      mainVisible: true,
      loginVisible: false
    });
    this.props.form.resetFields();
  }

  showModalLogin = (status) => {
    this.setState({
      loginVisible: true,
      mainVisible: false,
      status
    });
  }

  handleSubmitLogin = (e) => {
    e.preventDefault();

    this.setState({ loading: true });

    this.props.form.validateFields((err, values) => {
      if (!err) {
        const { param, password } = values;

        if (this.state.status === 1) {
          axios.post('/api/members/login', { param, password }).then(res => {
            login(res.data.token, res.data.user.status, res.data.user.name, res.data.user.id);

            notification.success({
              message: `Bem Vindo, ${res.data.user.name}`,
              description: 'AEE! HACCOON está de portas abertas.'
            });

            this.setState({ nav: '/home' });
          }).catch(ex => {
            this.setState({ loading: false });
            // console.log('error: ', ex);

            if (ex.response.status === 401) {
              notification.error({
                message: 'Errooouu!',
                description: 'Tem problema aqui, ó: Credenciais Erradas Chapa.'
              });
            } else {
              notification.error({
                message: 'Errooouu!',
                description: 'Tem problema aqui, ó: Não sabemos ao certo, tente novamente, se persistirem os sintomas o médico deverá ser consultado, ops, o técnico.'
              });
            }
          });
        } else {
          axios.post('/api/customers/login', {
            param, password
          }).then(res => {
            login(res.data.token, res.data.user.status, res.data.user.name, res.data.user.id);

            notification.success({
              message: `Bem Vindo, ${res.data.user.name}`,
              description: 'Seu login foi aprovado, bem-vindo!'
            });

            this.setState({ nav: '/projects' });
          }).catch(ex => {
            this.setState({ loading: false });
            // console.log('error: ', ex);

            if (ex.response.status === 401) {
              notification.error({
                message: 'Opa!',
                description: 'Houve um problema aqui: Credenciais Erradas.'
              });
            } else {
              notification.error({
                message: 'Opa!',
                description: 'Houve um problema aqui: Não sabemos ao certo, em caso de dúvidas, contate o suporte técnico.'
              });
            }
          });
        }
      } else {
        this.setState({ loading: false });
      }
    });
  }

  render() {
    if (this.state.nav) {
      return (<Redirect to = { this.state.nav } />);
    }

    const { getFieldDecorator } = this.props.form;

    return (
      <Row>
        <Modal mask = {false} closable = {false} footer = {null} visible = { this.state.mainVisible } centered>
          <Row style = {{ textAlign: 'center' }}>
            <img style = {{ height: 60, cursor: 'pointer' }} src = {logo} alt = "Logo HACCOON" />
          </Row>

          <Row style = {{ textAlign: 'center', marginTop: 8 }}>
            <Text> Bem vindo ao sistema HACCOON, escolha uma das opções abaixo para fazer seu login e entrar no sistema. </Text>
          </Row>

          <Row style = {{ marginTop: 25, textAlign: 'center' }}>
            <Col style = {{ padding: 10, borderRight: '1px solid #E0E0DF' }} span = {12}>
              <Avatar style = {{ color: '#3867D6', backgroundColor: '#3867D638' }}>
                <Icon type = "crown" />
              </Avatar>

              <Title style = {{ marginBottom: 0, marginTop: 10 }} level = {3}> Clientes </Title>
              <Text type = "secondary"> Para clientes que fazem parte da história da Haken. </Text>

              <Button onClick = { () => this.showModalLogin(-1) } type = "primary" style = {{ marginTop: 10, width: '100%', backgroundColor: '#3867D6', borderColor: '#3867D6' }}>
                <Icon style = {{ fontSize: 10, verticalAlign: 'middle' }} type = "left" /> Fazer Login
              </Button>
            </Col>

            <Col style = {{ padding: 10 }} span = {12}>
              <Avatar style = {{ color: '#20BF6B', backgroundColor: '#20BF6B38' }}>
                <Icon type = "team" />
              </Avatar>

              <Title style = {{ marginBottom: 0, marginTop: 10 }} level = {3}> Equipe </Title>
              <Text type = "secondary"> Para toda família, nossos membros, trainees e diretores. </Text>

              <Button onClick = { () => this.showModalLogin(1) } type = "primary" style = {{ marginTop: 10, width: '100%', backgroundColor: '#20BF6B', borderColor: '#20BF6B' }}>
                Fazer Login <Icon style = {{ fontSize: 10, verticalAlign: 'middle' }} type = "right" />
              </Button>
            </Col>
          </Row>
        </Modal>

        <Modal mask = {false} closable = {false} footer = {null} visible = { this.state.loginVisible } centered>
          <Form onSubmit = { this.handleSubmitLogin }>
            <Row style = {{ marginBottom: 30 }}>
              <Button className = { this.state.status === 1 ? 'back-button-member' : 'back-button-customer' } onClick = { !this.state.loading ? this.showMainModal : null } style = {{ fontSize: 20, color: 'rgba(0, 0, 0, 0.25)', padding: 0, borderWidth: 0 }}>
                <Icon type = "arrow-left" />
              </Button>
            </Row>

            <Form.Item>
              { getFieldDecorator('param', this.state.status === 1 ? {
                rules: [{ required: true, message: 'Por favor, insira um R.A ou Username!' }]
              } : {
                rules: [{ required: true, message: 'Por favor, insira um Username ou Email!' }]
              })(
                <Input
                  prefix = { <Icon type = "idcard" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                  placeholder = { this.state.status === 1 ? 'R.A ou Username' : 'Username ou Email' }
                />
              )}
            </Form.Item>

            <Form.Item>
              { getFieldDecorator('password', {
                rules: [
                  { required: true, message: 'Por favor, insira uma senha!' },
                  { min: 6, message: 'Por favor, insira uma senha maior que 5!' }
                ]
              })(
                <Input.Password
                  prefix = { <Icon type = "lock" style = {{ color: 'rgba(0, 0, 0, .25)' }} /> }
                  placeholder = "Senha"
                />
              )}
            </Form.Item>

            <Form.Item style = {{ marginBottom: 0 }}>
              <Button loading = { this.state.loading } icon = "login" type = "primary" htmlType = "submit" style = {{ width: '100%', backgroundColor: `${this.state.status === 1 ? '#20BF6B' : '#3867D6'}`, borderColor: `${this.state.status === 1 ? '#20BF6B' : '#3867D6'}` }}>
                Entrar
              </Button>
            </Form.Item>
          </Form>
        </Modal>
      </Row>
    );
  }
}

export default Form.create()(Login);
