import React, { Component } from 'react';
import { BrowserRouter, Switch, Route } from 'react-router-dom';
import { ConfigProvider } from 'antd';

import ptBR from 'antd/lib/locale-provider/pt_BR';
import moment from 'moment';

import * as serviceWorker from './serviceWorker';

import GihtubConnection from './pages/githubConnection/githubConnection';

import Gamification from './pages/gamification/gamification';

import Projects from './pages/project/projects';
import InsideProject from './pages/project/insideProject';
import editProject from './pages/project/editProject';
import NewProject from './pages/project/newProject';

import File from './pages/file/file';

import Calendar from './pages/calendar/calendar';

import Goals from './pages/goals/goals';

import Member from './pages/member/member';
import MemberSignUp from './pages/signUp/memberSignUp';
import Profile from './pages/profile/profile';

import Customer from './pages/customer/customer';
import CustomerSignUp from './pages/signUp/customerSignUp';

import EditProfile from './pages/profile/editProfile';
import Login from './pages/login/login';

import Home from './pages/home';
import GenericErrorPage from './pages/errors/genericErrorPage';

import 'moment/locale/pt-br';
import './App.css';

moment.locale('pt-BR');

class App extends Component {
  render() {
    return (
      <ConfigProvider locale = {ptBR}>
        <BrowserRouter>
          <Switch>
            <Route path = "/githubconnection" exact component = { GihtubConnection } />

            <Route path = "/gamification" exact component = { Gamification } />

            <Route path = "/insideProject/:id" exact component = { InsideProject } />
            <Route path = "/editProject/:id" exact component = { editProject } />
            <Route path = "/projects" exact component = { Projects } />
            <Route path = "/newProject" exact component = { NewProject } />

            <Route path = "/files" exact component = { File } />
            <Route path = "/files/:id" exact component = { File } />

            <Route path = "/calendar" exact component = { Calendar } />

            <Route path = "/goals" exact component = { Goals } />

            <Route path = "/members" exact component = { Member } />
            <Route path = "/memberSignUp" exact component = { MemberSignUp } />
            <Route path = "/profile/:username" exact component = { Profile } />

            <Route path = "/customerSignUp" exact component = { CustomerSignUp } />
            <Route path = "/customers" exact component = { Customer } />

            <Route path = "/edit/profile" exact component = { EditProfile } />
            <Route path = "/" exact component = { Login } />

            <Route path = "/home" exact component = { Home } />

            <Route exact path = "/notAuthorizate" render = { () => <GenericErrorPage error = {500} /> } />
            <Route exact path = "/notFound" render = { () => <GenericErrorPage error = {404} /> } />
            <Route exact path = "*" render = { () => <GenericErrorPage error = {404} /> } />
          </Switch>
        </BrowserRouter>
      </ConfigProvider>
    );
  }
}

serviceWorker.unregister();

export default App;
