// home.js

import React, { Component } from 'react';
import { Button, Card, Carousel, Row, Col, Typography, Divider, Popover, Icon, Tag, Avatar, List } from 'antd';
import { Redirect, Link } from 'react-router-dom';

import axios from 'axios';
import moment from 'moment';

import { getStatus, getID } from '../services/auth';
import MainLayout from '../components/layout/layout';

import './home.css';

const { Title, Text, Paragraph } = Typography;

const podium = require('../images/podium.png');

// const { TabPane } = Tabs;

const projectLabels = [
  { color: '#F7B731', title: 'Consultoria', icon: 'book' },
  { color: '#FED330', title: 'Site', icon: 'desktop' },
  { color: '#26DE81', title: 'Aplicativo', icon: 'mobile' },
  { color: '#0FB9B1', title: 'Sistema Web', icon: 'ie' },
  { color: '#2BCBBA', title: 'Sistema Interno', icon: 'home' }
];

const projectStatus = [
  { color: '#3867D6', icon: 'deployment-unit', title: 'Planejamento', label: 'Começando o sofrimento denovo.' },
  { color: '#FA8231', icon: 'rocket', title: 'Inicializando', label: 'Fechando contrato e preparando as almas de membros para o abate.' },
  { color: '#45AAF2', icon: 'coffee', title: 'Em Andamento', label: 'Abastecendo os membros com café para manter o progresso.' },
  { color: '#778CA3', icon: 'frown', title: 'Cancelado', label: 'Cliente deu pra trás.' },
  { color: '#EB3B5A', icon: 'stop', title: 'Congelado', label: 'Sei lá, deu uma parada por agora.' },
  { color: '#20BF6B', icon: 'like', title: 'Acabou', label: 'Acaboooouuuu!!' }
];

const eventsLabel = [
  { name: 'FEJEPAR', color: '#FC5C65' },
  { name: 'Reunião DIREX', color: '#FA8231' },
  { name: 'Reunião Geral', color: '#FD9644' },
  { name: 'Reunião com Cliente', color: '#F7B731' },
  { name: 'Levantamento de Requisitos', color: '#FED330' },
  { name: 'Planning Poker', color: '#26DE81' },
  { name: 'Processo Seletivo', color: '#0FB9B1' },
  { name: 'Reunião do Conselho', color: '#2D98DA' },
  { name: 'Reunião de Projeto', color: '#4B7BEC' },
  { name: 'Outros', color: '#A55EEA' }
];

export default class Home extends Component {
  constructor(props) {
    super(props);

    this.state = {
      // nav: '',
      projects: [],
      events: [],
      filteredMembers: []
    };
  }

  componentDidMount() {
    this.loadProjects();
    this.loadEvents();
  }

  loadEvents = () => {
    axios.get('/api/calendar').then(res => {
      axios.get('/api/members').then(resCalendar => {
        const members = resCalendar.data;

        this.setState({ members, events: res.data });
        this.formatEvents();
      }).catch(() => {
        // console.log(errCalendar);
      });
      // console.log(res.data);
    }).catch(() => {
      // console.log(err);
    });
  }

  loadProjects = () => {
    axios.get('/api/projects').then(resProjects => {
      axios.get('/api/members').then(resMembers => {
        axios.get('/api/customers').then(resCustomers => {
          const customers = resCustomers.data;
          const projects = [...new Set(resProjects.data.filter(e => e.leader[0] === getID()).concat(resProjects.data.filter(e => e.members[0].includes(getID()))))];

          projects.forEach((item, index) => {
            projects[index].leader = resMembers.data[resMembers.data.map(e => e._id).indexOf(item.leader[0])];
            projects[index].customer = customers[customers.map(e => e._id).indexOf(item.customer[0])];

            const member = item.members[0];
            if (item.members[0].length) {
              item.members[0].forEach((itemMember, indexMember) => {
                member[indexMember] = resMembers.data[resMembers.data.map(e => e._id).indexOf(itemMember)];
              });
              projects[index].members = member;
            } else {
              projects[index].members = [member];
            }
          });
          const filteredMembers = [];

          resMembers.data.forEach(membro => {
            if (membro.points.length !== 0) {
              let qtdPonto = 0;
              const newMember = {};
              membro.points.forEach(ponto => {
                if (moment().isSame(moment(ponto.year), 'year')) {
                  qtdPonto += parseInt(ponto.point, 10);
                }
              });
              newMember.image = membro.image;
              newMember.name = membro.name;
              newMember.username = membro.username;
              newMember.point = qtdPonto;
              filteredMembers.push(newMember);
            }
          });
          filteredMembers.sort((x, y) => x.point <= y.point);
          // console.log('membros filtrados: ', filteredMembers);
          this.setState({ projects, filteredMembers });
          // console.log('membros: ', this.state.filteredMembers);
        });
      });
    });
  }

  formatEvents() {
    const aux = this.state.events.filter(item => moment(item.initialDate).isSameOrAfter(moment().format()));
    this.setState({ events: aux });
  }

  render() {
    if (this.state.nav) return (<Redirect to = { this.state.nav } />);
    if (['-1'].includes(getStatus())) return (<Redirect to = "/notAuthorizate" />);

    return (
      <MainLayout page = "home">
        <Row>
          <Row style = {{ marginBottom: 15 }}>
            <Col>
              <Card bordered = {false}>
                <Row>
                  { this.state.events.length > 0 ?
                    (
                      <Carousel autoplay>
                        { this.state.events.map(item => (
                          <Row key = {item._id}>
                            <Title level = {4} style = {{ marginBottom: 0, paddingBottom: 6, borderBottom: '1px solid #E9E9E9', fontWeight: 500 }}>
                              <Popover placement = "topLeft" content = { eventsLabel[item.status].name } arrowPointAtCenter>
                                <Avatar size = {14} style = {{ backgroundColor: `${eventsLabel[item.status].color}`, marginBottom: 3, marginRight: 14 }} />
                              </Popover>
                              { item.name }
                            </Title>

                            <div style = {{ height: 10 }} />
                            <Col span = {16}>
                              <Text>
                                <Icon type = "calendar" style = {{ marginRight: 5 }} /> {(moment(item.finalDate).diff(moment(item.initialDate), 'days')) === 0 ? item.allDay ? moment(item.initialDate).format('dddd, D MMMM') : `${moment(item.initialDate).format('dddd, D MMMM')} ⠂${moment(item.initialDate).format('HH:mm')} - ${moment(item.finalDate).format('HH:mm')}` : item.allDay ? `${moment(item.initialDate).format('D')} - ${moment(item.finalDate).format('D')} de ${moment(item.finalDate).format('MMMM')} de ${moment(item.finalDate).format('YYYY')}` : `${moment(item.initialDate).format('D MMMM YYYY, HH:mm')} - ${moment(item.finalDate).format('D MMMM YYYY, HH:mm')}`}
                              </Text>

                              <div style = {{ height: 10 }} />

                              <Text>
                                { item.description ? <Icon type = "align-left" style = {{ marginRight: 5 }} /> : null} {item.description}
                              </Text>

                              <div style = {{ height: 10 }} />

                              <Text>
                                { item.membersInvited.length > 0 && item.membersInvited[0].length > 0 ? (
                                  <div>
                                    <Icon type = "user" style = {{ marginRight: 5 }} />
                                    { item.membersInvited.length } membros convidados
                                  </div>
                                ) : null }
                              </Text>

                              <div style = {{ height: 5 }} />

                              { item.membersInvited.length > 0 && item.membersInvited[0].length > 0 ? (
                                item.membersInvited.map(memberID => {
                                  const id = memberID[0];
                                  const member = this.state.members.map(e => e._id).indexOf(id);
                                  const memberParticipation = item.membersPresent.map(e => e[0]).includes(id);

                                  return (
                                    member > -1 ? (
                                      <Paragraph key = {id} style = {{ marginBottom: 5 }}>
                                        <Avatar style = {{ height: 24, width: 24, marginRight: 5, marginLeft: 20 }} src = { this.state.members[member].image } />
                                        { memberParticipation ? <Avatar size = {16} style = {{ backgroundColor: '#87d068', marginLeft: -12, verticalAlign: 'bottom', marginBottom: -2 }} icon = "check" /> : null }
                                        <Text style = {{ marginLeft: 2 }}> { this.state.members[member].name } </Text>
                                      </Paragraph>
                                    ) : null
                                  );
                                })
                              ) : null }

                              <div style = {{ height: 5 }} />
                            </Col>
                            <Col span = {8}>
                              <Carousel>
                                { item.photos && item.photos.length > 0 ? (
                                  item.photos.map(photo => (
                                    <div key = {photo.url} style = {{ width: '100% !important', height: '200px !important', overflow: 'hidden !important' }}>
                                      <img style = {{ width: '100%', height: 200, objectFit: 'cover' }} alt = "Imagem do Evento" src = { photo.url } />
                                    </div>
                                  ))
                                ) : null }
                              </Carousel>
                            </Col>
                          </Row>
                        ))
                        }
                      </Carousel>
                    ) : null
                  }
                </Row>
              </Card>
            </Col>
          </Row>
          <Row style = {{ marginBottom: 15 }}>
            <Col>
              <Card bordered = {false}>
                <Row>
                  <Carousel>
                    { this.state.projects.map(item => (
                      getStatus() !== '-1' || (item.customer && item.customer._id === getID()) ? (
                        <Card bordered = {false} key = { item._id } hoverable bodyStyle = {{ cursor: 'auto', paddingBottom: 6 }} style = {{ marginTop: 30 }}>
                          <Col span = {8}>
                            <img alt = "Imagem do Projeto" style = {{ width: '100%', maxHeight: 200, objectFit: 'cover' }} src = {item.image} />
                          </Col>

                          <Col span = {16}>
                            <Row style = {{ marginBottom: 15 }}>
                              <Title level = {3} style = {{ marginBottom: 0 }} ellipsis>
                                { item.name } &nbsp;
                                <Popover
                                  overlayStyle = {{ width: 300 }}
                                  title = { <Text> <Icon type = { projectStatus[item.status].icon } /> &nbsp; { projectStatus[item.status].title } </Text>}
                                  content = {<Paragraph style = {{ marginBottom: 0 }}> { projectStatus[item.status].label } </Paragraph>}
                                >
                                  <Tag style = {{ verticalAlign: 3, fontWeight: 600, cursor: 'pointer' }} color = { projectStatus[item.status].color }> { projectStatus[item.status].title } </Tag>
                                </Popover>

                                <Button type = "primary" onClick = { () => this.setState({ nav: `/insideProject/${item._id}` }) } icon = "eye" style = {{ position: 'absolute', right: 0 }} size = "small" />
                              </Title>
                            </Row>

                            <Row style = {{ marginBottom: 15, minHeight: 120 }}>
                              { item.customer ? (
                                <Col span = {12} style = {{ margin: 'auto' }}>
                                  <Paragraph style = {{ marginBottom: 10 }}>
                                    <Text strong> Cliente: </Text>
                                    <Text> { item.customer.name } </Text>
                                  </Paragraph>
                                </Col>
                              ) : null }

                              <Col span = {12} style = {{ margin: 'auto' }}>
                                <Paragraph style = {{ marginBottom: 10 }}>
                                  <Text strong> Preço: </Text>
                                  <Text> R$ {item.cost.toString().replace(/[^\d]/g, '').replace(/\B(?=(\d{3})+(?!\d))/g, '.')},00 </Text>
                                </Paragraph>
                              </Col>

                              { item.labels.length > 0 ? (
                                <Col span = {12} style = {{ margin: 'auto' }}>
                                  <Paragraph style = {{ marginBottom: 10 }}>
                                    <Text strong> Rótulos: </Text>
                                    { item.labels.map(label => (
                                      <Popover key = { label } placement = "top" content = { projectLabels[label].title }>
                                        <Tag style = {{ marginBottom: 3, marginRight: 2, fontWeight: 600 }} key = { label } color = { projectLabels[label].color }> <Icon type = { projectLabels[label].icon } /> </Tag>
                                      </Popover>
                                    )) }
                                  </Paragraph>
                                </Col>
                              ) : null }

                              { item.description ? (
                                <Col span = {24} style = {{ margin: 'auto' }}>
                                  <Paragraph ellipsis style = {{ marginBottom: 10 }}>
                                    <Text strong> Descrição: </Text>
                                    <Text> { item.description } </Text>
                                  </Paragraph>
                                </Col>
                              ) : null }
                            </Row>
                          </Col>
                        </Card>
                      ) : <span />
                    ))}
                  </Carousel>
                </Row>
              </Card>
            </Col>
          </Row>
          <Row gutter = {16} style = {{ marginBottom: 15 }}>
            <Col span = {11}>
              <Card bordered = {false} style = {{ height: 300 }}>
                <Row>
                  <Title level = {3}>Vencedor Gamification</Title>
                  <List
                    style = {{ marginBottom: 15, minHeight: 120 }}
                    grid = {{ gutter: 16, column: 2 }}
                    size = "small"
                    dataSource = { this.state.filteredMembers }
                    renderItem = { member => (
                      <Col key = { member._id } span = {12} style = {{ paddingLeft: 20 }}>
                        <Popover title = {member.name} content = {(<Text>{member.point}</Text>)}>
                          <Link to = {`/profile/${member.username}`}>
                            <Avatar size = {30} style = {{ marginBottom: 5 }} src = { member.image } />
                            <Text style = {{ marginLeft: 11 }} ellipsis>{member.name}</Text>
                          </Link>
                        </Popover>
                      </Col>
                    )}
                  />
                </Row>
              </Card>
            </Col>
            <Col span = {1}>
              <Divider type = "vertical" style = {{ height: 300 }} />
            </Col>
            <Col span = {11}>
              <Card bordered = {false} style = {{ height: 300 }}>
                <Row>
                  <Title level = {3}>Gamification Podium</Title>
                  {this.state.filteredMembers[0] !== undefined ? (
                    <Row style = {{ marginBottom: 200 }}>
                      <Col style = {{ marginTop: -80 }}>
                        <Link to = {`/profile/${this.state.filteredMembers[2].username}`}>
                          <Avatar size = {50} style = {{ position: 'relative', zIndex: 10, marginBottom: -280, marginLeft: 95 }} src = {this.state.filteredMembers[2].image} alt = "HACCOON's podium" />
                        </Link>
                        <Link to = {`/profile/${this.state.filteredMembers[1].username}`}>
                          <Avatar size = {50} style = {{ position: 'relative', zIndex: 10, marginBottom: -275, marginLeft: 115 }} src = {this.state.filteredMembers[1].image} alt = "HACCOON's podium" />
                        </Link>
                        <Link to = {`/profile/${this.state.filteredMembers[0].username}`}>
                          {/* <Avatar style = {{ color: '#FED330', backgroundColor: 'white', position: 'relative', zIndex: 20 }} size = {20} icon = "crown" /> */}
                          <Avatar size = {50} style = {{ position: 'relative', zIndex: 10, marginBottom: -145, marginLeft: 175 }} src = {this.state.filteredMembers[0].image} alt = "HACCOON's podium" />
                        </Link>
                        <img style = {{ height: 300, zIndex: 1 }} src = {podium} alt = "HACCOON's podium" />
                      </Col>
                    </Row>
                  ) : <Text>Carregando</Text>
                  }
                </Row>
              </Card>
            </Col>
          </Row>

        </Row>
      </MainLayout>
    );
  }
}
