// newProject.js

import React, { Component } from 'react';
import { Row, Col, Form, Input, Button, Upload, Avatar, Typography, Divider, Spin, Select, notification, message } from 'antd';
import { Redirect } from 'react-router-dom';

import axios from 'axios';

import { getStatus } from '../../services/auth';

import MainLayout from '../../components/layout/layout';

const { Title } = Typography;
const { Option } = Select;

const usersStatus = [
  { color: '#FED330', icon: 'star', title: 'Presidente', label: 'Rei da Cocada Preta' },
  { color: '#26DE81', icon: 'star', title: 'Vice-Presidente/Diretor de RH', label: 'Manda só quando o presidente não está.' },
  { color: '#45AAF2', icon: 'crown', title: 'Diretor de Projetos', label: 'The Best' },
  { color: '#0FB9B1', icon: 'star', title: 'Diretor Administrativo-Financeiro', label: 'Contando Centavos...' },
  { color: '#A55EEA', icon: 'star', title: 'Diretor de Marketing', label: 'Criando umas Artes' },
  { color: '#FD9644', icon: 'thunderbolt', title: 'Membro', label: 'Abastecendo-se de Café' },
  { color: '#FC5C65', icon: 'fire', title: 'Trainee', label: 'Sofrendo' },
  { color: '#4B6584', icon: 'bulb', title: 'Ancião', label: 'Os Baqueados' }
];

const projectStatus = [
  { color: '#3867D6', icon: 'deployment-unit', title: 'Planejamento', label: 'Começando o sofrimento denovo.' },
  { color: '#FA8231', icon: 'rocket', title: 'Inicializando', label: 'Fechando contrato e preparando as almas de membros para o abate.' },
  { color: '#45AAF2', icon: 'coffee', title: 'Em Andamento', label: 'Abastecendo os membros com café para manter o progresso.' },
  { color: '#778CA3', icon: 'frown', title: 'Cancelado', label: 'Cliente deu pra trás.' },
  { color: '#EB3B5A', icon: 'stop', title: 'Congelado', label: 'Sei lá, deu uma parada por agora.' },
  { color: '#20BF6B', icon: 'like', title: 'Acabou', label: 'Acaboooouuuu!!' }
];

const projectLabels = [
  { color: '#F7B731', title: 'Consultoria', icon: 'book' },
  { color: '#FED330', title: 'Site', icon: 'desktop' },
  { color: '#26DE81', title: 'Aplicativo', icon: 'mobile' },
  { color: '#0FB9B1', title: 'Sistema Web', icon: 'ie' },
  { color: '#2BCBBA', title: 'Sistema Interno', icon: 'home' }
];

class NewProject extends Component {
  constructor(props) {
    super(props);

    this.state = {
      members: [],
      customers: [],
      projects: [],
      newProjectPhoto: [],
      projectPhoto: '',
      projectPhotoButtonLoading: false,
      loadingPage: true,
      buttonLoading: false,
      nav: ''
    };
  }

  componentDidMount() {
    axios.get('/api/members').then(resMembers => {
      const members = resMembers.data;

      members.sort((obj1, obj2) => (obj1.status < obj2.status ? -1 : (obj1.status < obj2.status ? 1 : 0)));

      axios.get('/api/customers').then(resCustomers => {
        const customers = resCustomers.data;

        customers.sort((obj1, obj2) => (obj1.status < obj2.status ? -1 : (obj1.status < obj2.status ? 1 : 0)));

        axios.get('/api/projects').then(resProjects => {
          const projects = resProjects.data;

          projects.sort((obj1, obj2) => (obj1.status < obj2.status ? -1 : (obj1.status < obj2.status ? 1 : 0)));

          this.setState({ projects, members, customers, loadingPage: false });
        }).catch(() => {
          // console.log(errProjects);
        });
      }).catch(() => {
        // console.log(errCustomers);
      });
    }).catch(() => {
      // console.log(errMembers);
    });
  }

  beforeUploadPhoto = (file) => {
    if (file.type === 'image/jpeg' || file.type === 'image/png') {
      const arquivos = [file];
      this.setState({ newProjectPhoto: arquivos });
      return true;
    }

    message.error('Extensão Inválida! Permitido apenas imagens');
    this.setState({ newProjectPhoto: [] });
    return false;
  }

  nameValidation = (rule, value, callback) => {
    const { projects } = this.state;

    if (projects[projects.map((e) => e.name).indexOf(value)]) {
      callback('Nome já Cadastrado!');
    } else {
      callback();
    }

    if (this.props.form.isFieldValidating('RA') === undefined) {
      this.props.form.validateFields(['RA']);
    }
  }

  changeProjectPhoto = () => {
    this.setState({ projectPhotoButtonLoading: true });

    const formData = new FormData();
    formData.append('api_key', '784737987534174');
    formData.append('timestamp', (Date.now() / 1000));
    formData.append('upload_preset', 'hatsxc2p');
    formData.append('file', this.state.newProjectPhoto[0]);

    axios.post('https://api.cloudinary.com/v1_1/haken/image/upload', formData, {
      headers: { 'X-Requested-With': 'XMLHttpRequest' }
    }).then(res => {
      this.setState({
        projectPhoto: res.data.secure_url,
        projectPhotoButtonLoading: false
      });
    }).catch(() => {
      // console.log(err);
      this.setState({
        newProjectPhoto: [],
        projectPhotoButtonLoading: false
      });
    });
  }

  filteredOptions = (value, option) => {
    if (!option.props.label && option.key.toLowerCase().includes(value.toLowerCase())) return true;

    return false;
  }

  handleSubmit = async (e) => {
    e.preventDefault();

    this.setState({ buttonLoading: true });

    this.props.form.validateFields(['name', 'leader', 'customer', 'status', 'labels', 'cost', 'embedded', 'members', 'description'], (err, values) => {
      if (!err) {
        const { name, leader, customer, status, labels, cost, embedded, members, description } = values;
        const { projectPhoto } = this.state;
        const image = projectPhoto || `https://api.adorable.io/avatars/285/${name}.png`;
        const currentFile = '5ceed7dc6ff597001754d28b';

        axios.post('/api/files', { name, father: currentFile, type: 1 }).then(resFile => {
          axios.post('/api/projects', { name, members: members || [], leader, labels, description, image, customer, status, files: resFile.data._id, embedded: embedded || [], cost: cost ? cost.replace(/[^\d]/g, '') : '0' }).then(() => {
            notification.success({
              message: 'Uhullll!',
              description: 'Informações Cadastradas!'
            });

            this.setState({ buttonLoading: false, nav: '/projects' });
          }).catch(() => {
            // console.log(error);
            this.setState({ buttonLoading: false });

            notification.error({
              message: 'Erroouuuuu!',
              description: 'Houve um problema aqui: Não conseguimos enviar dados no Servidor.'
            });
          });
        }).catch(() => {
          // console.log(ex);
          this.setState({ buttonLoading: false });

          notification.error({
            message: 'Errooouu!',
            description: 'Tem problema aqui, ó: Não conseguimos criar este arquivo/diretório.'
          });
        });
      } else {
        this.setState({ buttonLoading: false });
      }
    });
  }

  render() {
    if (this.state.nav) return (<Redirect to = { this.state.nav } />);
    if (['-1', '5', '6', '7'].includes(getStatus())) return (<Redirect to = "/notAuthorizate" />);

    const { getFieldDecorator, getFieldError, getFieldValue, resetFields } = this.props.form;

    return (
      <MainLayout page = "projects">
        <Spin spinning = { this.state.loadingPage }>
          <Row style = {{ marginBottom: 60 }}>
            <Col span = {16} offset = {4}>
              <Title id = "info" level = {2} style = {{ fontWeight: 500, marginBottom: 4 }}> Novo Projeto </Title>

              <Divider style = {{ marginTop: 0 }} />

              <Row gutter = {16}>
                <Col span = {8}>
                  <Avatar shape = "square" size = {200} src = { this.state.projectPhoto ? this.state.projectPhoto : `https://api.adorable.io/avatars/285/${getFieldValue('name')}.png` } />
                  <Upload beforeUpload = { this.beforeUploadPhoto } customRequest = { this.changeProjectPhoto } fileList = { this.state.newProjectPhoto } showUploadList = {false} accept = "image/*">
                    <Button loading = { this.state.projectPhotoButtonLoading } icon = "edit" type = "primary" style = {{ backgroundColor: '#383A3F', borderColor: '#383A3F', position: 'absolute', bottom: 5, left: 12 }}> Inserir Imagem </Button>
                  </Upload>
                </Col>

                <Col span = {14}>
                  <Form onSubmit = { this.handleSubmit }>
                    <Row gutter = {16}>
                      <Col span = {12}>
                        <Form.Item label = "Nome" hasFeedback validateStatus = { getFieldError('name') ? 'error' : getFieldValue('name') ? 'success' : '' }>
                          { getFieldDecorator('name', {
                            rules: [
                              { required: true, message: 'Por favor, insira um nome!' },
                              { validator: this.nameValidation }
                            ]
                          })(
                            <Input style = {{ width: '100%' }} placeholder = "Nome" />
                          )}
                        </Form.Item>
                      </Col>

                      <Col span = {12}>
                        <Form.Item label = "Líder" hasFeedback validateStatus = { getFieldError('leader') ? 'error' : getFieldValue('leader') ? 'success' : '' }>
                          { getFieldDecorator('leader')(
                            <Select allowClear onChange = { () => resetFields(['members']) } placeholder = "Líder" showSearch filterOption = { this.filteredOptions }>
                              <Select.OptGroup key = "Diretoria" label = "Diretoria">
                                { this.state.members.map(item => (
                                  item.status < 5 ? (
                                    <Select.Option key = { `${item.name} ${item.username}` } value = { item._id }>
                                      { item.name } ({ item.username })
                                    </Select.Option>
                                  ) : null
                                )) }
                              </Select.OptGroup>

                              { usersStatus.map((categoria, index) => (
                                index > 4 ? (
                                  <Select.OptGroup key = { categoria.title } label = { categoria.title }>
                                    { this.state.members.map(item => (
                                      index === item.status ? (
                                        <Select.Option key = { `${item.name} ${item.username}` } value = { item._id }>
                                          { item.name } ({ item.username })
                                        </Select.Option>
                                      ) : null
                                    )) }
                                  </Select.OptGroup>
                                ) : null
                              )) }
                            </Select>
                          )}
                        </Form.Item>
                      </Col>
                    </Row>

                    <Row gutter = {16}>
                      <Col span = {12}>
                        <Form.Item label = "Cliente" hasFeedback validateStatus = { getFieldError('customer') ? 'error' : getFieldValue('customer') ? 'success' : '' }>
                          { getFieldDecorator('customer')(
                            <Select disabled = { !getFieldValue('labels') || getFieldValue('labels').length === 0 || (getFieldValue('labels') && getFieldValue('labels').includes('4')) } placeholder = "Cliente do Projeto" showSearch filterOption = { this.filteredOptions }>
                              { this.state.customers ? this.state.customers.map(customer => (
                                <Option key = { customer.name } value = { customer._id }> { customer.name } </Option>
                              )) : null }
                            </Select>
                          )}
                        </Form.Item>
                      </Col>

                      <Col span = {12}>
                        <Form.Item label = "Status" hasFeedback validateStatus = { getFieldError('status') ? 'error' : getFieldValue('status') ? 'success' : '' }>
                          { getFieldDecorator('status', {
                            rules: [{ required: true, message: 'Por favor, insira um status!' }]
                          })(
                            <Select placeholder = "Status" showSearch filterOption = { this.filteredOptions }>
                              { projectStatus.map((status, index) => (
                                index < 3 ? (
                                  <Option key = { status.title } value = { index.toString() }> { status.title } </Option>
                                ) : null
                              )) }
                            </Select>
                          )}
                        </Form.Item>
                      </Col>
                    </Row>

                    <Form.Item label = "Labels" hasFeedback validateStatus = { getFieldError('labels') ? 'error' : getFieldValue('labels') ? 'success' : '' }>
                      { getFieldDecorator('labels', {
                        rules: [{ required: true, message: 'Por favor, adicione labels!' }]
                      })(
                        <Select mode = "multiple" filterOption = { this.filteredOptions } onChange = { (labels) => (labels.includes('4') ? resetFields(['customer']) : null) } placeholder = "Labels">
                          { projectLabels.map((label, index) => (
                            <Option key = { label.title } value = { index.toString() }> { label.title } </Option>
                          )) }
                        </Select>
                      )}
                    </Form.Item>

                    <Form.Item label = "Custo">
                      { getFieldDecorator('cost', {
                        normalize: (value) => (value ? value.toString().replace(/[^\d]/g, '').replace(/\B(?=(\d{3})+(?!\d))/g, '.') : null)
                      })(
                        <Input addonBefore = "R$" style = {{ maxWidth: 250 }} addonAfter = ",00" placeholder = "Custo" type = "text" />
                      )}
                    </Form.Item>

                    <Form.Item label = "Projetos Relacionados" hasFeedback validateStatus = { getFieldError('embedded') ? 'error' : getFieldValue('embedded') ? 'success' : '' }>
                      { getFieldDecorator('embedded')(
                        <Select mode = "multiple" filterOption = { this.filteredOptions } placeholder = "Projetos Relacionados">
                          { this.state.projects ? this.state.projects.map(project => (
                            <Option key = { project.name } value = { project._id }> { project.name } </Option>
                          )) : null }
                        </Select>
                      )}
                    </Form.Item>

                    <Form.Item label = "Membros" hasFeedback validateStatus = { getFieldError('members') ? 'error' : getFieldValue('members') ? 'success' : '' }>
                      { getFieldDecorator('members')(
                        <Select disabled = { !getFieldValue('leader') || getFieldValue('leader').length === 0 } mode = "multiple" placeholder = "Membros do Projeto" filterOption = { this.filteredOptions }>
                          <Select.OptGroup key = "Diretoria" label = "Diretoria">
                            { this.state.members.map(item => (
                              item.status < 5 && item._id.toString() !== getFieldValue('leader') ? (
                                <Select.Option key = { `${item.name} ${item.username}` } value = { item._id }>
                                  { item.name } ({ item.username })
                                </Select.Option>
                              ) : null
                            )) }
                          </Select.OptGroup>

                          { usersStatus.map((categoria, index) => (
                            index > 4 ? (
                              <Select.OptGroup key = { categoria.title } label = { categoria.title }>
                                { this.state.members.map(item => (
                                  index === item.status && item._id.toString() !== getFieldValue('leader') ? (
                                    <Select.Option key = { `${item.name} ${item.username}` } value = { item._id }>
                                      { item.name } ({ item.username })
                                    </Select.Option>
                                  ) : null
                                )) }
                              </Select.OptGroup>
                            ) : null
                          )) }
                        </Select>
                      )}
                    </Form.Item>

                    <Form.Item label = "Descrição" hasFeedback validateStatus = { getFieldError('description') ? 'error' : getFieldValue('description') ? 'success' : '' }>
                      { getFieldDecorator('description', {
                        rules: [{ required: true, message: 'Por favor, insira uma descrição!' }]
                      })(
                        <Input.TextArea placeholder = "Descrição do Projeto" autosize = {{ minRows: 5, maxRows: 5 }} />
                      )}
                    </Form.Item>

                    <Row style = {{ textAlign: 'right' }}>
                      <Button type = "primary" htmlType = "submit" loading = { this.state.buttonLoading }> Cadastrar Projeto </Button>
                    </Row>
                  </Form>
                </Col>
              </Row>
            </Col>
          </Row>
        </Spin>
      </MainLayout>
    );
  }
}

export default Form.create()(NewProject);
