// editProfile.js

import React, { Component } from 'react';

import MainLayout from '../../components/layout/layout';

import EditProfileMember from './editProfileMember';
import EditProfileCustomer from './editProfileCustomer';

import { getStatus } from '../../services/auth';

import './profile.css';

export default class EditProfile extends Component {
  constructor(props) {
    super(props);

    this.state = {
      status: getStatus()
    };
  }

  render() {
    return (
      <MainLayout>
        { this.state.status !== '-1' ? (
          <EditProfileMember />
        ) : (
          <EditProfileCustomer />
        ) }
      </MainLayout>
    );
  }
}
