// memberSignUp.js

import React, { Component } from 'react';
import { Row, Col, Form, DatePicker, Input, Select, InputNumber, Button, Upload, Avatar, Typography, Divider, Icon, Steps, notification, message } from 'antd';
import { Redirect } from 'react-router-dom';

import axios from 'axios';

import { getStatus } from '../../services/auth';

import MainLayout from '../../components/layout/layout';

const { Step } = Steps;
const { Title } = Typography;
const { Option } = Select;

class memberSignUp extends Component {
  constructor(props) {
    super(props);

    this.state = {
      users: [],
      current: 0,
      RA: '',
      birth: null,
      RG: '',
      CPF: '',
      username: '',
      name: '',
      password: '',
      email: '',
      cellphone: '',
      status: '6',
      location: {
        cep: '',
        neighborhood: '',
        address: '',
        number: null,
        complement: '',
        city: '',
        uf: ''
      },
      hasAddress: true,
      hasNeighborhood: true,
      newProfilePhoto: [],
      profilePhoto: '',
      buttonloading: false,
      changeProfilePhotoButtonloading: false,
      nav: ''
    };
  }

  componentDidMount() {
    axios.get('/api/members').then(res => {
      this.setState({ users: res.data });
    });
  }

  RAValidation = (rule, value, callback) => {
    const { users } = this.state;
    const valueFormatted = value ? value.substring(0, 9).replace(/[^\d]/g, '') : '';

    this.props.form.setFieldsValue({ RA: valueFormatted });

    if (valueFormatted && valueFormatted.length >= 10) {
      callback('RA deve ter menos que 10 dígitos!');
    } else if (users[users.map((e) => e.RA).indexOf(valueFormatted)]) {
      callback('RA já Cadastrado!');
    } else {
      callback();
    }

    if (this.props.form.isFieldValidating('RA') === undefined) {
      this.props.form.validateFields(['RA']);
    }
  }

  RGValidation = (rule, value, callback) => {
    const valueFormatted = value ? value.substring(0, 14).toUpperCase() : '';
    this.props.form.setFieldsValue({ RG: valueFormatted });

    if (valueFormatted && valueFormatted.length >= 15) {
      callback('RG deve ter menos que 15 dígitos!');
    } else {
      callback();
    }

    if (this.props.form.isFieldValidating('RG') === undefined) {
      this.props.form.validateFields(['RG']);
    }
  }

  CPFValidation = (rule, value, callback) => {
    const valueFormatted = value ? value.substring(0, 14).replace(/[^\d]/g, '').replace(/^(\d{3})(\d{3})(\d{3})(\d)/, '$1.$2.$3-$4') : '';
    this.props.form.setFieldsValue({ CPF: valueFormatted });

    if (valueFormatted && valueFormatted.length !== 14) {
      callback('CPF deve ter 14 dígitos!');
    } else {
      callback();
    }

    if (this.props.form.isFieldValidating('CPF') === undefined) {
      this.props.form.validateFields(['CPF']);
    }
  }

  usernameValidation = (rule, value, callback) => {
    const { users } = this.state;

    this.props.form.setFieldsValue({ username: value.replace(/ /g, '') });

    if (users[users.map((e) => e.username).indexOf(value)]) {
      callback('Username já Cadastrado!');
    } else {
      callback();
    }

    if (this.props.form.isFieldValidating('username') === undefined) {
      this.props.form.validateFields(['username']);
    }
  }

  cellphoneValidation = (rule, value, callback) => {
    const valueFormatted = value ? value.substring(0, 16).replace(/[^\d]/g, '').replace(/^(\d{2})(\d{1})(\d{4})(\d)/, '($1) $2 $3-$4') : '';
    this.props.form.setFieldsValue({ cellphone: valueFormatted });

    if (valueFormatted && valueFormatted.toString().length !== 16) {
      callback('Telefone deve ter 16 dígitos!');
    } else {
      callback();
    }

    if (this.props.form.isFieldValidating('cellphone') === undefined) {
      this.props.form.validateFields(['cellphone']);
    }
  }

  cepValidation = async (rule, value, callback) => {
    const valueFormatted = value ? value.substring(0, 9).replace(/[^\d]/g, '').replace(/^(\d{5})(\d)/, '$1-$2') : '';
    this.props.form.setFieldsValue({ cep: valueFormatted });

    if (valueFormatted && valueFormatted.length === 9) {
      try {
        const res = await axios.get(`http://api.postmon.com.br/v1/cep/${valueFormatted}`);

        if (res.status === 200) {
          this.props.form.setFieldsValue({
            city: res.data.cidade,
            uf: res.data.estado
          });

          this.setState({ hasAddress: true, hasNeighborhood: true });

          if (!res.data.logradouro) {
            this.setState({ hasAddress: false });
          } else {
            this.props.form.setFieldsValue({
              address: res.data.logradouro
            });
          }

          if (!res.data.bairro) {
            this.setState({ hasNeighborhood: false });
          } else {
            this.props.form.setFieldsValue({
              neighborhood: res.data.bairro
            });
          }

          callback();
        }
      } catch (ex) {
        callback('CEP não Existe!');
      }
    } else if (valueFormatted && valueFormatted.length !== 9) {
      callback('CEP deve ter 9 dígitos!');
    } else {
      callback();
    }

    if (this.props.form.isFieldValidating('cep') === undefined) {
      this.props.form.validateFields(['cep']);
    }
  }

  compareToFirstPassword = (rule, value, callback) => {
    if (value && value !== this.props.form.getFieldValue('password')) {
      callback('Senhas Diferentes!');
    } else {
      callback();
    }
  }

  validateToNextPassword = (rule, value, callback) => {
    if (value && this.props.form.getFieldValue('confirmPassword')) {
      this.props.form.validateFields(['confirmPassword'], { force: true });
    }
    callback();
  }

  prevStep = () => {
    const current = this.state.current - 1;
    const location = {
      cep: this.props.form.getFieldValue('cep'),
      neighborhood: this.props.form.getFieldValue('neighborhood'),
      address: this.props.form.getFieldValue('address'),
      number: this.props.form.getFieldValue('number'),
      complement: this.props.form.getFieldValue('complement'),
      city: this.props.form.getFieldValue('city'),
      uf: this.props.form.getFieldValue('uf')
    };

    this.setState({
      current,
      status: this.props.form.getFieldValue('status'),
      email: this.props.form.getFieldValue('email'),
      cellphone: this.props.form.getFieldValue('cellphone'),
      location
    });
  }

  nextStep = () => {
    const current = this.state.current + 1;

    this.props.form.validateFields(['RA', 'birth', 'RG', 'CPF', 'username', 'name', 'password', 'confirmPassword'], (err, values) => {
      if (!err) {
        this.setState({
          current,
          RA: values.RA,
          birth: values.birth,
          RG: values.RG,
          CPF: values.CPF,
          username: values.username,
          name: values.name,
          password: values.password
        });
      }
    });
  }

  beforeUploadPhoto = (file) => {
    if (file.type === 'image/jpeg' || file.type === 'image/png') {
      const arquivos = [file];
      this.setState({ newProfilePhoto: arquivos });
      return true;
    }

    message.error('Extensão Inválida! Permitido apenas imagens');
    this.setState({ newProfilePhoto: [] });
    return false;
  }

  changeProfilePhoto = () => {
    this.setState({ changeProfilePhotoButtonloading: true });

    const formData = new FormData();
    formData.append('api_key', '784737987534174');
    formData.append('timestamp', (Date.now() / 1000));
    formData.append('upload_preset', 'hatsxc2p');
    formData.append('file', this.state.newProfilePhoto[0]);

    axios.post('https://api.cloudinary.com/v1_1/haken/image/upload', formData, {
      headers: { 'X-Requested-With': 'XMLHttpRequest' }
    }).then(res => {
      this.setState({
        profilePhoto: res.data.secure_url,
        changeProfilePhotoButtonloading: false
      });
    }).catch(() => {
      // console.log(err);
      this.setState({
        newProfilePhoto: [],
        changeProfilePhotoButtonloading: false
      });
    });
  }

  handleSubmit = () => {
    this.setState({ buttonloading: true });

    this.props.form.validateFields(['email', 'cellphone', 'status', 'cep', 'neighborhood', 'address', 'number', 'complement', 'city', 'uf'], (err, values) => {
      if (!err) {
        const { email, cellphone, status, cep, address, neighborhood, number, complement, city, uf } = values;
        const { RA, birth, RG, CPF, name, username, password, profilePhoto } = this.state;
        const location = { cep, address, neighborhood, number, complement, city, uf };
        const image = profilePhoto || `https://api.adorable.io/avatars/285/${username}.png`;

        axios.post('/api/members', { RA, birth, RG, CPF, username, name, cellphone, status: status.toString(), email, location, image, password }).then(() => {
          notification.success({
            message: 'Tudo Certo!',
            description: 'Informações Cadastradas!'
          });

          this.setState({
            buttonloading: false,
            nav: '/members'
          });
        }).catch(() => {
          // console.log(error);
          this.setState({ buttonloading: false });

          notification.error({
            message: 'Opa!',
            description: 'Houve um problema aqui: Não conseguimos enviar dados no Servidor.'
          });
        });
      } else {
        this.setState({ buttonloading: false });
      }
    });
  }

  render() {
    if (this.state.nav) return (<Redirect to = { this.state.nav } />);
    if (['-1', '5', '6', '7'].includes(getStatus())) return (<Redirect to = "/notAuthorizate" />);

    const { getFieldDecorator, getFieldError, getFieldValue } = this.props.form;

    return (
      <MainLayout page = "members">
        <Row gutter = {16} style = {{ marginBottom: 60 }}>
          <Col span = {16} offset = {4}>
            { this.state.current === 0 ? (
              <Row>
                <Title id = "info" level = {2} style = {{ fontWeight: 500, marginBottom: 4 }}> Dados Pessoais </Title>
                <Divider style = {{ marginTop: 0 }} />

                <Row gutter = {16}>
                  <Col span = {8}>
                    <Avatar shape = "square" size = {200} src = { this.state.profilePhoto ? this.state.profilePhoto : getFieldValue('username') ? `https://api.adorable.io/avatars/285/${getFieldValue('username')}.png` : `https://api.adorable.io/avatars/285/${this.state.username}.png` } />
                    <Upload beforeUpload = { this.beforeUploadPhoto } customRequest = { this.changeProfilePhoto } fileList = { this.state.newProfilePhoto } showUploadList = {false} accept = "image/*">
                      <Button loading = { this.state.changeProfilePhotoButtonloading } icon = "plus" type = "primary" style = {{ backgroundColor: '#383A3F', borderColor: '#383A3F', position: 'absolute', bottom: 5, left: 12 }}> Adicionar Imagem </Button>
                    </Upload>
                  </Col>

                  <Col span = {14}>
                    <Form onSubmit = { this.handleSubmitInfo }>
                      <Row gutter = {16}>
                        <Col span = {12}>
                          <Form.Item label = "RA" hasFeedback validateStatus = { getFieldError('RA') ? 'error' : getFieldValue('RA') ? 'success' : '' }>
                            { getFieldDecorator('RA', {
                              initialValue: this.state.RA,
                              rules: [
                                { required: true, message: 'Por favor, insira um RA!' },
                                { validator: this.RAValidation }
                              ]
                            })(
                              <Input style = {{ width: '100%' }} placeholder = "RA" />
                            )}
                          </Form.Item>
                        </Col>

                        <Col span = {12}>
                          <Form.Item label = "Data de Nascimento" hasFeedback validateStatus = { getFieldError('birth') ? 'error' : getFieldValue('birth') ? 'success' : '' }>
                            { getFieldDecorator('birth', {
                              initialValue: this.state.birth,
                              rules: [{ required: true, message: 'Por favor, insira uma data!' }]
                            })(
                              <DatePicker placeholder = "Data de Nascimento" format = "DD/MM/YYYY" />
                            )}
                          </Form.Item>
                        </Col>
                      </Row>

                      <Row gutter = {16}>
                        <Col span = {12}>
                          <Form.Item label = "RG" hasFeedback validateStatus = { getFieldError('RG') ? 'error' : getFieldValue('RG') ? 'success' : '' }>
                            { getFieldDecorator('RG', {
                              initialValue: this.state.RG,
                              rules: [
                                { required: true, message: 'Por favor, insira um RG!' },
                                { validator: this.RGValidation }
                              ]
                            })(
                              <Input style = {{ width: '100%' }} placeholder = "RG" />
                            )}
                          </Form.Item>
                        </Col>

                        <Col span = {12}>
                          <Form.Item label = "CPF" hasFeedback validateStatus = { getFieldError('CPF') ? 'error' : getFieldValue('CPF') ? 'success' : '' }>
                            { getFieldDecorator('CPF', {
                              initialValue: this.state.CPF,
                              rules: [
                                { required: true, message: 'Por favor, insira um CPF!' },
                                { validator: this.CPFValidation }
                              ]
                            })(
                              <Input style = {{ width: '100%' }} placeholder = "CPF" />
                            )}
                          </Form.Item>
                        </Col>
                      </Row>

                      <Form.Item label = "Username" hasFeedback validateStatus = { getFieldError('username') ? 'error' : getFieldValue('username') ? 'success' : '' }>
                        { getFieldDecorator('username', {
                          initialValue: this.state.username,
                          rules: [
                            { required: true, whitespace: true, message: 'Por favor, insira um username!' },
                            { validator: this.usernameValidation }
                          ]
                        })(
                          <Input
                            prefix = { <Icon type = "smile" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                            placeholder = "Username"
                          />
                        )}
                      </Form.Item>

                      <Form.Item label = "Nome" hasFeedback validateStatus = { getFieldError('name') ? 'error' : getFieldValue('name') ? 'success' : '' }>
                        { getFieldDecorator('name', {
                          initialValue: this.state.name,
                          rules: [{ required: true, whitespace: true, message: 'Por favor, insira um nome!' }]
                        })(
                          <Input
                            prefix = { <Icon type = "user" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                            placeholder = "Nome"
                          />
                        )}
                      </Form.Item>

                      <Row gutter = {16}>
                        <Col span = {12}>
                          <Form.Item label = "Senha">
                            { getFieldDecorator('password', {
                              initialValue: this.state.password,
                              rules: [
                                { required: true, whitespace: true, message: 'Por favor, insira uma senha!' },
                                { min: 6, message: 'Por favor, insira uma senha maior que 5!' },
                                { validator: this.validateToNextPassword }
                              ]
                            })(
                              <Input.Password
                                prefix = { <Icon type = "lock" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                                placeholder = "Senha"
                              />
                            )}
                          </Form.Item>
                        </Col>

                        <Col span = {12}>
                          <Form.Item label = "Confirmar Senha" hasFeedback validateStatus = { getFieldError('confirmPassword') ? 'error' : getFieldValue('confirmPassword') ? 'success' : '' }>
                            { getFieldDecorator('confirmPassword', {
                              initialValue: this.state.password,
                              rules: [
                                { required: true, whitespace: true, message: 'Por favor, insira uma senha!' },
                                { min: 6, message: 'Por favor, insira uma senha maior que 5!' },
                                { validator: this.compareToFirstPassword }
                              ]
                            })(
                              <Input
                                type = "password"
                                prefix = { <Icon type = "lock" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                                placeholder = "Repita a Senha"
                              />
                            )}
                          </Form.Item>
                        </Col>
                      </Row>
                    </Form>
                  </Col>
                </Row>
              </Row>
            ) : (
              <Row>
                <Title id = "contact-info" level = {2} style = {{ fontWeight: 500, marginBottom: 4 }}> Dados de Contato </Title>
                <Divider style = {{ marginTop: 0 }} />

                <Row gutter = {16}>
                  <Col span = {14} offset = {5}>
                    <Form onSubmit = { this.handleSubmitContactInfo }>
                      <Form.Item label = "Email" hasFeedback validateStatus = { getFieldError('email') ? 'error' : getFieldValue('email') ? 'success' : '' }>
                        { getFieldDecorator('email', {
                          initialValue: this.state.email,
                          rules: [
                            { required: true, whitespace: true, message: 'Por favor, insira um email!' },
                            { validator: this.emailValidation },
                            { type: 'email', message: 'Por favor, insira um email válido!' }
                          ]
                        })(
                          <Input
                            prefix = { <Icon type = "mail" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                            placeholder = "Email"
                          />
                        )}
                      </Form.Item>

                      <Row gutter = {16}>
                        <Col span = {12}>
                          <Form.Item label = "Telefone Celular" hasFeedback validateStatus = { getFieldError('cellphone') ? 'error' : getFieldValue('cellphone') ? 'success' : '' }>
                            { getFieldDecorator('cellphone', {
                              initialValue: this.state.cellphone,
                              rules: [
                                { required: true, message: 'Por favor, insira um celular!' },
                                { validator: this.cellphoneValidation }
                              ]
                            })(
                              <Input style = {{ width: '100%' }} placeholder = "Telefone Celular" />
                            )}
                          </Form.Item>
                        </Col>

                        <Col span = {12}>
                          <Form.Item label = "Status" hasFeedback validateStatus = { getFieldError('status') ? 'error' : getFieldValue('status') ? 'success' : '' }>
                            { getFieldDecorator('status', {
                              initialValue: this.state.status,
                              rules: [{ required: true, message: 'Por favor, insira um status!' }]
                            })(
                              <Select placeholder = "Selecione Status" showSearch filterOption = {(input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0 }>
                                <Option value = "5"> Membro </Option>
                                <Option value = "6"> Trainee </Option>
                              </Select>
                            )}
                          </Form.Item>
                        </Col>
                      </Row>

                      <Row gutter = {16}>
                        <Col span = {6}>
                          <Form.Item label = "CEP" hasFeedback validateStatus = { getFieldError('cep') ? 'error' : getFieldValue('cep') ? 'success' : '' }>
                            { getFieldDecorator('cep', {
                              initialValue: this.state.location.cep,
                              rules: [
                                { required: true, message: 'Por favor, insira um cep!' },
                                { validator: this.cepValidation }
                              ]
                            })(
                              <Input style = {{ width: '100%' }} placeholder = "CEP" />
                            )}
                          </Form.Item>
                        </Col>

                        <Col span = {18}>
                          <Form.Item label = "Bairro" hasFeedback validateStatus = { getFieldError('neighborhood') ? 'error' : getFieldValue('neighborhood') ? 'success' : '' }>
                            { getFieldDecorator('neighborhood', {
                              initialValue: this.state.location.neighborhood,
                              rules: [{ required: true, whitespace: true, message: 'Por favor, insira um bairro!' }]
                            })(
                              <Input
                                disabled = { this.state.hasNeighborhood }
                                prefix = { <Icon type = "home" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                                placeholder = "Bairro"
                              />
                            )}
                          </Form.Item>
                        </Col>
                      </Row>

                      <Row gutter = {16}>
                        <Col span = {18}>
                          <Form.Item label = "Endereço" hasFeedback validateStatus = { getFieldError('address') ? 'error' : getFieldValue('address') ? 'success' : '' }>
                            { getFieldDecorator('address', {
                              initialValue: this.state.location.address,
                              rules: [{ required: true, whitespace: true, message: 'Por favor, insira um endereço!' }]
                            })(
                              <Input
                                disabled = { this.state.hasAddress }
                                prefix = { <Icon type = "home" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                                placeholder = "Endereço"
                              />
                            )}
                          </Form.Item>
                        </Col>

                        <Col span = {6}>
                          <Form.Item label = "Número" hasFeedback validateStatus = { getFieldError('number') ? 'error' : getFieldValue('number') ? 'success' : '' }>
                            { getFieldDecorator('number', {
                              initialValue: this.state.location.number,
                              rules: [{ required: true, message: 'Por favor, insira um número!' }]
                            })(
                              <InputNumber
                                className = "no-number-options"
                                placeholder = "Número"
                              />
                            )}
                          </Form.Item>
                        </Col>
                      </Row>

                      <Row gutter = {16}>
                        <Col span = {9}>
                          <Form.Item label = "Complemento" hasFeedback validateStatus = { getFieldError('complement') ? 'error' : getFieldValue('complement') ? 'success' : '' }>
                            { getFieldDecorator('complement', {
                              initialValue: this.state.location.complement
                            })(
                              <Input
                                prefix = { <Icon type = "home" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                                placeholder = "Complemento"
                              />
                            )}
                          </Form.Item>
                        </Col>

                        <Col span = {10}>
                          <Form.Item label = "Cidade" hasFeedback validateStatus = { getFieldError('city') ? 'error' : getFieldValue('city') ? 'success' : '' }>
                            { getFieldDecorator('city', {
                              initialValue: this.state.location.city,
                              rules: [{ required: true, whitespace: true, message: 'Por favor, insira uma cidade!' }]
                            })(
                              <Input
                                disabled
                                prefix = { <Icon type = "home" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                                placeholder = "Cidade"
                              />
                            )}
                          </Form.Item>
                        </Col>

                        <Col span = {5}>
                          <Form.Item label = "Estado" hasFeedback validateStatus = { getFieldError('uf') ? 'error' : getFieldValue('uf') ? 'success' : '' }>
                            { getFieldDecorator('uf', {
                              initialValue: this.state.location.uf,
                              rules: [{ required: true, whitespace: true, message: 'Por favor, insira um estado!' }]
                            })(
                              <Input
                                disabled
                                prefix = { <Icon type = "home" style = {{ color: 'rgba(0, 0, 0, 0.25)' }} /> }
                                placeholder = "Estado"
                              />
                            )}
                          </Form.Item>
                        </Col>
                      </Row>
                    </Form>
                  </Col>
                </Row>
              </Row>
            ) }

            <Row>
              <Col span = {14} offset = {8} style = {{ textAlign: 'right' }}>
                { this.state.current < 1 ? (
                  <Button type = "primary" onClick = { () => this.nextStep() }>
                    Próximo <Icon type = "arrow-right" />
                  </Button>
                ) : null }
              </Col>
            </Row>

            <Row>
              <Col span = {7} offset = {5}>
                { this.state.current > 0 ? (
                  <Button icon = "arrow-left" onClick = { () => this.prevStep() }> Anterior </Button>
                ) : null }
              </Col>

              <Col span = {7} style = {{ textAlign: 'right' }}>
                { this.state.current === 1 ? (
                  <Button type = "primary" onClick = { () => this.handleSubmit() } loading = { this.state.buttonloading }> Cadastrar </Button>
                ) : null }
              </Col>
            </Row>
          </Col>

          <Col span = {4} style = {{ marginTop: 32 }}>
            <Steps direction = "vertical" current = { this.state.current } size = "small">
              <Step title = "Dados Pessoais" description = "Quem é este polvilho?" />
              <Step title = "Dados de Contato" description = "Como se comporta, onde vive, o que come." />
            </Steps>
          </Col>
        </Row>
      </MainLayout>
    );
  }
}

export default Form.create()(memberSignUp);
