// navbar.js

import React, { Component } from 'react';
import { Layout, Row, Col, Menu, Avatar, Dropdown, Icon, message } from 'antd';
import { Link, Redirect } from 'react-router-dom';

import axios from 'axios';

import { logout, getName, getStatus, getID } from '../../services/auth';

import './navbar.css';

const logo = require('../../images/logo.png');

const { Header } = Layout;

const CLIENT_ID = 'a909ba7f264d474e8f36';
const REDIRECT_URI = 'http://localhost:3000/githubconnection';

export default class NavBar extends Component {
  constructor(props) {
    super(props);

    this.state = {
      nav: '',
      user: {},
      name: getName(),
      status: getStatus(),
      modal: false
    };

    this.toggle = this.toggle.bind(this);
  }

  componentDidMount() {
    if (this.state.status === '-1') {
      axios.get(`/api/customers/id/${getID()}`).then(res => {
        this.setState({ user: res.data });
      });
    } else if (this.state.status) {
      axios.get(`/api/members/id/${getID()}`).then(res => {
        if (!res.data.github_code) window.location.href = `https://github.com/login/oauth/authorize?client_id=${CLIENT_ID}&scope=user&redirect_uri=${REDIRECT_URI}&allow_signup=true`;
        this.setState({ user: res.data });
      });
    }
  }

  signout = () => {
    const hide = message.loading('Bye Bye 👋', 0);

    setTimeout(() => {
      setTimeout(hide, 1500);

      logout();
      this.setState({ nav: '/' });
    }, 1500);
  }

  toggle() {
    this.setState({
      modal: !this.state.modal
    });
  }

  render() {
    if (this.state.nav) {
      return (<Redirect to = { this.state.nav } />);
    }

    return (
      <Header className = "haccoon-header">
        <div className = "gradient-top" />

        <Row className = "header-content" style = {{ padding: '0 25px' }}>
          <Col sm = {{ span: 10, offset: 7 }} xs = {{ span: 12, offset: 0 }}>
            <div className = "text-center">
              <Link to = "/home">
                <img style = {{ height: 35 }} src = {logo} alt = "Logo HACCOON" />
              </Link>
            </div>
          </Col>

          { this.state.status ? (
            <Col sm = {{ span: 7 }} xs = {{ span: 12 }} className = "menu-user">
              <Dropdown
                overlay = {(
                  <Menu>
                    <Menu.Item key = "perfil">
                      <Link to = { this.state.status !== '-1' ? `/profile/${this.state.user.username}` : '/edit/profile' } replace>
                        <Icon type = "idcard" /> Perfil
                      </Link>
                    </Menu.Item>

                    <Menu.Item key = "logout" onClick = { this.signout }>
                      <Icon type = "frown" /> Logout
                    </Menu.Item>
                  </Menu>
                )}
              >
                <span style = {{ marginLeft: 10, fontSize: 13 }}>
                  <Avatar style = {{ height: 20, width: 20, marginTop: -6, marginRight: 4 }} src = { this.state.user.image } />
                  <span className = "username"> { this.state.name } </span> <Icon style = {{ fontSize: 9 }} type = "down" />
                </span>
              </Dropdown>
            </Col>
          ) : null }
        </Row>
      </Header>
    );
  }
}
