/* eslint-disable no-unused-vars */
// insideProject.js

import React, { Component } from 'react';
import { Row, Col, Spin, Typography, Divider, Avatar, Button, Icon, Tag, Slider, List, Card, Skeleton, Popover, Input, Modal, notification, message } from 'antd';
import { Redirect, Link } from 'react-router-dom';

import axios from 'axios';
import moment from 'moment';

import MainLayout from '../../components/layout/layout';
import { getStatus, getID } from '../../services/auth';

const { Meta } = Card;
const { Title, Text, Paragraph } = Typography;
const { TextArea } = Input;

const projectStatus = [
  { color: '#3867D6', icon: 'deployment-unit', title: 'Planejamento', label: 'Começando o sofrimento denovo.' },
  { color: '#FA8231', icon: 'rocket', title: 'Inicializando', label: 'Fechando contrato e preparando as almas de membros para o abate.' },
  { color: '#45AAF2', icon: 'coffee', title: 'Em Andamento', label: 'Abastecendo os membros com café para manter o progresso.' },
  { color: '#778CA3', icon: 'frown', title: 'Cancelado', label: 'Cliente deu pra trás.' },
  { color: '#EB3B5A', icon: 'stop', title: 'Congelado', label: 'Sei lá, deu uma parada por agora.' },
  { color: '#20BF6B', icon: 'like', title: 'Acabou', label: 'Acaboooouuuu!!' }
];

const projectLabels = [
  { color: '#F7B731', title: 'Consultoria', icon: 'book' },
  { color: '#FED330', title: 'Site', icon: 'desktop' },
  { color: '#26DE81', title: 'Aplicativo', icon: 'mobile' },
  { color: '#0FB9B1', title: 'Sistema Web', icon: 'ie' },
  { color: '#2BCBBA', title: 'Sistema Interno', icon: 'home' }
];

export default class InsideProject extends Component {
  constructor(props) {
    super(props);

    this.state = {
      visibleModalRate: true,
      rateServices: 5,
      rateGrade: 5,
      rateHelp: 5,
      textPositivePoints: '',
      textGetBetter: '',
      project: '',
      nav: '',
      name: '',
      link: '',
      newMessage: '',
      members: [],
      customers: [],
      loadingPage: true,
      loadingProjectCard: true,
      loadingMembersCard: true,
      visiblePopoverNewLink: false,
      buttonLoadingNewLink: false,
      buttonLoadingModalRate: false
    };
  }

  componentDidMount() {
    this.getProjectData(this.props.match.params.id);
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    this.getProjectData(nextProps.match.params.id);
  }

  getProjectData = (id) => {
    this.setState({
      visibleModalRate: true,
      rateServices: 5,
      rateGrade: 5,
      rateHelp: 5,
      textPositivePoints: '',
      textGetBetter: '',
      project: '',
      nav: '',
      name: '',
      link: '',
      newMessage: '',
      members: [],
      customers: [],
      loadingPage: true,
      loadingProjectCard: true,
      loadingMembersCard: true,
      visiblePopoverNewLink: false,
      buttonLoadingNewLink: false,
      buttonLoadingModalRate: false
    });

    axios.get('/api/members').then(resMembers => {
      const members = resMembers.data;

      axios.get('/api/customers').then(resCustomers => {
        const customers = resCustomers.data;

        axios.get('/api/projects').then(resProjects => {
          const projects = resProjects.data;
          const project = projects[projects.map(e => e._id).indexOf(id)];

          if (!project) this.setState({ nav: '/notFound' });
          else if (getStatus() === '-1' && project.customer[0] !== getID()) this.setState({ nav: '/notFound' });
          else {
            project.customer = customers[customers.map(e => e._id).indexOf(project.customer[0])];
            project.leader = members[members.map(e => e._id).indexOf(project.leader[0])];
            project.chatReverse = project.chat;
            project.chatReverse.reverse();

            const membersProject = [];
            if (project.members[0]) {
              project.members[0].forEach(item => {
                const member = members[members.map(e => e._id).indexOf(item)];
                if (member) membersProject.push(member);
              });
            }
            project.members = membersProject;

            const embeddedProjects = [];
            if (project.embedded[0]) {
              project.embedded[0].forEach(item => {
                const embedded = projects[projects.map(e => e._id).indexOf(item)];
                if (embedded) embeddedProjects.push(embedded);
              });
            }
            project.embedded = embeddedProjects;

            this.setState({ project, members, customers, loadingPage: false, loadingProjectCard: false, loadingMembersCard: false });
          }
        }).catch(() => {
          // console.log(errProjects);
        });
      }).catch(() => {
        // console.log(errCustomers);
      });
    }).catch(() => {
      // console.log(errMembers);
    });
  }

  handleSubmitNewLink = () => {
    this.setState({ buttonLoadingNewLink: true });

    const { name, link, project } = this.state;
    const { links, _id } = project;

    if (name !== '' && link !== '') {
      links.push({ name, link });

      axios.put('/api/projects', { id: _id, links }).then(() => {
        notification.success({
          message: 'Tudo Certo!',
          description: 'Link Adicionado!'
        });

        this.setState({ visiblePopoverNewLink: false, buttonLoadingNewLink: false, name: '', link: '' });
      });
    } else {
      this.setState({ buttonLoadingNewLink: false });
      message.error(name === '' ? 'Por favor, insira um nome!' : 'Por favor, insira um link!');
    }
  }

  confirmDeleteLink = (link) => {
    const _this = this;

    Modal.confirm({
      title: 'Deseja realmente apagar este link?',
      content: 'Esta ação é permanente, ainda não implmentamos lixeira (e nem vamos)',
      okType: 'danger',
      onOk() {
        _this.handleSubmitDeleteLink(link);
      },
      onCancel() {}
    });
  }

  handleSubmitDeleteLink = (link) => {
    const { project } = this.state;
    const links = project.links.filter(r => r._id !== link._id);

    axios.put('/api/projects', { id: project._id, links }).then(() => {
      notification.success({
        message: 'Tudo Certo!',
        description: 'Link Deletado!'
      });

      this.componentDidMount();
    });
  }

  sendMessage = (value) => {
    const newChat = {};

    newChat.message = value;
    newChat.date = moment();
    if (getStatus() === '-1') newChat.customerID = [getID()];
    else newChat.memberID = [getID()];

    const { project } = this.state;
    const { chat, _id } = project;
    let { chatReverse } = project;

    if (value.trim() !== '') {
      chat.push(newChat);
      chatReverse = chat.reverse();

      axios.put('/api/projects', { id: _id, chat }).then(() => {
        this.setState({ newMessage: '' });
      });
    } else {
      message.error('Por favor, insira uma mensagem!');
    }
  }

  rateProject = () => {
    if (this.state.textPositivePoints && this.state.textGetBetter) {
      this.setState({ buttonLoadingModalRate: true });
      const { rateServices, rateGrade, rateHelp, textPositivePoints, textGetBetter } = this.state;

      axios.put('/api/projects', { id: this.state.project._id, grade: { rateServices, rateGrade, rateHelp, textPositivePoints, textGetBetter } }).then(() => {
        notification.success({
          message: 'Deu certo!',
          description: 'Avaliação Enviada! Obrigado pelo feedback.'
        });

        this.setState({ visibleModalRate: false, buttonLoadingModalRate: false });
      }).catch(() => {
        // console.log(error);
        this.setState({ buttonLoadingModalRate: false });

        notification.error({
          message: 'Ops!',
          description: 'Houve um problema aqui: Não conseguimos enviar dados no Servidor.'
        });
      });
    } else {
      if (!this.state.textPositivePoints) message.error('Digite nossos pontos positivos, eles dados são importantes para melhorar nosso serviço!');
      if (!this.state.textGetBetter) message.error('Digite o que podemos melhorar, eles dados são importantes para melhorar nosso serviço!');
    }
  }

  render() {
    if (this.state.nav) return (<Redirect to = { this.state.nav } />);

    return (
      <MainLayout page = "projects">
        <Spin spinning = { this.state.loadingPage }>
          <Row style = {{ marginBottom: 60 }}>
            <Col span = {14} offset = {2}>
              <Row gutter = {16}>
                <Col span = {8}>
                  <Card bodyStyle = {{ padding: 16 }} bordered = {false} style = {{ textAlign: 'center' }} loading = { this.state.loadingProjectCard }>
                    <img alt = "Imagem do Projeto" style = {{ width: '100%', maxHeight: 200, marginBottom: 20, objectFit: 'cover' }} src = { this.state.project.image } />

                    { this.state.project ? this.state.project.labels.map(label => (
                      <Popover key = { projectLabels[label].title } placement = "top" content = { projectLabels[label].title }>
                        <Tag color = { projectLabels[label].color } style = {{ marginBottom: 3, marginRight: 5, fontWeight: 600 }}> <Icon type = { projectLabels[label].icon } /> </Tag>
                      </Popover>
                    )) : null }
                  </Card>

                  <Card
                    title = { <Title level = {4} style = {{ fontWeight: 500, marginBottom: 0, marginTop: 0 }}> Projetos Relacionados: </Title> }
                    loading = { this.state.loadingProjectCard }
                    size = "small"
                    bordered
                    hoverable
                  >
                    <List
                      size = "small"
                      dataSource = { this.state.project.embedded && getStatus() !== '-1' ? this.state.project.embedded.filter(e => e[0] !== null) : this.state.project.embedded ? this.state.project.embedded.filter(e => e[0] !== null && e[0] === getID()) : [] }
                      locale = {{ emptyText: 'Não há projetos relacionados 😛' }}
                      renderItem = { embedded => (
                        <Row style = {{ paddingLeft: 9 }} onClick = { () => this.setState({ nav: `/insideProject/${embedded._id}` }) }>
                          <Avatar size = {30} shape = "square" style = {{ marginBottom: 5 }} src = { embedded.image } />
                          <Text style = {{ marginLeft: 9, fontSize: 16 }}> { embedded.name } </Text>
                        </Row>
                      )}
                    />
                  </Card>

                  { getStatus() !== '-1' ? (
                    <Card
                      style = {{ marginTop: 15 }}
                      title = { <Title level = {4} style = {{ fontWeight: 500, marginBottom: 0 }}> Links Relacionados: </Title> }
                      loading = { this.state.loadingProjectCard }
                      size = "small"
                      bordered
                      hoverable
                      extra = {(
                        <Popover content = "Adicionar Link">
                          <Popover
                            title = "Novo Link"
                            trigger = "click"
                            visible = { this.state.visiblePopoverNewLink }
                            onVisibleChange = { visiblePopoverNewLink => this.setState({ visiblePopoverNewLink }) }
                            content = {(
                              <Row style = {{ display: 'grid' }}>
                                <Input style = {{ width: 300, marginBottom: 5 }} size = "small" placeholder = "Título" value = { this.state.name } onChange = { e => this.setState({ name: e.target.value }) } />
                                <Input addonBefore = "www." style = {{ width: 300 }} size = "small" placeholder = "Link" value = { this.state.link } onChange = { e => this.setState({ link: e.target.value }) } />

                                <Button loading = { this.state.buttonLoadingNewLink } size = "small" type = "primary" style = {{ marginTop: 10, width: '100%' }} onClick = { () => this.handleSubmitNewLink() }> Inserir </Button>
                              </Row>
                            )}
                          >
                            <Button size = "small" type = "primary" icon = "plus" style = {{ height: 25 }} />
                          </Popover>
                        </Popover>
                      )}
                    >
                      <List
                        size = "small"
                        dataSource = { this.state.project.links }
                        locale = {{ emptyText: 'Não há links 😛' }}
                        renderItem = { link => (
                          <Paragraph style = {{ marginLeft: 10, marginRight: 18, marginTop: 5, marginBottom: 0 }}>
                            <Text strong onClick = { () => window.open(`https://www.${link.link}`, '_blank') }> { link.name } </Text>
                            <Text type = "secondary" onClick = { () => window.open(`https://www.${link.link}`, '_blank') }> ({ link.link }) </Text>
                            <Text style = {{ position: 'absolute', right: 4 }}>
                              <Popover content = "Deletar Link"> <Icon onClick = { () => this.confirmDeleteLink(link) } style = {{ color: '#EB3B5A' }} type = "delete" /> </Popover>
                            </Text>
                          </Paragraph>
                        )}
                      />
                    </Card>
                  ) : null }

                  { getStatus() < 5 && getStatus() !== '-1' ? (
                    <Button style = {{ marginTop: 20, width: '100%' }} onClick = { () => this.setState({ nav: `/files/${this.state.project.files[0] || ''}` }) }>
                      <Icon type = "folder" /> Acessar Arquivos
                    </Button>
                  ) : null }
                </Col>

                <Col span = {15} offset = {1}>
                  <Row>
                    <Col span = {16}>
                      <Skeleton loading = { this.state.loadingProjectCard } active avatar>
                        <Title level = {2} style = {{ fontWeight: 500, marginBottom: 4 }}>
                          { this.state.project.name }

                          { getStatus() !== '-1' && this.state.project ? (
                            <Popover
                              placement = "bottomRight"
                              overlayStyle = {{ width: 300 }}
                              title = { <Text> <Icon type = { projectStatus[this.state.project.status].icon } /> &nbsp; { projectStatus[this.state.project.status].title } </Text> }
                              content = { <Paragraph style = {{ marginBottom: 0 }}> { projectStatus[this.state.project.status].label } </Paragraph> }
                            >
                              <Tag style = {{ verticalAlign: 3, marginLeft: 10, fontWeight: 600, cursor: 'pointer' }} color = { projectStatus[this.state.project.status].color }> { projectStatus[this.state.project.status].title } </Tag>
                            </Popover>
                          ) : null }
                        </Title>
                      </Skeleton>
                    </Col>

                    <Col style = {{ textAlign: 'right' }} span = {8}>
                      { getStatus() < 5 && getStatus() !== '-1' ? (
                        <Button onClick = { () => this.setState({ nav: `/editProject/${this.state.project._id}` }) }>
                          <Icon type = "edit" /> Editar Projeto
                        </Button>
                      ) : null }
                    </Col>
                  </Row>

                  <Divider style = {{ marginTop: 0 }} />

                  <Skeleton loading = { this.state.loadingProjectCard } active avatar>
                    <Paragraph type = "secondary" style = {{ textAlign: 'center' }}> { this.state.project.description } </Paragraph>

                    { this.state.project.customer ? (
                      <Paragraph style = {{ marginLeft: 20, marginBottom: 3 }}>
                        <Text strong> Cliente: </Text>
                        <Text> { this.state.project.customer.name } </Text>
                      </Paragraph>
                    ) : null }

                    <Paragraph style = {{ marginLeft: 20, marginBottom: 3 }}>
                      <Text strong> Preço: </Text>
                      <Text> R$ {this.state.project.cost ? this.state.project.cost.toString().replace(/[^\d]/g, '').replace(/\B(?=(\d{3})+(?!\d))/g, '.') : this.state.project.cost},00 </Text>
                    </Paragraph>
                  </Skeleton>

                  <Card title = { <Title id = "info" level = {4} style = {{ fontWeight: 500, marginBottom: 0, marginTop: 0 }}> Membros: </Title> } loading = { this.state.loadingMembersCard } bordered = {false} style = {{ marginTop: 50 }}>
                    <List
                      size = "small"
                      locale = {{ emptyText: 'Não há membros ainda 😛' }}
                      dataSource = { this.state.project.members && this.state.project.leader ? [...new Set([this.state.project.leader].concat(this.state.project.members))] : [] }
                      renderItem = { member => (
                        getStatus() !== '-1' ? (
                          <Col key = { member._id } span = {12} style = {{ paddingLeft: 20 }}>
                            <Link to = {`/profile/${member.username}`}>
                              <Avatar size = {30} style = {{ marginBottom: 5 }} src = { member.image } />
                              { member === this.state.project.leader ? (
                                <Popover placement = "top" content = "Lider do Projeto">
                                  <Avatar icon = "crown" style = {{ cursor: 'pointer', position: 'absolute', bottom: 0, left: 40, backgroundColor: '#F7B731' }} size = {20} />
                                </Popover>
                              ) : null }

                              <Text style = {{ marginLeft: 11 }} ellipsis>{member.username}</Text>
                            </Link>
                          </Col>
                        ) : (
                          <Col key = { member._id } span = {12} style = {{ paddingLeft: 20 }}>
                            <Avatar size = {30} style = {{ marginBottom: 5 }} src = { member.image } />
                            { member === this.state.project.leader ? (
                              <Popover placement = "top" content = "Lider do Projeto">
                                <Avatar icon = "crown" style = {{ cursor: 'pointer', position: 'absolute', bottom: 0, left: 40, backgroundColor: '#F7B731' }} size = {20} />
                              </Popover>
                            ) : null }

                            <Text style = {{ marginLeft: 11 }} ellipsis>{member.username}</Text>
                          </Col>
                        )
                      )}
                    />
                  </Card>
                </Col>
              </Row>
            </Col>

            <Col span = {5} offset = {1}>
              <Title level = {4} style = {{ fontWeight: 500, marginBottom: 0, marginTop: 0 }}> Mural: </Title>
              <Input.Search style = {{ marginTop: 10, marginBottom: 10 }} value = { this.state.newMessage } onChange = { e => this.setState({ newMessage: e.target.value }) } placeholder = "Envie uma mensagem" enterButton = {<Icon type = "mail" />} onSearch = { msg => this.sendMessage(msg) } />

              <List
                style = {{ maxHeight: 700, overflowY: 'scroll' }}
                size = "small"
                dataSource = { this.state.project.chatReverse }
                locale = {{ emptyText: 'Não há mensagens no chat 😛' }}
                renderItem = { chat => (
                  <Row className = "chat-message-col" type = "flex" justify = "space-around" align = "middle">
                    <Card bordered = {false} hoverable size = "small" style = {{ width: 'calc(100% - 7px)', borderColor: 'rgba(0, 0, 0, 0.09)', boxShadow: '0 2px 8px rgba(0, 0, 0, 0.09)' }}>
                      <Meta
                        avatar = {(
                          <Col>
                            { chat.memberID && chat.memberID.length > 0 ? (
                              <Avatar size = {30} style = {{ marginBottom: 5 }} src = { this.state.members[this.state.members.map(e => e._id).indexOf(chat.memberID[0])].image } />
                            ) : (
                              <Avatar size = {30} style = {{ marginBottom: 5 }} src = { this.state.customers[this.state.customers.map(e => e._id).indexOf(chat.customerID[0])].image } />
                            )}
                          </Col>
                        )}
                        title = {(
                          chat.memberID && chat.memberID.length > 0 ? (
                            <Text style = {{ fontSize: 16 }}>{this.state.members[this.state.members.map(e => e._id).indexOf(chat.memberID[0])].name}</Text>
                          ) : (
                            <Text style = {{ fontSize: 16 }}>{this.state.customers[this.state.customers.map(e => e._id).indexOf(chat.customerID[0])].name}</Text>
                          )
                        )}
                        description = { <Row style = {{ marginTop: -12 }}> <Text style = {{ fontSize: 10 }}>{moment(chat.date).fromNow()}</Text> </Row> }
                      />
                      <Col style = {{ marginTop: 15 }}>
                        <Row type = "flex" justify = "start">
                          <Paragraph ellipsis = {{ rows: 2 }} style = {{ fontSize: 12, marginBottom: 0 }}>{chat.message}</Paragraph>
                        </Row>
                      </Col>
                    </Card>
                  </Row>
                )}
              />
            </Col>
          </Row>
        </Spin>

        { this.state.project && !this.state.project.grade && this.state.project.status === 5 && getStatus() === '-1' && this.state.project.customer && this.state.project.customer._id === getID() ? (
          <Modal okButtonProps = {{ loading: this.state.buttonLoadingModalRate }} visible = { this.state.visibleModalRate } closable = {false} cancelText = "Voltar" onCancel = { () => this.setState({ nav: '/projects' }) } okText = "Avaliar" onOk = { this.rateProject }>
            <Paragraph style = {{ fontSize: 30, textAlign: 'center', marginBottom: 5 }}> Avalie o Projeto </Paragraph>

            <Divider style = {{ minWidth: '60%', width: '60%', marginTop: 0, marginLeft: 'auto', marginRight: 'auto' }}>
              <Icon type = "like" />
            </Divider>

            <Row style = {{ padding: 20, paddingBottom: 0 }} gutter = {16}>
              <Paragraph>
                <Text> O quanto você indica nossos serviços? </Text>
                <Slider marks = {{ 0: <Icon style = {{ color: this.state.rateServices <= 5 ? '#383A3F' : '#778CA3' }} type = "frown-o" />, 10: <Icon style = {{ color: this.state.rateServices > 5 ? '#383A3F' : '#778CA3' }} type = "smile-o" /> }} value = { this.state.rateServices } onChange = { (rateServices) => this.setState({ rateServices }) } max = {10} min = {0} />
              </Paragraph>

              <Paragraph style = {{ marginTop: 20 }}>
                <Text> Qual nota você dá aos nossos serviços? </Text>
                <Slider marks = {{ 0: <Icon style = {{ color: this.state.rateGrade <= 5 ? '#383A3F' : '#778CA3' }} type = "frown-o" />, 10: <Icon style = {{ color: this.state.rateGrade > 5 ? '#383A3F' : '#778CA3' }} type = "smile-o" /> }} value = { this.state.rateGrade } onChange = { (rateGrade) => this.setState({ rateGrade }) } max = {10} min = {0} />
              </Paragraph>

              <Paragraph style = {{ marginTop: 20 }}>
                <Text> O quanto nosso serviço ajudou você com o seu problema? </Text>
                <Slider marks = {{ 0: <Icon style = {{ color: this.state.rateHelp <= 5 ? '#383A3F' : '#778CA3' }} type = "frown-o" />, 10: <Icon style = {{ color: this.state.rateHelp > 5 ? '#383A3F' : '#778CA3' }} type = "smile-o" /> }} value = { this.state.rateHelp } onChange = { (rateHelp) => this.setState({ rateHelp }) } max = {10} min = {0} />
              </Paragraph>

              <Paragraph style = {{ marginTop: 20 }}>
                <Text> O que você gostaria de destacar como pontos positivos? </Text>
                <TextArea rows = {4} value = { this.state.textPositivePoints } onChange = { (e) => this.setState({ textPositivePoints: e.target.value }) } />
              </Paragraph>

              <Paragraph style = {{ marginTop: 20 }}>
                <Text> Em quais pontos você julga que precisamos melhorar? </Text>
                <TextArea rows = {4} value = { this.state.textGetBetter } onChange = { (e) => this.setState({ textGetBetter: e.target.value }) } />
              </Paragraph>
            </Row>
          </Modal>
        ) : null }
      </MainLayout>
    );
  }
}
