// githubConnection.js

import React, { Component } from 'react';
import { notification } from 'antd';
import { Redirect } from 'react-router-dom';

import axios from 'axios';

import { getStatus, getID } from '../../services/auth';

const REDIRECT_URI = 'http://localhost:3000/githubconnection';

export default class Home extends Component {
  constructor(props) {
    super(props);

    this.state = {
      status: getStatus(),
      nav: ''
    };
  }

  componentDidMount() {
    const code = new URL(window.location.href).searchParams.get('code');

    if (this.state.status !== '-1' && code) {
      axios.get(`/api/members/authenticate/${code}`).then(res => {
        const data = new URL(`${REDIRECT_URI}?${res.data}`).searchParams.get('access_token');

        if (data) {
          axios.put('/api/members', { id: getID(), githubCode: data }).then(() => {
            notification.success({
              message: 'Conexão com Github Funfou!!',
              description: 'AEE! HACCOON está de portas abertas.'
            });
            this.setState({ nav: '/home' });
          });
        } else this.setState({ nav: '/home' });
      });
    } else if (this.state.status !== '-1') {
      this.setState({ nav: '/home' });
    }
  }

  render() {
    if (this.state.nav) return (<Redirect to = { this.state.nav } />);
    if (['-1'].includes(getStatus())) return (<Redirect to = "/notAuthorizate" />);

    return (
      <span />
    );
  }
}
