// genericErrorPage.js

import React, { Component } from 'react';
import { Layout, Row, Col, Icon, Typography, BackTop } from 'antd';

import NavBar from '../../components/navbar/navbar';

const raccoon = require('../../images/raccoon.png');

const { Title } = Typography;
const { Content } = Layout;

const errorsSettings = [
  { error: 404, title: 'Página não encontrada', label: 'Ops! Parece que esta página foi roubada, recarregue a página ou verifique se está buscando a página certa.' },
  { error: 500, title: 'Página não autorizada', label: 'Ops! Parece que esta página foi roubada e você não têm autorização para encontrá-la, recarregue a página ou torça para que alguém te de uma autorização.' }
];

export default class GenericErrorPage extends Component {
  constructor() {
    super();

    this.state = {
      error: ''
    };
  }

  componentDidMount() {
    this.setState({ error: errorsSettings[errorsSettings.map(e => e.error).indexOf(this.props.error)] });
  }

  render() {
    return (
      <Layout style = {{ height: '100vh', overflow: 'hidden', width: '100%' }}>
        <BackTop />
        <NavBar />

        <Content style = {{ padding: 24, paddingTop: 50, background: '#FFF', minHeight: 280 }}>
          <Row>
            <Col span = {13}>
              <Title style = {{ color: '#EB3B5A', fontSize: 208, fontWeight: 800, textAlign: 'center', marginBottom: 0 }}> { this.state.error.error } </Title>
              <Title style = {{ textAlign: 'center', fontWeight: 800, marginBottom: 0, marginTop: -25 }}> { this.state.error.title } </Title>
              <Title level = {4} style = {{ textAlign: 'center', fontWeight: 'lighter', maxWidth: 560, marginLeft: 'auto', marginRight: 'auto' }}> { this.state.error.label } </Title>

              <Title onClick = { () => window.history.back() } level = {4} style = {{ fontWeight: 800, cursor: 'pointer', marginTop: 100, maxWidth: 400, marginLeft: 'auto', marginRight: 'auto' }}> <Icon style = {{ color: '#3867D6' }} type = "caret-left" /> Voltar à última localização </Title>
            </Col>

            <Col span = {11}>
              <img style = {{ height: '100vh', marginTop: 20 }} src = {raccoon} alt = "HACCOON's Raccoon" />
            </Col>
          </Row>
        </Content>
      </Layout>
    );
  }
}
